import { FC, Fragment, useContext } from 'react'
import { CardContent, CardWrapper } from '@primer/components/Card'
import { getChildItems, PageBuilder2Context, Widget } from 'components/PageBuilder2/utils'
import { CardWidget } from '.'
import CardHeader from '@primer/components/CardHeader'
import DropTarget from 'components/PageBuilder2/components/DropTarget'
import { BuildComp } from '../index'
import { DividerType } from '../types'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'

const Accepts: string[] = [DividerType]

interface BuildProps {
	id: string
}

const Build: FC<BuildProps> = ({ id }) => {
	const { items, removeItem, updateItem } = useContext(PageBuilder2Context)
	let item = items.find((x) => x.id === id) as CardWidget
	let childItems: Widget[] = getChildItems(items, item.id)

	const [isHovered, bindHoverable] = useHoverable()

	return !item ? null : (
		<CardWrapper
			onClick={(e) => {
				e.stopPropagation()
				if (item) openEdit({ item, removeItem, updateItem })
			}}
			{...bindHoverable}
			className={clsx('cursor-pointer transition-all duration-100', isHovered && 'ring-4 ring-primary-500')}
		>
			{item.data.title && <CardHeader title={item.data.title} />}
			<CardContent>
				{childItems.map((x, i) => (
					<Fragment key={x.id}>
						<DropTarget parentId={item.id} index={i} isFirst={i === 0} accept={Accepts} />

						<BuildComp id={x.id} />
					</Fragment>
				))}

				<DropTarget
					parentId={item.id}
					index={childItems.length}
					isFirst={childItems.length === 0}
					isLast
					accept={Accepts}
				/>
			</CardContent>
		</CardWrapper>
	)
}

export default Build
