import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { EntityResource, entityResource } from '.'

export enum PAGE_TYPE {
	None = 1,
	Entity = 2,
	Unit = 3,
	Relation = 4,
}

export const pageTypeOptions: { value: PAGE_TYPE; label: string }[] = [
	{ value: PAGE_TYPE.None, label: 'None' },
	{ value: PAGE_TYPE.Unit, label: 'Unit' },
	{ value: PAGE_TYPE.Entity, label: 'Entity' },
	{ value: PAGE_TYPE.Relation, label: 'Relation' },
]

export interface PageModel extends Model {
	entityId?: FK
	relationId?: FK
	parentId?: FK
	key: string
	slug: string
	name: string
	type: PAGE_TYPE
	title: string
	structure: string
	path: string
}

export interface PageResource extends PageModel {
	readonly data: PageModel | null
	readonly entity: EntityResource | null
}

export const pageResource = (id: Identifier<PageModel>): PageResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.pages.find(id) : id

	if (!data) return null

	return {
		...data,
		data,
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
	}
}
