import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { FieldType } from '../types'
import openEdit from './Edit'
import Build from './Build'
import View from './View'

export interface FieldWidget extends Widget {
	type: typeof FieldType
	data: {
		fieldId: string
		label?: string
		customFormat?: string
	}
}

export const isFieldWidget = (x: any): x is FieldWidget => x.type === FieldType

export const FieldTool: Tool<FieldWidget> = {
	name: 'Field',
	type: FieldType,
	description: 'Display a value',
	create: () => ({
		id: uuid(),
		type: FieldType,
		data: {
			fieldId: '',
			label: '',
			customFormat: '',
		},
	}),
}

export const FieldBuild = Build
export const FieldView = View
export const openEditField = openEdit
