import { ReactNode } from 'react'
import { useDrag } from 'react-dnd'
import clsx, { ClassValue } from 'clsx'

interface DraggerProps<T extends {}> {
	item: T
	type: string
	className?: ClassValue | ((x: { isDragging: boolean }) => ClassValue)
	children?: ReactNode | ((x: { isDragging: boolean }) => ReactNode)
}

function Dragger<T>({ item, type, className, children }: DraggerProps<T>) {
	const [{ isDragging }, dragRef] = useDrag({
		type,
		item,
		collect: (monitor) => {
			return {
				isDragging: !!monitor.isDragging(),
			}
		},
	})

	return (
		<div ref={dragRef} className={typeof className === 'function' ? className({ isDragging }) : clsx(className)}>
			{children}
		</div>
	)
}

export default Dragger
