import { useContext } from 'react'
import { PageBuilder2Context, PageRenderer2Context } from '../utils'
import Dragger from 'components/Builder/Dragger'

import { CardType, DividerType } from './types'

import { CardTool, CardBuild, CardView, openEditCard } from './Card'
import { DividerTool, DividerBuild, DividerView, openEditDivider } from './Divider'

export const pbtools = {
	[CardType]: CardTool,
	[DividerType]: DividerTool,
}

export const formtools = {
	[CardType]: CardTool,
	[DividerType]: DividerTool,
}

export const builds = {
	[CardType]: CardBuild,
	[DividerType]: DividerBuild,
}

export const views = {
	[CardType]: CardView,
	[DividerType]: DividerView,
}

export const editors = {
	[CardType]: openEditCard,
	[DividerType]: openEditDivider,
}

export const BuildComp = ({ id }: { id: string }) => {
	const { items } = useContext(PageBuilder2Context)
	let item = items.find((x) => x.id === id)

	if (!item) return null

	const Comp = builds[item.type]

	return (
		<Dragger type={item.type} item={item}>
			<Comp id={id} />
		</Dragger>
	)
}

export const ViewComp = ({ id }: { id: string }) => {
	const { items } = useContext(PageRenderer2Context)
	let item = items.find((x) => x.id === id)

	if (!item) return null

	const Comp = views[item.type]

	return <Comp id={id} />
}
