import { FC, ReactNode } from 'react'
import { useField, useFormikContext } from 'formik'
import clsx, { ClassValue } from 'clsx'

interface FormCheckboxProps {
	name: string
	label?: ReactNode
	description?: ReactNode
	defaultValue?: any
	disabled?: boolean
	inputClassName?: ClassValue
	className?: ClassValue
	color?: string
	autoFocus?: boolean
}

const FormCheckbox: FC<FormCheckboxProps> = ({
	name,
	label,
	description,
	disabled,
	color = 'bg-primary-500 dark:bg-primary-500',
	className,
	autoFocus,
}) => {
	// helpers: {setValue: ƒ, setTouched: ƒ, setError: ƒ}
	const [field] = useField({
		name,
		type: 'checkbox',
	})

	const { value } = field

	const formik = useFormikContext()

	return (
		<div className={clsx(className)}>
			<label className="text-sm font-medium leading-5 text-gray-700 dark:text-gray-200 inline-flex items-center cursor-pointer">
				<Toggle checked={value} color={color} />
				{(label || description) && (
					<div className="ml-3">
						{label && <div>{label}</div>}
						{description && <div className="font-normal text-gray-500">{description}</div>}
					</div>
				)}
				<input
					{...field}
					type="checkbox"
					disabled={disabled || formik.isSubmitting}
					className="hidden"
					autoFocus={autoFocus}
				/>
			</label>
		</div>
	)
}

interface ToggleProps {
	checked: boolean
	color: string
}

const Toggle: FC<ToggleProps> = ({ checked, color }) => {
	return (
		<span
			role="checkbox"
			tabIndex={0}
			aria-checked="false"
			className={clsx(
				'relative inline-block flex-shrink-0 h-6 w-11 border-2 border-transparent rounded-full cursor-pointer transition-colors ease-in-out duration-200 focus:outline-none focus:shadow-outline',
				{
					[color]: checked,
					'bg-gray-200 dark:bg-gray-800': !checked,
				}
			)}
		>
			<span
				aria-hidden="true"
				className={clsx(
					'translate-x-0 inline-block h-5 w-5 rounded-full shadow transform transition ease-in-out duration-200',
					{
						'translate-x-5 bg-white dark:bg-white': checked,
						'translate-x-0 bg-white dark:bg-gray-400': !checked,
					}
				)}
			></span>
		</span>
	)
}

export default FormCheckbox
