import { FC, ReactNode } from 'react'
import clsx, { ClassValue } from 'clsx'
import { ChevronDownIcon, CheckIcon, ChevronLeftIcon, LogoutIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import useDarkMode from '@primer/hooks/useDarkMode'
// import { useLocation } from '@reach/router'

export const TopbarIntents = {
	light: {
		background: 'bg-white dark:bg-gray-700 border-b border-gray-300 dark:border-gray-600 -mt-px',
		text: 'text-gray-800 dark:text-white',
	},
	dark: {
		background: 'bg-gray-800 dark:bg-gray-900',
		text: 'text-gray-200 antialiased',
	},
}

export type TopbarIntentType = keyof typeof TopbarIntents

interface TopbarProps {
	intent?: TopbarIntentType
	className?: ClassValue
	children?: ReactNode
}

const Topbar: FC<TopbarProps> = ({ intent = 'light', children, className }) => {
	const profile = {}

	const logout = async () => {
		// store.getState().logout()
	}

	const [darkMode, setDarkMode] = useDarkMode()

	return (
		<div className={clsx(TopbarIntents[intent].background)}>
			<div
				className={clsx(
					'max-w-7xl mx-auto h-16 px-4 sm:px-6 lg:px-8 flex items-center justify-between',
					className
				)}
			>
				<div>{children}</div>
				{profile && (
					<div className="flex items-center space-x-2">
						<Actions
							actions={[
								{
									title: (
										<div className="flex items-center space-x-4">
											<div className={clsx('text-sm font-medium', TopbarIntents[intent].text)}>
												Your Name
											</div>
										</div>
									),
									icon: (
										<ChevronDownIcon
											className={clsx('w-5 h-5 opacity-75', TopbarIntents[intent].text)}
										/>
									),
									iconSide: 'right',
									intent: 'invisible',
									placement: 'bottom-end',
									size: 'xs',
									actions: [
										[
											{
												title: 'Colour Scheme',
												icon: <ChevronLeftIcon className="w-4 h-4" />,
												iconSide: 'left',
												placement: 'left-start',
												hoverable: true,
												actions: [
													{
														title: 'Dark Mode',
														icon: (
															<CheckIcon
																className={clsx(
																	'w-4 h-4',
																	darkMode === true ? 'opacity-100' : 'opacity-0'
																)}
															/>
														),
														iconSide: 'left',
														onClick: () => setDarkMode(true),
													},
													{
														title: 'Light Mode',
														icon: (
															<CheckIcon
																className={clsx(
																	'w-4 h-4',
																	darkMode === false ? 'opacity-100' : 'opacity-0'
																)}
															/>
														),
														iconSide: 'left',
														onClick: () => setDarkMode(false),
													},
													{
														title: 'System Preference',
														icon: (
															<CheckIcon
																className={clsx(
																	'w-4 h-4',
																	darkMode === null ? 'opacity-100' : 'opacity-0'
																)}
															/>
														),
														iconSide: 'left',
														onClick: () => setDarkMode(null),
													},
												],
											},
										],

										[
											{
												title: 'Logout',
												icon: <LogoutIcon className="w-4 h-4" />,
												iconSide: 'left',
												onClick: logout,
											},
										],
									],
								},
							]}
						/>
					</div>
				)}
			</div>
		</div>
	)
}

export default Topbar
