import { FC, useState } from 'react'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import FormSelect from '@primer/components/Form/FormSelect'
import { RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import db from 'db'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Stack from '@primer/components/Stack'
import FormDate from '@primer/components/Form/FormDate'
import FormToggle from '@primer/components/Form/FormToggle'
import { entityResource } from 'db/types'

interface FormSchema {
	fields: {
		[x: string]: {
			id?: string
			value: string
		}
	}
}

const UnitForm: FC<RouteComponentProps<{ entityId: string; mode: 'create' | 'edit'; id: string }>> = ({
	entityId = '',
	mode,
	id = '',
}) => {
	const entity = entityResource(entityId)
	const fields = entity?.fields

	const [initialValues] = useState<FormSchema>({
		fields:
			fields?.reduce((list, field) => {
				let value = field.value(id)
				let key = field?.key || ''
				let defaultValue = field?.defaultValue
				let type = field?.type
				let valueData = value

				if (valueData) {
					return {
						...list,
						[key]: {
							id: valueData?.id,
							value: valueData?.value || '',
						},
					}
				} else {
					return {
						...list,
						[key]: {
							id: undefined,
							value:
								type === 'string'
									? defaultValue || ''
									: type === 'date'
									? defaultValue
										? new Date(defaultValue || '').toISOString()
										: null
									: type === 'boolean'
									? defaultValue?.toLowerCase() === 'true' || 'false'
									: type === 'number'
									? defaultValue || ''
									: '',
						},
					}
				}
			}, {}) || {},
	})

	const formContext = useForm<FormSchema>({
		initialValues,
		onSubmit: (formData) => {
			if (!entityId) return

			let newUnit = db.units.save({ entityId, ...(id ? { id } : {}) })

			Object.entries(formData.fields).forEach(([key, field]) => {
				let entityField = db.fields.find((x) => x.key === key)

				if (!entityField) return

				let save = initialValues.fields[entityField.key].value !== field.value

				if (save || !field.id) {
					// console.log('value is', typeof field.value)
					db.values.save({
						value: String(field.value),
						id: '',
						unitId: newUnit.id,
						fieldId: entityField?.id,
						created: String(new Date().valueOf()),
					})
				}
			})

			// navigate(`${process.env.PUBLIC_URL}/${entity?.key}`)
			window.history.back()
		},
	})

	const title = mode === 'create' ? `Create ${entity?.plural}` : `Edit ${entity?.name}`

	return (
		<Form context={formContext}>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entity?.key}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Stack>
					<Card>
						<Stack>
							{fields?.map((field) =>
								field?.type === 'string' ? (
									<FormInput
										key={field?.key}
										name={`fields.${field?.key}.value`}
										label={field?.name}
									/>
								) : field?.type === 'number' ? (
									<FormInput
										key={field?.key}
										name={`fields.${field?.key}.value`}
										label={field?.name}
										type="number"
									/>
								) : field?.type === 'date' ? (
									<FormDate
										key={field?.key}
										name={`fields.${field?.key}.value`}
										label={field?.name}
									/>
								) : field?.type === 'boolean' ? (
									<FormToggle
										key={field?.key}
										name={`fields.${field?.key}.value`}
										label={field?.name}
									/>
								) : field?.type === 'option' ? (
									<FormSelect
										key={field?.key}
										name={`fields.${field?.key}.value`}
										label={field?.name}
										options={field?.options.map((x) => ({ label: x, value: x })) || []}
									/>
								) : null
							)}
						</Stack>
					</Card>
					<div className="flex items-center justify-end">
						<Actions
							actions={[
								{
									title: mode === 'create' ? 'Create' : 'Save',
									intent: 'primary',
									icon: <CheckIcon className="w-5 h-5" />,
									type: 'submit',
									disabled: formContext.isSubmitting,
								},
							]}
						/>
					</div>
				</Stack>
			</PageContent>
		</Form>
	)
}

export default UnitForm
