import { isNotNone } from '@primer/types'
import db from 'db'
import { Model, Identifier, FK } from 'types/db'
import { workflowStepConnectionResource, WorkflowStepConnectionResource } from './'

export interface WorkflowStepModel extends Model {
	workflowId: FK
	targetWorkflow?: FK
	name: string
	description: string
	isTransition?: boolean
	isStart?: boolean
	posX?: number
	posY?: number
}

export interface WorkflowStepResource extends WorkflowStepModel {
	readonly connections: WorkflowStepConnectionResource[]
}

export const workflowStepResource = (id: Identifier<WorkflowStepModel>): WorkflowStepResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.workflowSteps.find(id) : id

	if (!data) return null

	return {
		...data,
		get connections() {
			return db.workflowStepConnections
				.all((x) => x.sourceId === data?.id)
				.map(workflowStepConnectionResource)
				.filter(isNotNone)
		},
	}
}
