import { FC, ReactNode, useRef } from 'react'
import clsx from 'clsx'
import ReactQuill from 'react-quill'
import { ArrowsExpandIcon, ChevronDownIcon, XIcon } from '@heroicons/react/outline'
import { FormValidations } from '@primer/components/Form/utils'
import FormEditor from '@primer/components/Form/FormEditor'
import FormLabel from '@primer/components/Form/FormLabel'
import Expandable from '@primer/components/Expandable'
import { templateTagActions } from 'pages/Settings/EmailTemplates/utils/actions'

interface FormTemplateEditorProps {
	name: string
	label?: ReactNode
	validations?: FormValidations
	focus: string
}

const FormTemplateEditor: FC<FormTemplateEditorProps> = ({ name, label, validations, focus }) => {
	const ref = useRef<ReactQuill>(null)

	const insertTag = (str: string) => {
		let editor = ref.current?.getEditor()
		let selection = editor?.getSelection()
		let text = editor?.getText()

		if (!editor || !text) return

		const index = selection?.index
		const length = selection?.length

		if (!index) {
			editor.insertText(editor.getText().length - 1, str)
		} else {
			if (length) editor.deleteText(index, length)
			editor.insertText(index, str)
		}
	}

	return (
		<Expandable>
			{(setExpanded, isExpanded) => (
				<div className={clsx(isExpanded && 'bg-white h-full')}>
					{!isExpanded && label && <FormLabel>{label}</FormLabel>}

					<FormEditor
						name={name}
						validations={validations}
						className={clsx(!isExpanded && label && 'mt-1', isExpanded && 'h-full pb-10')}
						ref={ref}
						actions={[
							{
								title: 'Insert Tag',
								icon: <ChevronDownIcon className="w-4 h-4" />,
								iconSide: 'right',
								intent: 'secondary',
								size: 'sm',
								actions: templateTagActions(insertTag, focus) || [],
							},
							{
								icon: isExpanded ? (
									<XIcon className="w-4 h-4 -mx-1" />
								) : (
									<ArrowsExpandIcon className="w-4 h-4 -mx-1" />
								),
								size: 'sm',
								onClick: () => setExpanded((x) => !x),
							},
						]}
					/>
				</div>
			)}
		</Expandable>
	)
}

export default FormTemplateEditor
