import { FC, Fragment } from 'react'
import { v4 as uuid } from 'uuid'
import { navigate, RouteComponentProps } from '@reach/router'
import Card from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Actions from '@primer/components/Actions'
import Stack from '@primer/components/Stack'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import FormLabel from '@primer/components/Form/FormLabel'
import FormDate from '@primer/components/Form/FormDate'
import FormSelect from '@primer/components/Form/FormSelect'
import FormSection from '@primer/components/Form/FormSection'
import FormToggle from '@primer/components/Form/FormToggle'
import AutoHeight from '@primer/components/AutoHeight'
import Tabs from '@primer/components/Tabs'
import { ArrowLeftIcon, BeakerIcon, CheckIcon, ChevronDownIcon, PlusIcon } from '@heroicons/react/outline'
import { XIcon } from '@heroicons/react/solid'
import { ActionType, isNotNone } from '@primer/types'
import {
	types,
	repeatOptions,
	yearInOptions,
	getMonthOnOptions,
	weekOnOptions,
	getRuleOperatorOptions,
	entityTiming,
	entityTimingType,
} from './utils/options'
import useFieldArray from '@primer/components/Form/useFieldArray'
import { watch } from '@primer/components/Form/utils'
import { useFormikContext } from 'formik'
import FormTemplateEditor from 'components/FormTemplateEditor'
import { templateTagActions } from 'pages/Settings/EmailTemplates/utils/actions'
import db from 'db'
import { confirm } from '@primer/alerts'

interface RuleType {
	entity: string
	field: string
	operator: string
	value: string
}

interface RuleGroupType {
	rules: RuleType[]
}

interface FormData {
	name: string
	enabled: boolean
	enabledDate: Date | undefined
	focus: string

	triggerType: string
	triggerRepeat: number

	triggerEvery: string
	triggerYearIn: string
	triggerMonthOn: string
	triggerWeekOn: string
	triggerDayAt: string

	triggerAction: string

	obeyTriggerTiming: boolean
	triggerTimingType: string
	triggerTiming: string
	triggerDate: string

	ruleGroups: RuleGroupType[]

	subject: string
	content: string
}

const NotificationForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const originalNotification = db.notifications.find(id)

	const formContext = useForm<FormData>({
		initialValues: {
			name: originalNotification?.name || '',
			focus: originalNotification?.focus || '',
			enabled: originalNotification?.enabled || false,
			enabledDate: originalNotification?.enabledDate || undefined,

			triggerType: originalNotification?.triggerType || 'action',
			triggerRepeat: originalNotification?.triggerRepeat || 1,

			triggerEvery: originalNotification?.triggerEvery || '',
			triggerYearIn: originalNotification?.triggerYearIn || '',
			triggerMonthOn: originalNotification?.triggerMonthOn || '',
			triggerWeekOn: originalNotification?.triggerWeekOn || '',
			triggerDayAt: originalNotification?.triggerDayAt || '',

			triggerAction: originalNotification?.triggerAction || '',

			obeyTriggerTiming: originalNotification?.obeyTriggerTiming || true,
			triggerTimingType: originalNotification?.triggerTimingType || '',
			triggerTiming: originalNotification?.triggerTiming || '',
			triggerDate: originalNotification?.triggerDate || '',

			ruleGroups: originalNotification?.ruleGroups || [],

			subject: originalNotification?.subject || '',
			content: originalNotification?.content || '',
		},
		onSubmit: (formData) => {
			db.notifications.save({
				id: uuid(),
				...(originalNotification || {}),
				...formData,
			})

			navigate('/settings/notifications')
		},
	})

	const {
		values: ruleGroups,
		arrayHelpers: { push: appendRuleGroup, remove: removeRuleGroup },
	} = useFieldArray('ruleGroups', formContext)

	// obeyTriggerTiming,

	const triggerType = watch('triggerType', formContext)
	const triggerRepeat = watch('triggerRepeat', formContext)
	const triggerEvery = watch('triggerEvery', formContext)
	const triggerYearIn = watch('triggerYearIn', formContext)
	const triggerMonthOn = watch('triggerMonthOn', formContext)
	const triggerWeekOn = watch('triggerWeekOn', formContext)
	const triggerTiming = watch('triggerTiming', formContext)
	const triggerTimingType = watch('triggerTimingType', formContext)
	const enabled = watch('enabled', formContext)
	const subject = watch('subject', formContext)
	const focus = watch('focus', formContext)

	const title = mode === 'create' ? 'Create Notification' : 'Edit Notification'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/notifications',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<Stack dividers>
						<FormSection title="Details" subtitle="Basic information about the notification">
							<Card>
								<Stack>
									<FormInput
										name="name"
										label="Name"
										validations={{ required: 'Name is required' }}
									/>

									<div className="flex items-center space-x-3">
										<FormLabel>Notification is about</FormLabel>
										<FormSelect
											name="focus"
											options={db.entities.all().map((x) => ({ value: x.id, label: x.name }))}
											validations={{ required: 'Focus is required' }}
											className="w-[150px]"
										/>
									</div>

									<FormToggle name="enabled" label="Enabled" />

									<AutoHeight show={!!enabled}>
										<FormDate name="enabledDate" label="Enabled from" />
									</AutoHeight>
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Trigger"
							subtitle={
								<div className="space-y-3">
									<div>Choose how to trigger the notification</div>
									<ul className="list-disc pl-5 text-xs">
										<li>
											<strong>Action</strong>: When a certain action occurs within the platform
										</li>
										<li>
											<strong>Repeat</strong>: On a recurring schedule
										</li>
										<li>
											<strong>Date</strong>: Relative to a defined date
										</li>
									</ul>
								</div>
							}
						>
							<Tabs
								value={triggerType}
								onChange={(x) => formContext.setFieldValue('triggerType', x)}
								intent="card"
								tabs={types}
							/>

							<div className="relative z-10">
								<Card>
									<Stack>
										<div>
											<Stack className={triggerType === 'action' ? '' : 'hidden'}>
												<div className="w-[300px]">
													<FormSelect
														name="triggerAction"
														options={db.actions
															.all((x) => x.entityId === focus)
															.map((x) => ({ value: x.id, label: x.name }))}
														// validations={{ required: 'Action is required' }}
														placeholder="Select action..."
													/>
												</div>
											</Stack>

											<Stack className={triggerType === 'repeat' ? '' : 'hidden'}>
												<div className="flex items-center space-x-3">
													<div className="flex items-center space-x-3">
														<FormLabel>Every</FormLabel>
														<div className="w-[60px]">
															<FormInput
																name="triggerRepeat"
																validations={{
																	required: 'required',
																	min: {
																		value: 1,
																		message: 'Every must be at least 1',
																	},
																}}
																placeholder=""
																type="number"
																inputProps={{
																	min: 1,
																}}
															/>
														</div>
														<div className="w-[100px]">
															<FormSelect
																name="triggerEvery"
																options={repeatOptions.map((x) => ({
																	label:
																		x.label + ((triggerRepeat || 1) > 1 ? 's' : ''),
																	value: x.value,
																}))}
																placeholder=""
															/>
														</div>
													</div>

													{triggerEvery === 'year' && (
														<div className="flex items-center space-x-3">
															<FormLabel>in</FormLabel>
															<div className="w-[100px]">
																<FormSelect
																	name="triggerYearIn"
																	options={yearInOptions}
																	validations={{
																		required: 'Time is required',
																	}}
																	placeholder=""
																/>
															</div>
														</div>
													)}

													{(triggerEvery === 'month' ||
														(triggerEvery === 'year' && isNotNone(triggerYearIn))) && (
														<div className="flex items-center space-x-3">
															<FormLabel>on the</FormLabel>
															<div className="w-[100px]">
																<FormSelect
																	name="triggerMonthOn"
																	options={
																		triggerEvery === 'month'
																			? (
																					getMonthOnOptions(
																						triggerYearIn || 'february'
																					) || []
																			  ).concat([
																					{
																						label: 'last day',
																						value: Infinity,
																					},
																			  ])
																			: getMonthOnOptions(
																					triggerYearIn || 'february'
																			  ) || []
																	}
																	validations={{
																		required: 'Time is required',
																	}}
																	placeholder=""
																/>
															</div>
														</div>
													)}

													{triggerEvery === 'week' && (
														<div className="flex items-center space-x-3">
															<FormLabel>on</FormLabel>
															<div className="w-[100px]">
																<FormSelect
																	name="triggerWeekOn"
																	options={weekOnOptions}
																	validations={{
																		required: 'Time is required',
																	}}
																	placeholder=""
																/>
															</div>
														</div>
													)}

													{(triggerEvery === 'day' ||
														isNotNone(triggerWeekOn) ||
														isNotNone(triggerMonthOn)) && (
														<div className="flex items-center space-x-3">
															<FormLabel>at</FormLabel>
															<div className="w-[130px]">
																<FormInput name="triggerDayAt" type="time" />
															</div>
														</div>
													)}
												</div>
											</Stack>

											<Stack className={triggerType === 'date' ? '' : 'hidden'}>
												<div className="flex items-center space-x-3">
													<div className="w-[60px]">
														<FormInput
															name="triggerRepeat"
															validations={{
																required: 'required',
																min: {
																	value: 1,
																	message: 'min 1',
																},
															}}
															placeholder=""
															type="number"
															inputProps={{
																min: 1,
															}}
														/>
													</div>

													<div className="w-[100px]">
														<FormSelect
															name="triggerTiming"
															options={entityTiming.map((x) => ({
																label: x.label + ((triggerRepeat || 1) > 1 ? 's' : ''),
																value: x.value,
															}))}
															placeholder=""
														/>
													</div>

													{triggerTiming && (
														<div className="w-[100px]">
															<FormSelect
																name="triggerTimingType"
																options={entityTimingType}
																validations={{ required: 'required' }}
																placeholder=""
															/>
														</div>
													)}

													{triggerTimingType && (
														<div className="w-[200px]">
															<FormSelect
																name="triggerDate"
																options={db.fields
																	.all(
																		(x) => x.entityId === focus && x.type === 'date'
																	)
																	.map((x) => ({ value: x.id, label: x.name }))}
																validations={{ required: 'required' }}
																placeholder=""
															/>
														</div>
													)}
												</div>
											</Stack>
										</div>
									</Stack>
								</Card>
							</div>
						</FormSection>

						<FormSection
							title="Conditions"
							subtitle="Fine grained controls over if the notification gets sent"
						>
							<Card>
								<Stack>
									{ruleGroups.map((x, i) => (
										<Fragment key={i}>
											<RuleGroup index={i} remove={() => removeRuleGroup(i)} />
											{i !== ruleGroups.length - 1 && (
												<div className="flex justify-center">
													<span className="text-sm font-medium px-4 py-1 rounded-full bg-indigo-600 text-white">
														OR
													</span>
												</div>
											)}
										</Fragment>
									))}

									<Actions
										actions={{
											title: 'Add condition set',
											icon: <PlusIcon className="w-5 h-5" />,
											onClick: () => appendRuleGroup({ rules: [] }),
										}}
									/>
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Template"
							subtitle={
								<div className="space-y-3">
									<div>Customise the content of the email message</div>
									<div>Use the tag reference to add placeholders for real values</div>
									<Actions
										actions={[
											{
												title: 'Import Template',
												icon: <ChevronDownIcon className="w-5 h-5" />,
												iconSide: 'right',
												placement: 'bottom-start',
												actions: db.templates.all().map<ActionType>((x) => ({
													title: x.name,
													onClick: async () => {
														let pass = false

														if (
															formContext.values.content !== '' &&
															formContext.values.content !== '<p><br></p>'
														) {
															await confirm({
																title: 'Are you sure?',
																message:
																	'This will overwrite your existing email content',
																intent: 'primary',
																onAccept: () => {
																	pass = true
																},
															})
														} else {
															pass = true
														}

														if (pass) {
															formContext.setFieldValue('content', x.content)
														}
													},
												})),
											},
										]}
									/>
								</div>
							}
						>
							<Card>
								<Stack>
									<div className="flex items-start">
										<FormInput
											name="subject"
											label="Subject line"
											validations={{ required: 'Subject line is required' }}
											className="flex-1 mr-3"
										/>
										<div className="mt-6">
											<Actions
												actions={{
													title: 'Insert Tag',
													icon: <ChevronDownIcon className="w-5 h-5" />,
													iconSide: 'right',
													actions:
														templateTagActions((x) => {
															formContext.setFieldValue(
																'subject',
																subject?.length ? `${subject}${x}` : `${x}`
															)
														}, focus) || [],
												}}
											/>
										</div>
									</div>

									<FormTemplateEditor
										name="content"
										label="Content"
										validations={{ required: 'Content is required' }}
										focus={focus}
									/>
								</Stack>
							</Card>
						</FormSection>

						{/* <AutoHeight show={focus !== '' && focus !== 'user'}>
							<FormSection title="Recipients" subtitle="Who will receive this email notification">
								<Card>
									<MultiColumns
										columns={2}
										items={[
											(focus === 'case' || focus === 'event') && (
												<FormCheckbox name="recipients.caseApplicant" label="Case Applicant" />
											),
											(focus === 'case' || focus === 'event') && (
												<FormCheckbox
													name="recipients.caseRespondent"
													label="Case Respondent"
												/>
											),
											focus === 'event' && (
												<FormCheckbox name="recipients.eventAttendee" label="Event Attendees" />
											),
										]}
									/>
								</Card>
							</FormSection>
						</AutoHeight> */}

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: 'Test',
										intent: 'secondary',
										icon: <BeakerIcon className="w-5 h-5" />,
										onClick: () => {},
										disabled: !isNotNone(focus) || true,
									},
									{
										title: mode === 'create' ? 'Create' : 'Save',
										intent: 'primary',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
										disabled: !isNotNone(focus),
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

const RuleGroup: FC<{ index: number; remove: () => void }> = ({ index, remove }) => {
	const formContext = useFormikContext<FormData>()

	const {
		values: rules,
		arrayHelpers: { push: appendRule, remove: removeRule },
	} = useFieldArray(`ruleGroups.${index}.rules`, formContext)

	return (
		<div className="relative">
			<div className="absolute top-0 right-0 -mr-3 -my-3">
				<Actions
					actions={{
						icon: <XIcon className="w-4 h-4 -mx-1 text-red-500" />,
						intent: 'secondary',
						size: 'sm',
						rounded: 'full',
						onClick: remove,
					}}
				/>
			</div>

			<Stack className="p-4 rounded-md bg-gray-100 dark:bg-gray-750">
				{rules.map((_, i) => (
					<Rule
						key={i}
						groupIndex={index}
						index={i}
						remove={() => removeRule(i)}
						prefix={i === 0 ? 'IF' : 'AND'}
					/>
				))}

				<Actions
					actions={{
						title: 'Add condition',
						icon: <PlusIcon className="w-5 h-5" />,
						onClick: () => appendRule({ entity: '', field: '', operator: '', value: '' }),
					}}
				/>
			</Stack>
		</div>
	)
}

const Rule: FC<{ groupIndex: number; index: number; prefix: string; remove: () => void }> = ({
	groupIndex,
	index,
	prefix,
	remove,
}) => {
	const formContext = useFormikContext<FormData>()

	const entity = watch(`ruleGroups.${groupIndex}.rules.${index}.entity`, formContext)
	const field = watch(`ruleGroups.${groupIndex}.rules.${index}.field`, formContext)
	const operator = watch(`ruleGroups.${groupIndex}.rules.${index}.operator`, formContext)
	const entityField = db.fields.find(field)
	const type = entityField?.type || 'string'
	const options = entityField?.options || []

	return (
		<div className="flex items-center justify-between space-x-3">
			<div className="flex items-center space-x-3 flex-1">
				<span className="text-sm font-medium text-center p-1 w-full max-w-[50px] rounded-full bg-white dark:bg-gray-800 text-indigo-600 dark:text-indigo-400 border border-gray-300 dark:border-gray-600">
					{prefix}
				</span>

				<FormSelect
					name={`ruleGroups.${groupIndex}.rules.${index}.entity`}
					options={db.entities.all().map((x) => ({ value: x.id, label: x.name }))}
					className="w-full max-w-[100px]"
				/>

				<FormSelect
					name={`ruleGroups.${groupIndex}.rules.${index}.field`}
					options={
						db.fields.all((x) => x.entityId === entity).map((x) => ({ value: x.id, label: x.name })) || []
					}
					className="w-full max-w-[200px]"
				/>

				<FormSelect
					name={`ruleGroups.${groupIndex}.rules.${index}.operator`}
					options={getRuleOperatorOptions(type) || []}
					className="w-full max-w-[200px]"
				/>

				{type === 'date' ? (
					<FormDate name={`ruleGroups.${groupIndex}.rules.${index}.value`} className="w-full max-w-[200px]" />
				) : type === 'string' && operator !== 'stringEmpty' && operator !== 'stringNotEmpty' ? (
					<FormInput
						name={`ruleGroups.${groupIndex}.rules.${index}.value`}
						className="w-full max-w-[200px]"
					/>
				) : type === 'option' ? (
					<FormSelect
						name={`ruleGroups.${groupIndex}.rules.${index}.value`}
						options={options.map((x) => ({ value: x, label: x }))}
						className="w-full max-w-[200px]"
					/>
				) : null}
			</div>

			<div>
				<Actions
					actions={{
						icon: <XIcon className="w-4 h-4 text-red-500 -mx-2" />,
						intent: 'invisible',
						onClick: remove,
					}}
				/>
			</div>
		</div>
	)
}

export default NotificationForm
