import { ReactNode } from 'react'
import { FormikConfig, Form as FormikForm, FormikProps, FormikHelpers, FormikProvider, useFormik } from 'formik'

type FormikRenderProp<T> = (props: FormikProps<T>) => ReactNode

export function isFormikRenderProp<T>(prop: any): prop is FormikRenderProp<T> {
	return typeof prop === 'function'
}

interface FormProps<T> {
	children: ReactNode | FormikRenderProp<T>
	context: FormikProps<T>
	className?: string
}

export function Form<T>({ children, className, context }: FormProps<T>) {
	return (
		<FormikProvider value={context}>
			<FormikForm className={className}>
				{isFormikRenderProp<T>(children) ? children(context) : children}
			</FormikForm>
		</FormikProvider>
	)
}

export default Form

export const useForm = <T,>({ onSubmit, ...props }: FormikConfig<T>) => {
	// https://github.com/formium/formik/issues/739
	const handleSubmit = async (formData: T, helpers: FormikHelpers<T>) => {
		await onSubmit(formData, helpers)
		helpers.setSubmitting(false)
	}

	const context = useFormik({
		onSubmit: handleSubmit,
		...props,
	})

	return context
}
