import { DragEventHandler, FC, forwardRef, MouseEventHandler, ReactNode, RefObject } from 'react'
import clsx, { ClassValue } from 'clsx'

interface CardProps {
	className?: ClassValue
	header?: ReactNode
	footer?: ReactNode
	children?: ReactNode
	onClick?: MouseEventHandler<HTMLDivElement>
	onMouseOver?: MouseEventHandler<HTMLDivElement>
	onMouseOut?: MouseEventHandler<HTMLDivElement>
	onDragOver?: DragEventHandler<HTMLDivElement>
	onDragEnter?: DragEventHandler<HTMLDivElement>
	onDragExit?: DragEventHandler<HTMLDivElement>
	onDragLeave?: DragEventHandler<HTMLDivElement>
	wrapperRef?: RefObject<HTMLDivElement>
}

const Card: FC<CardProps> = ({
	className,
	header,
	footer,
	children,
	onClick,
	onMouseOver,
	onMouseOut,
	onDragOver,
	onDragEnter,
	onDragExit,
	onDragLeave,
	wrapperRef,
}) => (
	<CardWrapper
		className={clsx(className)}
		onClick={onClick}
		onMouseOver={onMouseOver}
		onMouseOut={onMouseOut}
		onDragOver={onDragOver}
		onDragEnter={onDragEnter}
		onDragExit={onDragExit}
		onDragLeave={onDragLeave}
		ref={wrapperRef}
	>
		{header}
		{children && <CardContent>{children}</CardContent>}
		{footer && <CardFooter>{footer}</CardFooter>}
	</CardWrapper>
)

export const CardWrapper = forwardRef<HTMLDivElement, Omit<CardProps, 'header' | 'footer'>>(
	(
		{ className, children, onClick, onMouseOver, onMouseOut, onDragOver, onDragEnter, onDragExit, onDragLeave },
		ref
	) => (
		<div
			ref={ref}
			className={clsx('bg-white dark:bg-gray-700 shadow rounded-md', className)}
			onClick={onClick}
			onMouseOver={onMouseOver}
			onMouseOut={onMouseOut}
			onDragOver={onDragOver}
			onDragEnter={onDragEnter}
			onDragExit={onDragExit}
			onDragLeave={onDragLeave}
		>
			{children}
		</div>
	)
)

export const CardContent: FC<{ children: ReactNode; className?: ClassValue }> = ({ className, children }) => (
	<div className={clsx('px-4 py-5 sm:p-6', className)} data-card>
		{children}
	</div>
)

export const CardFooter: FC<{ children: ReactNode; className?: ClassValue }> = ({ className, children }) => (
	<div className={clsx('bg-gray-50 dark:bg-gray-750 sm:rounded-b-md px-4 py-4 sm:px-6', className)} data-card>
		{children}
	</div>
)

export default Card
