import clsx from 'clsx'
import Dragger from 'components/Builder/Dragger'
import { HandleIcon } from 'components/Icon'
import { FC } from 'react'
import { Tool } from '../utils'

const DraggableTool: FC<{ tool: Tool }> = ({ tool }) => {
	return (
		<Dragger
			type={tool.type}
			item={tool}
			className={({ isDragging }) =>
				clsx(
					'py-2 px-1 hover:pl-2.5 rounded-sm bg-white hover:ring-2 hover:ring-primary-500 transition-all duration-100 cursor-[grab] flex items-center justify-start space-x-3 hover:relative z-10',
					isDragging && 'opacity-50'
				)
			}
		>
			<HandleIcon className="w-5 h-5" />

			<div>
				<div className="text-sm font-medium">{tool.name}</div>
				<div className="text-xs text-gray-400">{tool.description}</div>
			</div>
		</Dragger>
	)
}

export default DraggableTool
