import { FC, useState } from 'react'
import { RouteComponentProps } from '@reach/router'
import logo from 'assets/img/logo-text.svg'
import { CardContent, CardFooter, CardWrapper } from '@primer/components/Card'
import Form, { useForm } from '@primer/components/Form/Form'
import Actions from '@primer/components/Actions'
import FormInput from '@primer/components/Form/FormInput'
import Stack from '@primer/components/Stack'
import { LoginIcon } from '@heroicons/react/outline'
import Tabs from '@primer/components/Tabs'
import FormSelect from '@primer/components/Form/FormSelect'
import { UnitModel, unitResource } from 'db/types'
import db from 'db'

const tabs = [
	{ label: 'Preset', value: 'preset' },
	{ label: 'Credentials', value: 'credentials' },
]

const userEntity = db.entities.find((x) => x.key === 'user')

const userOptions = db.units
	.all((x) => x.entityId === userEntity?.id)
	.map((x) => {
		let unit = unitResource(x)
		let firstNameField = unit?.entity?.fields.find((x) => x.key === 'first-name')
		let lastNameField = unit?.entity?.fields.find((x) => x.key === 'last-name')
		let firstName = unit?.value(firstNameField?.id || '')?.value
		let lastName = unit?.value(lastNameField?.id || '')?.value

		// console.log('firstName', unit?.value(firstNameField?.id || ''), unit?.values)

		return {
			label: firstName + ' ' + lastName,
			value: x,
		}
	})

interface FormSchema {
	username: string
	password: string
	user: UnitModel | null
}

const Login: FC<RouteComponentProps> = () => {
	const formContext = useForm<FormSchema>({
		initialValues: {
			username: '',
			password: '',
			user: null,
		},
		onSubmit: (formData) => {
			console.log('formData', formData)
		},
	})

	const [tab, setTab] = useState(tabs[0].value)

	return (
		<div className="bg-gray-900 min-h-screen flex items-center justify-center">
			<div className="max-w-2xl w-full space-y-6">
				<img src={logo} className="h-16" alt="logo" />
				<Form context={formContext}>
					<CardWrapper>
						<CardContent>
							<Stack>
								<Tabs tabs={tabs} value={tab} onChange={setTab} />

								{tab === 'preset' && (
									<Stack className="p-2 -m-2">
										<FormSelect name="user" label="User" options={userOptions} />
									</Stack>
								)}

								{tab === 'credentials' && (
									<Stack className="p-2 -m-2">
										<FormInput
											name="username"
											label="Email Address"
											validations={{ required: 'Email Address is required' }}
										/>
										<FormInput
											name="password"
											label="Password"
											type="password"
											validations={{ required: 'Password is required' }}
										/>
									</Stack>
								)}
							</Stack>
						</CardContent>
						<CardFooter>
							<div className="flex justify-end">
								<Actions
									actions={[
										{
											title: 'Login',
											icon: <LoginIcon className="w-5 h-5" />,
											intent: 'primary',
											type: 'submit',
										},
									]}
								/>
							</div>
						</CardFooter>
					</CardWrapper>
				</Form>
			</div>
		</div>
	)
}

export default Login
