import { FC, useContext } from 'react'
import SectionHeader from '@primer/components/SectionHeader'
import Stack from '@primer/components/Stack'
import { PageRendererContext } from 'components/PageBuilder/utils'
import { view, lensPath } from 'ramda'
import { SectionWidget } from '.'
import { ViewComp } from '..'
import { Path } from '../types'
import clsx from 'clsx'

const View: FC<{ path: Path }> = ({ path }) => {
	let { itemList } = useContext(PageRendererContext)
	let item: SectionWidget = view(lensPath(path), itemList)

	console.log(item)

	return (
		<div className={clsx(item.data.bottomBorder && 'border-b border-gray-200 pb-6', 'px-6')}>
			<Stack>
				{item.data.title && <SectionHeader title={item.data.title} />}
				{item.data.items.map((_, i) => (
					<ViewComp key={i} path={[...path, 'data', 'items', i]} />
				))}
			</Stack>
		</div>
	)
}

export default View
