import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { EntityResource, entityResource } from '.'

export type RelationType = 'one-to-one' | 'one-to-many' | 'many-to-many'

export interface RelationModel extends Model {
	key: string
	name: string
	entityId: FK
	relatedId: FK
	type: RelationType
}

export interface RelationResource extends RelationModel {
	readonly entity: EntityResource | null
	readonly related: EntityResource | null
}

export const relationResource = (id: Identifier<RelationModel>): RelationResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.relations.find(id) : id

	if (!data) return null

	return {
		...data,
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
		get related() {
			return entityResource((x) => x.id === data?.relatedId)
		},
	}
}
