import { FC, useContext } from 'react'
import { PageRendererContext } from 'components/PageBuilder/utils'
import { view, lensPath } from 'ramda'
import { ColumnsWidget } from '.'
import { ViewComp } from '..'
import { Path } from '../types'
import clsx from 'clsx'
import Stack from '@primer/components/Stack'

const View: FC<{ path: Path }> = ({ path }) => {
	let { itemList } = useContext(PageRendererContext)
	let item: ColumnsWidget = view(lensPath(path), itemList)

	return (
		<div className="flex space-x-4">
			<Stack
				className={clsx({
					'w-1/2': item.data.format === '1:1',
					'w-1/3': item.data.format === '1:2',
					'w-2/3': item.data.format === '2:1',
				})}
			>
				{item.data.items[0].map((_, i) => (
					<ViewComp key={i} path={[...path, 'data', 'items', 0, i]} />
				))}
			</Stack>
			<Stack
				className={clsx({
					'w-1/2': item.data.format === '1:1',
					'w-1/3': item.data.format === '2:1',
					'w-2/3': item.data.format === '1:2',
				})}
			>
				{item.data.items[1].map((_, i) => (
					<ViewComp key={i} path={[...path, 'data', 'items', 1, i]} />
				))}
			</Stack>
		</div>
	)
}

export default View
