import { useState, useEffect, ReactNode } from 'react'
import clsx, { ClassValue } from 'clsx'
import { useDrop } from 'react-dnd'
import { distance as calcDistance } from './utils'

interface DropperProps<T> {
	accept: string | string[]
	onDrop: (x: T) => void
	distance?: number
	className?: ClassValue | ((x: { isOver: boolean; isClose: boolean; canDrop: boolean }) => ClassValue)
	children: ReactNode | ((x: { isOver: boolean; isClose: boolean; canDrop: boolean }) => ReactNode)
}

function Dropper<T>({ accept, onDrop, distance = 20, className, children }: DropperProps<T>) {
	const [{ isOver, canDrop }, dropRef] = useDrop({
		accept,
		drop: onDrop,
		collect: (monitor) => ({
			isOver: !!monitor.isOver(),
			canDrop: !!monitor.canDrop(),
		}),
	})

	const [x, setX] = useState(0)
	const [y, setY] = useState(0)

	useEffect(() => {
		const handleMove = (e: MouseEvent) => {
			setX(e.pageX)
			setY(e.pageY)
		}
		window.addEventListener('mousemove', handleMove)
		return () => {
			window.removeEventListener('mousemove', handleMove)
		}
	}, [])

	useEffect(() => {
		const handleMove = (e: MouseEvent) => {
			e = e || window.event
			setX(e.pageX)
			setY(e.pageY)
		}
		window.addEventListener('dragover', handleMove)
		return () => {
			window.removeEventListener('dragover', handleMove)
		}
	}, [])

	let [isClose, setClose] = useState(false)

	return (
		<div
			ref={dropRef}
			className={clsx(
				'relative',
				typeof className === 'function' ? className({ isOver, isClose, canDrop }) : className
			)}
		>
			<div
				ref={(element: HTMLDivElement | null) => {
					if (element) {
						let d = calcDistance({ x, y }, element.getBoundingClientRect())
						let withinDistance = d < distance
						if (withinDistance !== isClose) {
							setClose(withinDistance)
						}
					}
				}}
				className="w-full h-full"
			>
				{typeof children === 'function' ? children({ isOver, isClose, canDrop }) : children}
			</div>
		</div>
	)
}

export default Dropper
