import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { RoleResource, EntityResource, roleResource, entityResource } from '.'

export interface EntityPermissionModel extends Model {
	roleId: FK
	entityId: FK
	view: boolean
	modify: boolean
	remove: boolean
}

export interface EntityPermissionResource extends EntityPermissionModel {
	readonly role: RoleResource | null
	readonly entity: EntityResource | null
}

export const entityPermissionResource = (id: Identifier<EntityPermissionModel>): EntityPermissionResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.entityPermissions.find(id) : id

	if (!data) return null

	return {
		...data,
		get role() {
			return roleResource((x) => x.id === data?.roleId)
		},
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
	}
}
