import { FC, useState } from 'react'
import { v4 as uuid } from 'uuid'
import PageHeading from '@primer/components/PageHeading'
import { RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Stack from '@primer/components/Stack'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import Card from '@primer/components/Card'
import clsx from 'clsx'
import DataListColumns from '@primer/components/DataListColumns'
import Checkbox from '@primer/components/Checkbox'

const modules = [
	{ id: uuid(), name: 'Shared Workspace' },
	{ id: uuid(), name: 'Document Production' },
	{ id: uuid(), name: 'Document Exchange' },
	{ id: uuid(), name: 'Video Conferencing' },
	{ id: uuid(), name: 'Peer Review' },
]

const AddModule: FC<RouteComponentProps> = () => {
	const [selected, setSelected] = useState<string[]>([])

	return (
		<>
			<PageHeading
				pageTitle="Add Module"
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/modules',
							}}
						/>
						<div>Add Module</div>
					</div>
				}
			/>

			<PageContent>
				<Stack>
					<Card>
						<div className="grid grid-cols-3 gap-6">
							{modules.map((x) => (
								<div
									key={x.id}
									className={clsx(
										'border rounded relative cursor-pointer shadow-sm group',
										'col-span-1 min-h-[300px]',
										{
											'bg-white dark:bg-gray-750 hover:bg-gray-50 border-gray-200 dark:border-gray-600':
												!selected.includes(x.id),
											'bg-primary-50 dark:bg-primary-500 dark:bg-opacity-25 hover:bg-primary-100 border-primary-500 dark:border-primary-500 ring-1 ring-primary-500':
												selected.includes(x.id),
										}
									)}
									onClick={() => {
										if (selected.includes(x.id)) {
											setSelected((s) => s.filter((y) => y !== x.id))
										} else {
											setSelected((s) => [...s, x.id])
										}
									}}
								>
									<Stack className="p-4">
										<DataListColumns
											items={[
												{
													value: <span className="text-lg font-medium">{x.name}</span>,
												},
												{
													title: 'Includes',
													value: (
														<ul className="list-disc pl-4">
															<li>3 Entities</li>
															<li>5 Templates</li>
															<li>5 Notifications</li>
														</ul>
													),
												},
											]}
											columns={1}
										/>
										<button className="text-primary-600 text-sm font-medium underline">
											View More
										</button>
									</Stack>
									<div className="p-4 absolute top-0 right-0 pointer-events-none">
										<Checkbox
											checked={selected.includes(x.id)}
											onChange={(e) => {
												if (e.target.checked) {
													setSelected((s) => s.filter((y) => y !== x.id))
												} else {
													setSelected((s) => [...s, x.id])
												}
											}}
										/>
									</div>
								</div>
							))}
						</div>
					</Card>

					<div className="flex justify-end">
						<Actions
							actions={{
								title: 'Save',
								icon: <CheckIcon className="w-5 h-5" />,
								intent: 'save',
								onClick: () => {},
							}}
						/>
					</div>
				</Stack>
			</PageContent>
		</>
	)
}

export default AddModule
