import { isNotNone } from '@primer/types'
import db from 'db'
import { Model, Identifier } from 'types/db'
import { workflowStepResource, WorkflowStepResource } from '.'

export interface WorkflowModel extends Model {
	name: string
}

export interface WorkflowResource extends WorkflowModel {
	readonly steps: WorkflowStepResource[]
}

export const workflowResource = (id: Identifier<WorkflowModel>): WorkflowResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.workflows.find(id) : id

	if (!data) return null

	return {
		...data,
		get steps() {
			return db.workflowSteps
				.all((x) => x.workflowId === data?.id)
				.map(workflowStepResource)
				.filter(isNotNone)
		},
	}
}
