import { FC, Suspense, useMemo, useRef, useState } from 'react'
import clsx from 'clsx'
import { RouteComponentProps, Link } from '@reach/router'
import Loader from '@primer/components/Loader'
import Sidebar from '@primer/components/Sidebar'
import MobileSidebar from '@primer/components/MobileSidebar'
import { CogIcon, MenuIcon, QuestionMarkCircleIcon, ViewGridIcon } from '@heroicons/react/outline'
import Topbar from '@primer/components/Topbar'
import OnRouteChange from '@primer/components/OnRouteChange'
import versions from '@primer/utils/versions'
import semver from 'semver'
import { site } from 'config'
import db from 'db'
import { FullLogo } from 'components/Logo'
import { NavListItems } from '@primer/components/NavList'

const PageSidebar: FC<RouteComponentProps> = ({ children }) => {
	const [isOpen, setOpen] = useState(false)
	const ref = useRef<HTMLDivElement>(null)

	const releases = versions.sort((a, b) => (semver.gt(a.number, b.number) ? -1 : 1))

	const entities = db.entities.all()

	const items: NavListItems = useMemo(
		() => [
			{
				title: 'Home',
				to: '/home',
				icon: <ViewGridIcon className="w-5 h-5" />,
			},
			...entities.map((entity) => ({
				title: entity.plural || entity.name,
				to: `/${entity.key}`,
				icon: <QuestionMarkCircleIcon className="w-5 h-5" />,
			})),
			{
				title: 'Settings',
				to: '/settings',
				icon: <CogIcon className="w-5 h-5" />,
			},
		],
		[entities]
	)

	return (
		<div className="h-screen flex flex-col md:flex-row md:overflow-hidden bg-gray-200 dark:bg-gray-800">
			<Sidebar
				name="main"
				intent="dark"
				minimizable
				items={items}
				className="hidden md:flex"
				header={(min) => (
					<div
						className={clsx(
							'h-16 mb-4 flex items-center bg-white bg-opacity-5 border-b border-white border-opacity-10 transition-all',
							min ? 'px-2.5' : 'px-5'
						)}
					>
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<FullLogo className="h-10" />
						</Link>
					</div>
				)}
				footer={(min) => (
					<div className={clsx('flex justify-between items-center', min ? 'flex-col space-y-2' : 'p-4')}>
						<div
							className={clsx(
								'text-gray-400 text-sm font-medium cursor-default',
								min ? 'text-center' : ''
							)}
						>
							{!min ? `Version: ${releases[0].number}` : `v${releases[0].number}`}
						</div>

						<SiteBadge min={min} />
					</div>
				)}
			/>

			<MobileSidebar
				isOpen={isOpen}
				setOpen={setOpen}
				intent="dark"
				items={items}
				header={
					<div className="px-2 my-2">
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<FullLogo className="h-11" />
						</Link>
					</div>
				}
				footer={
					<div className="flex justify-between items-center p-4">
						<div className="text-gray-400 text-sm font-medium cursor-default">
							Version: {releases[0].number}
						</div>

						<SiteBadge />
					</div>
				}
			/>

			<div className="flex flex-col flex-1 md:w-0 md:overflow-hidden">
				<main className="flex-1 relative z-0 md:overflow-y-auto focus:outline-none" ref={ref}>
					<Topbar>
						<button
							type="button"
							className="-ml-0.5 -mt-0.5 h-12 w-12 md:hidden inline-flex items-center justify-center rounded-md text-gray-500 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500"
							onClick={() => setOpen(true)}
						>
							<span className="sr-only">Open sidebar</span>
							<MenuIcon className="h-6 w-6" aria-hidden="true" />
						</button>
					</Topbar>

					<Suspense
						fallback={
							<div className="flex justify-center items-center h-screen">
								<Loader />
							</div>
						}
					>
						<div className="relative z-10 px-4 sm:px-0">{children}</div>
					</Suspense>
				</main>
			</div>

			<OnRouteChange
				action={() => {
					if (ref?.current?.scrollTo) ref.current.scrollTo(0, 0)
				}}
			/>
		</div>
	)
}

interface SiteBadgeProps {
	min?: boolean
}

const SiteBadge: FC<SiteBadgeProps> = ({ min }) => {
	return site === 'livetest' ? (
		<div
			className={clsx(
				'py-0.5 -my-0.5 font-medium text-sm leading-6 text-red-300 bg-red-400 dark:bg-red-500 bg-opacity-25 dark:bg-opacity-25',
				min ? 'w-full text-center' : 'px-2.5 rounded-full'
			)}
		>
			LiveTest
		</div>
	) : site === 'demo' ? (
		<div
			className={clsx(
				'py-0.5 -my-0.5 font-medium text-sm leading-6 text-green-300 bg-green-400 dark:bg-green-500 bg-opacity-25 dark:bg-opacity-25',
				min ? 'w-full text-center' : 'px-2.5 rounded-full'
			)}
		>
			Demo
		</div>
	) : site === 'staging' ? (
		<div
			className={clsx(
				'py-0.5 -my-0.5 font-medium text-sm leading-6 text-green-300 bg-green-400 dark:bg-green-500 bg-opacity-25 dark:bg-opacity-25',
				min ? 'w-full text-center' : 'px-2.5 rounded-full'
			)}
		>
			Staging
		</div>
	) : site === 'dev' ? (
		<div
			className={clsx(
				'py-0.5 -my-0.5 font-medium text-sm leading-6 text-indigo-300 bg-indigo-400 dark:bg-indigo-500 bg-opacity-25 dark:bg-opacity-25',
				min ? 'w-full text-center' : 'px-2.5 rounded-full'
			)}
		>
			Dev
		</div>
	) : null
}

export default PageSidebar
