import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { ColumnsType } from '../types'
import openEdit from './Edit'
import Build from './Build'
import View from './View'

export interface ColumnsWidget extends Widget {
	type: typeof ColumnsType
	data: {
		items: [Widget[], Widget[]]
		format: '1:1' | '1:2' | '2:1'
	}
}

export const isColumnsWidget = (x: any): x is ColumnsWidget => x.type === ColumnsType

export const ColumnsTool: Tool<ColumnsWidget> = {
	name: 'Columns',
	type: ColumnsType,
	description: 'Display a value',
	create: () => ({
		id: uuid(),
		type: ColumnsType,
		data: {
			items: [[], []],
			format: '1:1',
		},
	}),
}

export const ColumnsBuild = Build
export const ColumnsView = View
export const openEditColumns = openEdit
