import { FC } from 'react'
import { v4 as uuid } from 'uuid'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, BeakerIcon, CheckIcon } from '@heroicons/react/outline'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import { CardWrapper, CardContent, CardFooter } from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import FormTemplateEditor from 'components/FormTemplateEditor'
import db from 'db'

interface FormData {
	name: string
	content: string
}

const EmailTemplateForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const originalTemplate = db.templates.find(id)

	const formContext = useForm<FormData>({
		initialValues: {
			name: originalTemplate?.name || '',
			content: originalTemplate?.content || '',
		},
		onSubmit: (formData) => {
			db.templates.save({
				id: uuid(),
				...(originalTemplate || {}),
				...formData,
			})

			navigate(`${process.env.PUBLIC_URL}/settings/email-templates`)
		},
	})

	const title = mode === 'create' ? 'Create Template' : 'Edit Template'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/email-templates',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<CardWrapper>
						<CardContent>
							<Stack>
								<FormInput name="name" label="Name" validations={{ required: 'Name is required' }} />

								<FormTemplateEditor
									name="content"
									label="Content"
									validations={{ required: 'Content is required' }}
									focus={'all'}
								/>
							</Stack>
						</CardContent>

						<CardFooter>
							<div className="flex items-center justify-end">
								<Actions
									actions={[
										{
											title: 'Test',
											intent: 'secondary',
											icon: <BeakerIcon className="w-5 h-5" />,
											onClick: () => {},
											disabled: true,
										},
										{
											title: mode === 'create' ? 'Create' : 'Save',
											intent: 'primary',
											icon: <CheckIcon className="w-5 h-5" />,
											type: 'submit',
										},
									]}
								/>
							</div>
						</CardFooter>
					</CardWrapper>
				</Form>
			</PageContent>
		</>
	)
}

export default EmailTemplateForm
