import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { SectionType } from '../types'
import openEdit from './Edit'

import Build from './Build'
import View from './View'

export interface SectionWidget extends Widget {
	type: typeof SectionType
	data: {
		title?: string
		bottomBorder?: boolean
		items: Widget[]
	}
}

export const isSectionWidget = (x: any): x is SectionWidget => x.type === SectionType

export const SectionTool: Tool<SectionWidget> = {
	name: 'Section',
	type: SectionType,
	description: 'A box used for containing content',
	create: () => ({
		id: uuid(),
		type: SectionType,
		data: {
			title: '',
			bottomBorder: false,
			items: [],
		},
	}),
}

export const SectionBuild = Build
export const SectionView = View
export const openEditSection = openEdit
