import { FC, Suspense } from 'react'
import { Router, Redirect, RouteComponentProps } from '@reach/router'
import db from 'db'

import PageSidebar from '@primer/components/Layouts/PageSidebar'
import PageTopbar from '@primer/components/Layouts/PageTopbar'

import Login from 'pages/Login/Login'

// logged in
import Home from 'pages/Home'
import NotFound from 'pages/NotFound'

// entity
import Units from 'pages/Units/Units'
import ViewUnit from 'pages/Units/ViewUnit'
import UnitForm from 'pages/Units/UnitForm'
import UnitHistory from 'pages/Units/UnitHistory'

// entity value
import ValueHistory from 'pages/Values/ValueHistory'

// settings
import Settings from 'pages/Settings/Settings'

// notifications
import Notifications from 'pages/Settings/Notifications/Notifications'
import NotificationForm from 'pages/Settings/Notifications/NotificationForm'

// pages
import Pages from 'pages/Settings/Pages/Pages'
import PageForm from 'pages/Settings/Pages/PageForm'

// forms
import Forms from 'pages/Settings/Forms/Forms'

// email templates
import EmailTemplates from 'pages/Settings/EmailTemplates/EmailTemplates'
import EmailTemplateForm from 'pages/Settings/EmailTemplates/EmailTemplateForm'

// roles
import Roles from 'pages/Settings/Roles/Roles'
import RoleForm from 'pages/Settings/Roles/RoleForm'

// entities
import Entities from 'pages/Settings/Entities/Entities'
import EntityForm from 'pages/Settings/Entities/EntityForm'

// workflows
import Workflows from 'pages/Settings/Workflows/Workflows'
import WorkflowForm from 'pages/Settings/Workflows/WorkflowForm'

// modules
import Modules from 'pages/Settings/Modules/Modules'
import AddModule from 'pages/Settings/Modules/AddModule'

// site settings
import SiteSettings from 'pages/Settings/SiteSettings/SiteSettings'
import SiteSettingsForm from 'pages/Settings/SiteSettings/SiteSettingsForm'

import Test from 'pages/Test'
// import FormBuilder from 'components/FormBuilder/FormBuilder'

const isDev = !process.env.NODE_ENV || process.env.NODE_ENV === 'development'

const Route: FC<RouteComponentProps> = ({ children }) => <>{children}</>

console.log('process.env', process.env)

const Router2 = () => {
	const Wrapper = db.siteSettings.get('layout') === 'sidebar' ? PageSidebar : PageTopbar

	const entities = db.entities.all()

	return (
		<Suspense fallback={null}>
			<Router basepath={process.env.PUBLIC_URL}>
				<Login path="/login" />

				{/* <FormBuilder path={`/form-builder`} /> */}

				<WorkflowForm path="/settings/workflows/create" mode="create" />
				<WorkflowForm path="/settings/workflows/:id/edit" mode="edit" />

				<Wrapper path="/">
					<Redirect from="/" to="home" noThrow={isDev} />
					<NotFound default />
					<Home path="/home" />
					<Home path="/events" />
					<Home path="/users" />

					{entities.map((entity) => (
						<Route key={entity.id} path={`/${entity.key}`}>
							<NotFound default />
							<Units path="/" entityId={entity.id} />
							<ViewUnit path=":id" />
							<UnitForm path="/create" entityId={entity.id} mode="create" />
							<UnitHistory path=":id/history" />
							<UnitForm path=":id/edit" entityId={entity.id} mode="edit" />
							{/* <PageBuilder path=":id/plan/:name" /> */}
						</Route>
					))}

					<ValueHistory path="/value-history/:id" />

					<Settings path="/settings">
						<NotFound default />
						<Redirect from="/" to="entities" noThrow={isDev} />

						<Route path="/entities">
							<NotFound default />
							<Entities path="/" />
							<EntityForm path="/create" mode="create" />
							<EntityForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/roles">
							<NotFound default />
							<Roles path="/" />
							<RoleForm path="/create" mode="create" />
							<RoleForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/pages">
							<NotFound default />
							<Pages path="/" />
							<PageForm path="/create" mode="create" />
							<PageForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/forms">
							<NotFound default />
							<Forms path="/" />
						</Route>

						<Route path="/email-templates">
							<NotFound default />
							<EmailTemplates path="/" />
							<EmailTemplateForm path="/create" mode="create" />
							<EmailTemplateForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/notifications">
							<NotFound default />
							<Notifications path="/" />
							<NotificationForm path="/create" mode="create" />
							<NotificationForm path=":id/edit" mode="edit" />
						</Route>

						<Route path="/workflows">
							<NotFound default />
							<Workflows path="/" />
						</Route>

						<Route path="/modules">
							<NotFound default />
							<Modules path="/" />
							<AddModule path="/add" />
						</Route>

						<Route path="/site-settings">
							<NotFound default />
							<SiteSettings path="/" />
							<SiteSettingsForm path="/edit" />
						</Route>
					</Settings>

					{/* <PageBuilder path="/page-builder" /> */}
				</Wrapper>
				<Test path="/test" />
			</Router>
		</Suspense>
	)
}

export default Router2
