export type Unsaved<T> = Omit<T, 'id'>

export const isUnsaved = <T>(x: any): x is Unsaved<T> =>
	typeof x === 'object' && (typeof x.id === 'undefined' || x.id === '')
export const isSaved = <T>(x: any): x is Unsaved<T> => typeof x === 'object' && typeof x.id === 'string'

export type Identifier<T> = string | ((x: T) => boolean) | T

export type PK = string
export type FK = string

export interface Model {
	id: PK
	created?: string
}
