import { FC, Fragment, useState } from 'react'
import PageContent from '@primer/components/PageContent'
import Stack from '@primer/components/Stack'
import { getChildItems, PageBuilder2Provider, Tool } from './utils'
import DropTarget from './components/DropTarget'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, CheckIcon, TemplateIcon } from '@heroicons/react/outline'
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { entityResource, PageModel } from 'db/types'
import { Widget } from './utils'
import { builds } from './Widgets'
import DraggableTool from './components/DraggableTool'
import { isNotNone } from '@primer/types'

export type PageStructure = Pick<PageModel, 'title' | 'structure'>

interface PageBuilder2Props {
	page: PageModel
	onSave: (x: PageStructure) => void | Promise<void>
	entityId?: string
	tools: Record<string, Tool>
}

const PageBuilder2: FC<PageBuilder2Props> = ({ page, onSave, entityId, tools }) => {
	const entity = entityResource(entityId || '')

	const [title, setTitle] = useState(page?.title || 'Page title')

	const [items, setItems] = useState<Widget[]>(JSON.parse(page?.structure || 'null') || [])

	const save = () => {
		let structure = JSON.stringify(items)

		onSave({
			title,
			structure,
		})
	}

	// console.log(
	// 	'items',
	// 	items.sort((a, b) => a.index - b.index)
	// )

	const addItem = (data: Widget) => {
		// console.log('data', data)
		setItems((list) => {
			return [
				...list.map((x) => {
					if (x.parentId !== data.parentId) return x
					return {
						...x,
						index: x.index < data.index ? x.index : x.index + 1,
					}
				}),
				data,
			]
		})

		// interesting feature, might get annoying?
		// // @ts-ignore
		// editors[data.type]({ path, item: data, removeItem, updateItem })
	}

	const removeItem = (data: Widget) => {
		setItems((list) => {
			return list
				.map((x) => {
					if (x.id === data.id || x.parentId === data.id) return null
					return {
						...x,
						index: x.index > data.index ? x.index - 1 : x.index,
					}
				})
				.filter(isNotNone)
		})
	}

	const updateItem = (newValue: Widget) => {
		setItems((l) => l.map((x) => (x.id === newValue.id ? newValue : x)))
	}

	const baseItems = getChildItems(items, undefined)

	return (
		<div>
			<DndProvider backend={HTML5Backend}>
				<PageBuilder2Provider value={{ entity, items, addItem, removeItem, updateItem }}>
					<div className="flex flex-col bg-gray-200 dark:bg-gray-800">
						<div className="flex-1 flex">
							<div className="w-80 min-w-80 bg-white border-b border-gray-100">
								<div className="p-4 size-lg font-medium border-b border-gray-100 flex items-center space-between space-x-4">
									<TemplateIcon className="w-5 h-5 text-gray-500" />
									<div className="flex-1">Page Elements</div>
								</div>

								<Stack className="p-4">
									<div>
										{Object.entries(tools).map(([type, tool]) => (
											<Fragment key={type}>
												<DraggableTool tool={tool} />
											</Fragment>
										))}
									</div>

									<div className="group flex items-center h-6 w-full">
										<div className="group-hover:ring-2 ring-primary-500 transition-all duration-100 h-1 group-hover:bg-primary-500 rounded-full w-full" />
									</div>
								</Stack>
							</div>

							<PageContent className="py-6">
								<div className="flex items-center space-x-1 mb-6 mt-2">
									<Actions
										actions={{
											icon: <ArrowLeftIcon className="w-5 h-5 -mx-2 text-primary-500" />,
											intent: 'borderless',
											rounded: 'full',
											onClick: () => {},
										}}
									/>

									<input
										type="text"
										value={title}
										onChange={(e) => setTitle(e.target.value)}
										className="flex-1 flex items-center text-2xl leading-8 font-semibold text-gray-900 dark:text-white sm:text-3xl sm:leading-9 whitespace-nowrap bg-transparent border-none focus:ring-2 focus:ring-primary-500 rounded-md"
									/>
								</div>

								<Stack space={2}>
									{baseItems.map((item, i) => (
										<Fragment key={item.id}>
											<DropTarget index={i} isFirst={i === 0} accept={Object.keys(tools)} />
											{(() => {
												let BuildComp = builds[item.type]

												if (BuildComp) {
													return <BuildComp id={item.id} />
												}
												return null
											})()}
										</Fragment>
									))}

									<DropTarget
										index={baseItems.length}
										isFirst={baseItems.length === 0}
										isLast
										accept={Object.keys(tools)}
									/>
								</Stack>
							</PageContent>
						</div>
					</div>
				</PageBuilder2Provider>
			</DndProvider>

			<div className="p-4 flex justify-end">
				<Actions
					actions={[
						{
							title: 'Save',
							intent: 'save',
							icon: <CheckIcon className="w-5 h-5" />,
							onClick: save,
						},
					]}
				/>
			</div>
		</div>
	)
}

export default PageBuilder2
