import { FC } from 'react'
import { TrashIcon, CheckIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Stack from '@primer/components/Stack'
import openSlideout, { SlideoutHeader, SlideoutContent } from '@primer/hooks/openSlideout'
import { SlideoutResolveFn } from '@primer/types'
import { CardWidget } from '.'
import TableFrame from '@primer/components/TableFrame'
import Table, { TableSchema } from '@primer/components/Table'
import db from 'db'
import { RoleModel } from 'db/types'
import FormLabel from '@primer/components/Form/FormLabel'

interface OpenEditProps {
	item: CardWidget
	removeItem: (item: CardWidget) => void
	updateItem: (newValue: CardWidget) => void
}

const openEdit = (props: OpenEditProps) => {
	return openSlideout({
		// size: '2xl',
		render: (close) => <OpenEdit close={close} {...props} />,
	})
}

type FormSchema = CardWidget

const OpenEdit: FC<OpenEditProps & { close: SlideoutResolveFn }> = ({ item, removeItem, updateItem, close }) => {
	const formContext = useForm<FormSchema>({
		initialValues: item as FormSchema,
		onSubmit: (formData) => {
			console.log('new Data', formData)
			updateItem(formData)
			close()
		},
	})

	const permissionsSchema: TableSchema<RoleModel> = {
		cols: [
			{
				title: 'Role',
				value: (x) => x.name,
			},
			{
				title: 'Can View',
				value: () => (
					<div className="flex justify-center w-full">
						<input type="checkbox" className="form-checkbox text-primary-500" defaultChecked />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	}

	return (
		<Form context={formContext} className="contents">
			<SlideoutHeader title="Edit Card" />

			<SlideoutContent>
				<Stack className="py-6">
					<div className="px-6">
						<Stack>
							<FormInput name="data.title" label="Title" className="flex-1" autoFocus />
						</Stack>
					</div>

					<div className="px-6">
						<FormLabel>Permissions</FormLabel>
						<TableFrame className="mt-1">
							<Table schema={permissionsSchema} items={db.roles.all()} className="-my-px" />
						</TableFrame>
					</div>
				</Stack>
			</SlideoutContent>

			<div className="p-6 border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-750 flex justify-between">
				<Actions
					actions={[
						{
							title: 'Delete',
							icon: <TrashIcon className="w-5 h-5" />,
							intent: 'danger',
							onClick: () => {
								removeItem(item)
								close()
							},
						},
					]}
				/>

				<Actions
					actions={[
						{
							title: 'Cancel',
							onClick: () => close(),
						},
						{
							title: 'Save',
							icon: <CheckIcon className="w-5 h-5" />,
							intent: 'save',
							type: 'submit',
						},
					]}
				/>
			</div>
		</Form>
	)
}

export default openEdit
