import { FC } from 'react'
import openSlideout, { SlideoutHeader, SlideoutContent } from '@primer/hooks/openSlideout'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Stack from '@primer/components/Stack'
import { CheckIcon, ExclamationCircleIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import { SlideoutResolveFn } from '@primer/types'
import FormSelect, { Option } from '@primer/components/Form/FormSelect'
import { entityResource, fieldResource, WorkflowStepConnectionModel, WorkflowStepModel } from 'db/types'
import { WorkflowIcon } from 'components/Icon'
import { useFormikContext } from 'formik'
import { watch } from '@primer/components/Form/utils'
import FormToggle from '@primer/components/Form/FormToggle'
import db from 'db'
import { uuid } from 'utils/funcs'

const isStep = (x: any): x is WorkflowStepModel => x.id && x.name
const isConnection = (x: any): x is WorkflowStepConnectionModel => x.id && x.targetId

const triggerOptions: Option[] = [
	{
		value: 'document',
		label: 'A document was uploaded',
	},
	{
		value: 'field-update',
		label: 'A field was updated',
	},
	{
		value: 'date-met',
		label: 'A date was met',
	},
	{
		value: 'user-action',
		label: 'A user took an action',
	},
]

interface CreateProps {
	mode: 'create'
	step: WorkflowStepModel
	elements?: (WorkflowStepModel | WorkflowStepConnectionModel)[]
}

interface EditProps {
	mode: 'edit'
	step: WorkflowStepModel
	elements: (WorkflowStepModel | WorkflowStepConnectionModel)[]
}

type OpenWorkflowStepProps = CreateProps | EditProps

const openWorkflowStep = (props: OpenWorkflowStepProps) => {
	return openSlideout<FormSchema>({
		render: (close) => <Component close={close} {...props} />,
	})
}

type ConnectionRecords = Record<string, WorkflowStepConnectionModel>

interface FormSchema {
	id: string
	workflowId: string
	name: string
	description: string
	isTransition: boolean
	isStart: boolean
	targetWorkflow: string
	posX: number
	posY: number
	connections: ConnectionRecords
}

const Component: FC<OpenWorkflowStepProps & { close: SlideoutResolveFn<FormSchema> }> = ({
	mode = 'create',
	step,
	elements = [],
	close,
}) => {
	const connections = elements.filter(isConnection)
	const outgoingConnections = connections.filter((x) => x.sourceId === step.id)
	const incomingConnections = connections.filter((x) => x.targetId === step.id)

	const formContext = useForm<FormSchema>({
		initialValues: {
			id: step.id || uuid(),
			workflowId: step.workflowId || '',
			name: step.name || '',
			description: step.description || '',
			isTransition: step.isTransition || false,
			isStart: step.isStart || false,
			targetWorkflow: step.targetWorkflow || '',
			posX: step.posX || 0,
			posY: step.posY || 0,
			connections: outgoingConnections.reduce((list, con) => ({ ...list, [con.id]: con }), {}),
		},
		onSubmit: (formData) => {
			close(formData)
		},
	})

	const isTransition = watch('isTransition', formContext)
	const startStep = elements.filter(isStep).find((x) => x.isStart)

	return (
		<Form context={formContext} className="contents">
			<SlideoutHeader title={mode === 'create' ? 'Create Workflow Step' : 'Edit Workflow Step'} />

			<SlideoutContent>
				<Stack className="py-6" dividers>
					<div className="px-6">
						<Stack>
							<div>
								<div className="text-sm font-medium text-gray-800">Details</div>
								<div className="text-sm text-gray-500">Basic info about the step</div>
							</div>

							<FormInput
								name="name"
								label="Name"
								className="flex-1"
								validations={{ required: 'Name is required' }}
								intent="overlap"
								autoFocus
							/>

							<FormInput
								name="description"
								label="Description"
								className="flex-1"
								// validations={{ required: 'Description is required' }}
								intent="overlap"
							/>
						</Stack>
					</div>

					{incomingConnections.length === 0 && (!startStep || startStep.id === step.id) && (
						<div className="px-6">
							<Stack>
								<div>
									<div className="text-sm font-medium text-gray-800">Starting Point</div>
									<div className="text-sm text-gray-500">
										Is this step the beginning of the workflow
									</div>
								</div>

								<FormToggle name="isStart" label="Is Start" />
							</Stack>
						</div>
					)}

					{mode === 'edit' && (
						<div className="px-6">
							<Stack>
								<div>
									<div className="text-sm font-medium text-gray-800">Triggers</div>
									<div className="text-sm text-gray-500">
										What conditions cause the case to move to a next step
									</div>
								</div>

								{outgoingConnections.length !== 0 ? (
									<div className="flex flex-col space-y-4">
										{outgoingConnections.map((con) => (
											<TriggerSection key={con.id} connection={con} elements={elements} />
										))}
									</div>
								) : (
									<Stack>
										<FormToggle name="isTransition" label="Is a transition to another workflow" />
										{!isTransition ? (
											<div className="p-4 flex items-center justify-start space-x-3 text-sm font-medium rounded-md bg-yellow-50 text-yellow-500">
												<ExclamationCircleIcon className="w-5 h-5" />
												<div>No steps are connected after this one</div>
											</div>
										) : (
											<FormSelect
												name="targetWorkflow"
												label="Workflow to transition to"
												options={db.workflows
													.all()
													.filter((x) => x.id !== step.workflowId)
													.map((x) => ({ label: x.name, value: x.id }))}
												intent="overlap"
												validations={{ required: 'Required' }}
												placeholder=""
											/>
										)}
									</Stack>
								)}
							</Stack>
						</div>
					)}
				</Stack>
			</SlideoutContent>

			<div className="p-6 border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-750 flex justify-end">
				<Actions
					actions={[
						{
							title: 'Cancel',
							onClick: () => close(),
						},
						{
							title: 'Save',
							icon: <CheckIcon className="w-5 h-5" />,
							intent: 'save',
							type: 'submit',
						},
					]}
				/>
			</div>
		</Form>
	)
}

interface TriggerSectionProps {
	connection: WorkflowStepConnectionModel
	elements: (WorkflowStepModel | WorkflowStepConnectionModel)[]
}

const TriggerSection: FC<TriggerSectionProps> = ({ connection, elements }) => {
	const formContext = useFormikContext<FormSchema>()

	const trigger = watch(`connections.${connection.id}.trigger`, formContext)
	const triggerField = watch(`connections.${connection.id}.field`, formContext)
	const field = fieldResource(triggerField)

	const fieldOptions =
		entityResource((x) => x.key === 'case')?.fields?.map((x) => ({ label: x.name, value: x.id })) || []

	return (
		<div className="flex flex-col space-y-4 px-2 pt-1 pb-2 border border-gray-200 rounded-md">
			<div className="flex space-x-2 items-center">
				<WorkflowIcon className="w-5 h-5" />
				<div className="font-medium">
					{elements.filter(isStep).find((x) => x.id === connection.targetId)?.name}
				</div>
			</div>
			<div className="bg-white rounded-md p-1 pt-4">
				<Stack>
					<FormSelect
						name={`connections.${connection.id}.trigger`}
						label="What happened"
						options={triggerOptions}
						intent="overlap"
						validations={{ required: 'Required' }}
						placeholder=""
					/>

					{trigger === 'field-update' ? (
						<>
							<FormSelect
								name={`connections.${connection.id}.field`}
								label="Which field"
								options={fieldOptions}
								intent="overlap"
								validations={{ required: 'Required' }}
								placeholder=""
							/>
							{field &&
								(field?.type === 'option' ? (
									<FormSelect
										name={`connections.${connection.id}.fieldValue`}
										label="To what value"
										options={field.options.map((x) => ({ label: x, value: x }))}
										intent="overlap"
										validations={{ required: 'Required' }}
										placeholder=""
									/>
								) : (
									<FormInput
										name={`connections.${connection.id}.fieldValue`}
										label="To what value"
										intent="overlap"
										validations={{ required: 'Required' }}
									/>
								))}
						</>
					) : null}
				</Stack>
			</div>
		</div>
	)
}

export default openWorkflowStep
