import { FC, useContext } from 'react'
import Stack from '@primer/components/Stack'
import { PageRendererContext } from 'components/PageBuilder/utils'
import { view, lensPath } from 'ramda'
import { FormSectionWidget } from '.'
import { ViewComp } from 'components/PageBuilder/Widgets'
import { Path } from 'components/PageBuilder/Widgets/types'
import FormSection from '@primer/components/Form/FormSection'

const View: FC<{ path: Path }> = ({ path }) => {
	let { itemList } = useContext(PageRendererContext)
	let item: FormSectionWidget = view(lensPath(path), itemList)

	console.log(item)

	return (
		<FormSection title={item.data.title} subtitle={item.data.subtitle}>
			<Stack>
				{item.data.items.map((_, i) => (
					<ViewComp key={i} path={[...path, 'data', 'items', i]} />
				))}
			</Stack>
		</FormSection>
	)
}

export default View
