import { isUnsaved, Unsaved } from 'types/db'
import {
	EntityModel,
	FieldModel,
	ActionModel,
	RelationModel,
	NotificationModel,
	TemplateModel,
	RoleModel,
	ActionPermissionModel,
	RelationPermissionModel,
	FieldPermissionModel,
	EntityPermissionModel,
	UnitModel,
	ValueModel,
	WorkflowModel,
	SiteSettings,
	WorkflowStepModel,
} from 'db/types'
import { v4 as uuid } from 'uuid'
import { PageModel } from './types'
import { WorkflowStepConnectionModel } from './types/WorkflowStepConnection'
import { FormModel } from './types/Form'
import dbjson from './db.json'

interface Storage {
	siteSettings: SiteSettings
	units: UnitModel[]
	values: ValueModel[]
	entities: EntityModel[]
	entityPermissions: EntityPermissionModel[]
	fields: FieldModel[]
	fieldPermissions: FieldPermissionModel[]
	actions: ActionModel[]
	actionPermissions: ActionPermissionModel[]
	relations: RelationModel[]
	relationPermissions: RelationPermissionModel[]
	notifications: NotificationModel[]
	templates: TemplateModel[]
	workflows: WorkflowModel[]
	workflowSteps: WorkflowStepModel[]
	workflowStepConnections: WorkflowStepConnectionModel[]
	roles: RoleModel[]
	pages: PageModel[]
	forms: FormModel[]
}

const initialSettings: SiteSettings = { title: 'Resolutionary', layout: 'sidebar' }

const storage: Storage = {
	siteSettings: initialSettings,
	units: [],
	values: [],
	entities: [],
	entityPermissions: [],
	fields: [],
	fieldPermissions: [],
	actions: [],
	actionPermissions: [],
	relations: [],
	relationPermissions: [],
	notifications: [],
	templates: [],
	workflows: [],
	workflowSteps: [],
	workflowStepConnections: [],
	roles: [],
	pages: [],
	forms: [],
}

export const renderContainer = {
	current: () => {},
}

const dataActions = <T extends Storage[K][0], K extends keyof Omit<Storage, 'siteSettings'>>(prop: K) => ({
	all: (filter?: (x: T) => boolean): T[] => {
		if (filter && storage[prop]) return Array.prototype.filter.call(storage[prop], filter)
		// @ts-ignore
		return storage[prop] || []
	},
	find: (id?: string | ((x: T) => boolean)): T | undefined => {
		return Array.prototype.find.call(storage[prop], typeof id === 'function' ? id : (x) => x.id === id)
	},
	save: (data: T | Unsaved<T>) => {
		if (isUnsaved<T>(data)) {
			let x = {
				...data,
				id: uuid(),
				created: new Date().toISOString(),
			}
			Array.prototype.push.call(storage[prop], x)
			persist()
			renderContainer.current()
			return x
		} else {
			let added = false

			// @ts-ignore
			storage[prop] = storage[prop].map((x) => {
				// @ts-ignore
				if (x.id === data.id) {
					added = true
					return data
				}
				return x
			})

			if (!added) Array.prototype.push.call(storage[prop], data)

			persist()
			renderContainer.current()
			return data
		}
	},
	remove: (id: string) => {
		// @ts-ignore
		storage[prop] = storage[prop].filter((x) => x.id !== id)
		persist()
		renderContainer.current()
	},
})

const data = {
	siteSettings: {
		set: <K extends keyof SiteSettings>(key: K, val: SiteSettings[K]) => {
			storage.siteSettings[key] = val
			renderContainer.current()
			persist()
		},
		get: <K extends keyof SiteSettings>(key: K) => {
			return storage.siteSettings[key]
		},
	},
	units: dataActions('units'),
	values: dataActions('values'),
	entities: dataActions('entities'),
	entityPermissions: dataActions('entityPermissions'),
	fields: dataActions('fields'),
	fieldPermissions: dataActions('fieldPermissions'),
	actions: dataActions('actions'),
	actionPermissions: dataActions('actionPermissions'),
	relations: dataActions('relations'),
	relationPermissions: dataActions('relationPermissions'),
	notifications: dataActions('notifications'),
	templates: dataActions('templates'),
	workflows: dataActions('workflows'),
	workflowSteps: dataActions('workflowSteps'),
	workflowStepConnections: dataActions('workflowStepConnections'),
	roles: dataActions('roles'),
	pages: dataActions('pages'),
	forms: dataActions('forms'),
}

export const persist = () => localStorage.setItem('data', JSON.stringify(storage))

export const restore = () => {
	const data = JSON.parse(localStorage.getItem('data') || 'null')

	if (data) {
		storage.siteSettings = data.siteSettings || {}
		storage.units = data.units || []
		storage.values = data.values || []
		storage.entities = data.entities || []
		storage.entityPermissions = data.entityPermissions || []
		storage.fields = data.fields || []
		storage.fieldPermissions = data.fieldPermissions || []
		storage.actions = data.actions || []
		storage.actionPermissions = data.actionPermissions || []
		storage.relations = data.relations || []
		storage.relationPermissions = data.relationPermissions || []
		storage.notifications = data.notifications || []
		storage.templates = data.templates || []
		storage.workflows = data.workflows || []
		storage.workflowSteps = data.workflowSteps || []
		storage.workflowStepConnections = data.workflowStepConnections || []
		storage.roles = data.roles || []
		storage.pages = data.pages || []
		storage.forms = data.forms || []
	}
}

export const clear = () => {
	storage.siteSettings = initialSettings
	storage.units = []
	storage.values = []
	storage.entities = []
	storage.entityPermissions = []
	storage.fields = []
	storage.fieldPermissions = []
	storage.actions = []
	storage.actionPermissions = []
	storage.relations = []
	storage.relationPermissions = []
	storage.notifications = []
	storage.templates = []
	storage.workflows = []
	storage.workflowSteps = []
	storage.workflowStepConnections = []
	storage.roles = []
	storage.pages = []
	storage.forms = []

	persist()
}

export const seed = () => {
	clear()
	persist()

	localStorage.setItem('data', JSON.stringify(dbjson))
	window.location.reload()
}

restore()

if (!localStorage.getItem('data')) {
	seed()
}

export default data
