import { Dispatch, FC, ReactNode, SetStateAction, useState } from 'react'
import clsx from 'clsx'

interface ExpandableProps {
	children: (setOpen: Dispatch<SetStateAction<boolean>>, isExpanded: boolean) => ReactNode
}

const Expandable: FC<ExpandableProps> = ({ children }) => {
	const [isExpanded, setExpanded] = useState(false)

	return (
		<div className={clsx(isExpanded ? 'fixed z-[9999] w-screen h-screen' : 'static', 'top-0 left-0')}>
			{children(setExpanded, isExpanded)}
		</div>
	)
}

export default Expandable
