import { FC, Fragment } from 'react'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, ArrowRightIcon, CheckIcon, PencilIcon } from '@heroicons/react/outline'
import Form, { useForm } from '@primer/components/Form/Form'
import { watch } from '@primer/components/Form/utils'
import FormInput from '@primer/components/Form/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import Card from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import db from 'db'
import FormSection from '@primer/components/Form/FormSection'
import { pageResource, PageModel, PAGE_TYPE, pageTypeOptions, EntityModel } from 'db/types'
import { uuid } from 'utils/funcs'
import FormSelect from '@primer/components/Form/FormSelect'
import openPageBuilder from 'slideouts/openPageBuilder'
import { slugify } from '@primer/utils/funcs'
import { isNotNone } from '@primer/types'

const getEntityIdSlug = (entity?: EntityModel) => {
	return `:${entity?.key ? entity.key + 'Id' : 'id'}`
}

const getBreadcrumbs = (page: PageModel | undefined) => {
	if (!page) return []

	const arr = []

	let parentId: string | undefined = page.id

	while (parentId) {
		let parent = db.pages.find(parentId)
		if (parent) {
			arr.push(parent.name)

			parentId = parent.parentId
		} else {
			parentId = ''
		}
	}

	return arr.reverse().filter(isNotNone)
}

const getPageUrl = (page: PageModel | undefined) => {
	if (!page) return ''

	const arr = []

	let parentId: string | undefined = page.id

	while (parentId) {
		let parent = db.pages.find(parentId)
		if (parent) {
			arr.push(parent.slug)

			if (parent.type === PAGE_TYPE.Unit) {
				let entity = db.entities.find(parent.entityId)
				arr.push(getEntityIdSlug(entity))
			}

			parentId = parent.parentId
		} else {
			parentId = ''
		}
	}

	return arr.reverse().filter(isNotNone).join('/')
}

type FormSchema = PageModel

const PageForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const page = pageResource(id || '')

	const formContext = useForm<FormSchema>({
		initialValues: {
			id: page?.id || uuid(),
			type: page?.type || PAGE_TYPE.None,
			entityId: page?.entityId || '',
			relationId: page?.relationId || '',
			parentId: page?.parentId || '',
			key: page?.key || '',
			slug: page?.slug || '',
			name: page?.name || '',
			title: page?.title || '',
			structure: page?.structure || '',
			path: page?.path || '',
		},
		onSubmit: (formValues) => {
			const parentId = formValues.parentId
			const parent = db.pages.find(parentId)
			const url = getPageUrl(parent)
			const entity = db.entities.find(formValues.entityId)

			db.pages.save({
				...formValues,
				key: slugify(formValues.name),
				path: `/${[url, formValues.slug, formValues.type === PAGE_TYPE.Unit && getEntityIdSlug(entity)]
					.filter(isNotNone)
					.join('/')}`,
			})

			navigate(`${process.env.PUBLIC_URL}/settings/pages`)
		},
	})

	const type = watch('type', formContext)
	const slug = watch('slug', formContext)
	const name = watch('name', formContext)

	const entityId = watch('entityId', formContext)
	const entity = db.entities.find(entityId)

	const parentId = watch('parentId', formContext)
	const parent = db.pages.find(parentId)
	const url = getPageUrl(parent)
	const pageUrl = `/${[url, slug, type === PAGE_TYPE.Unit && getEntityIdSlug(entity)].filter(isNotNone).join('/')}`
	const breadcrumbs = getBreadcrumbs(parent)

	const relationId = watch('relationId', formContext)
	const relation = db.relations.find(relationId)

	const title = mode === 'create' ? 'Create Page' : 'Edit Page'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/pages',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<Stack dividers>
						<FormSection title="Details" subtitle="Basic details about the page">
							<Card>
								<Stack>
									<div className="w-[300px]">
										<FormInput
											name="name"
											label="Name"
											intent="overlap"
											validations={{ required: 'Name is required' }}
										/>
									</div>
								</Stack>
							</Card>
						</FormSection>

						<FormSection title="Nav" subtitle="Navigation details">
							<Card>
								<Stack>
									<div className="px-4 rounded-md bg-primary-100 dark:bg-primary-500 dark:bg-opacity-20 border border-primary-300 dark:border-primary-500 dark:border-opacity-30 divide-y divide-primary-300 dark:divide-primary-500 dark:divide-opacity-30">
										{parentId && (
											<div className="flex justify-between items-center space-x-4 py-4">
												<div className="text-sm font-medium text-primary-600 dark:text-primary-500">
													Breadcrumbs
												</div>
												<div className="flex items-center space-x-2">
													{[...breadcrumbs, name].map((breadcrumb, i) => (
														<Fragment key={i}>
															{i !== 0 && <ArrowRightIcon className="w-5 h-5" />}
															<div className="text-sm font-medium">{breadcrumb}</div>
														</Fragment>
													))}
												</div>
											</div>
										)}
										<div className="flex justify-between items-center space-x-4 py-4">
											<div className="text-sm font-medium text-primary-600 dark:text-primary-500">
												URL
											</div>
											<div className="text-sm font-medium">{pageUrl}</div>
										</div>
									</div>

									<div className="w-[300px]">
										<FormSelect
											name="parentId"
											label="Parent Page"
											intent="overlap"
											options={db.pages
												.all()
												.filter((x) => x.id !== id)
												.map((x) => ({ value: x.id, label: x.name }))}
											clearable
										/>
									</div>

									<div className="w-[300px]">
										<FormInput
											name="slug"
											label="Slug"
											intent="overlap"
											validations={{
												required:
													(type === PAGE_TYPE.Entity && parentId) ||
													(parent?.type === PAGE_TYPE.Unit && type === PAGE_TYPE.Relation)
														? 'Slug is required'
														: undefined,
											}}
										/>
									</div>
								</Stack>
							</Card>
						</FormSection>

						<FormSection title="Data" subtitle="Define the data requirements for this page">
							<Card>
								<Stack>
									<div className="grid grid-cols-2 gap-4">
										<div>
											<FormSelect
												name="type"
												label="Data Source"
												intent="overlap"
												options={pageTypeOptions}
												validations={{ required: 'Data Source is required' }}
											/>
										</div>

										{(type === PAGE_TYPE.Entity || type === PAGE_TYPE.Unit) && (
											<div>
												<FormSelect
													name="entityId"
													label="Entity"
													intent="overlap"
													options={db.entities
														.all()
														.map((x) => ({ label: x.name, value: x.id }))}
													validations={{ required: 'Entity is required' }}
												/>
											</div>
										)}

										{parent?.type === PAGE_TYPE.Unit && type === PAGE_TYPE.Relation && (
											<FormSelect
												name="relationId"
												label="Relation"
												intent="overlap"
												options={
													pageResource(parent)?.entity?.relations?.map((x) => ({
														value: x.id,
														label: `${x.name} (${x.related?.name})`,
													})) || []
												}
												validations={{ required: 'Relation is required' }}
											/>
										)}
									</div>
								</Stack>
							</Card>
						</FormSection>

						<FormSection title="Design" subtitle="Page layout design">
							<Card>
								<Stack>
									<Actions
										actions={[
											{
												title: 'Edit page layout',
												icon: <PencilIcon className="w-5 h-5" />,
												onClick: async () => {
													let updatedPage = await openPageBuilder({
														page: formContext.values,
														mode: mode || 'create',
														entityId:
															type === PAGE_TYPE.None
																? undefined
																: type === PAGE_TYPE.Relation
																? relation?.relatedId
																: entityId,
													})

													if (updatedPage) {
														formContext.setFieldValue('title', updatedPage.title)
														formContext.setFieldValue('structure', updatedPage.structure)
													}
												},
											},
										]}
									/>
								</Stack>
							</Card>
						</FormSection>

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: mode === 'create' ? 'Create' : 'Save',
										intent: 'save',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

export default PageForm
