import { FC, useContext } from 'react'
import { PageRendererContext } from 'components/PageBuilder/utils'
import { view, lensPath } from 'ramda'
import { InputWidget } from '.'
import { Path } from '../types'
import { fieldResource, unitResource } from 'db/types'

const View: FC<{ path: Path }> = ({ path }) => {
	let { itemList, unitId } = useContext(PageRendererContext)
	let item: InputWidget = view(lensPath(path), itemList)

	let field = item.data.fieldId !== 'custom' ? fieldResource(item.data.fieldId) : null
	let unit = unitResource(unitId)

	if (field) {
		return (
			<div className="">
				<div className="text-sm leading-5 text-gray-500 dark:text-gray-300">
					{item.data.label || field?.name || '-'}
				</div>
				<div className="mt-1 text-sm leading-5 break-all text-gray-900 dark:text-white">
					{unit?.value(field.id)?.formatted || '-'}
				</div>
			</div>
		)
	}

	if (item.data.fieldId === 'custom') {
		return (
			<div className="">
				<div className="text-sm leading-5 text-gray-500 dark:text-gray-300">{item.data.label || '-'}</div>
				<div className="mt-1 text-sm leading-5 break-all text-gray-900 dark:text-white">
					{unit?.value(item.data.fieldId)}
				</div>
			</div>
		)
	}

	return null
}

export default View
