import { EntityResource } from 'db/types'
import { createContext } from 'react'
import { WidgetType } from '../Widgets/types'

export interface PageBuilder2ContextType {
	entity: EntityResource | undefined | null
	items: Widget[]
	addItem: (item: Widget) => void
	removeItem: (item: Widget) => void
	updateItem: (newValue: Widget) => void
}

export const PageBuilder2Context = createContext<PageBuilder2ContextType>({
	entity: undefined,
	items: [],
	addItem: () => {},
	removeItem: () => {},
	updateItem: () => {},
})

export const PageBuilder2Provider = PageBuilder2Context.Provider

export interface PageRenderer2ContextType {
	unitId: string
	items: Widget[]
}

export const PageRenderer2Context = createContext<PageRenderer2ContextType>({
	unitId: '',
	items: [],
})

export const PageRenderer2Provider = PageRenderer2Context.Provider

export interface Widget {
	id: string
	parentId?: string
	index: number
	type: WidgetType
	data: unknown
}
export const isWidget = (x: any): x is Widget => typeof x.id === 'string'

export interface Tool<T = Widget> {
	name: string
	type: string
	description: string
	create: (x: any) => T
}
export const isTool = (x: any): x is Tool => typeof x.create === 'function'

export const getChildItems = (items: Widget[], parentId: string | undefined) => {
	return items.filter((x) => x.parentId === parentId).sort((a, b) => a.index - b.index)
}
