import { FC } from 'react'
import Card from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import { RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import { valueResource } from 'db/types'

const ValueHistory: FC<RouteComponentProps<{ id: string }>> = ({ id = '' }) => {
	const value = valueResource(id)
	const field = value?.field
	const entity = field?.entity
	const unit = value?.unit

	const title = `History of ${field?.name}`

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entity?.key}/${unit?.id}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Card>
					<ul>
						{value?.history.map((x) => (
							<li key={x.id}>{x.value}</li>
						))}
					</ul>
				</Card>
			</PageContent>
		</>
	)
}

export default ValueHistory
