import { FC } from 'react'
import openSlideout, { SlideoutHeader, SlideoutContent } from '@primer/hooks/openSlideout'
import Form, { useForm } from '@primer/components/Form/Form'
import Stack from '@primer/components/Stack'
import { RelationModel } from 'db/types'
import { CheckIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import { SlideoutResolveFn } from '@primer/types'
import FormSelect from '@primer/components/Form/FormSelect'
import db from 'db'
import { slugify } from '@primer/utils/funcs'
import FormInput from '@primer/components/Form/FormInput'

export const relationTypeOptions = [
	{ value: 'one-to-one', label: 'Single' },
	{ value: 'one-to-many', label: 'Many' },
]

export const oppositeRelationTypeOptions = [
	{ value: 'one-to-one', label: 'Single' },
	{ value: 'one-to-many', label: 'Single' },
]

type FormRelationModel = RelationModel

interface OpenConnectionProps {
	mode?: 'create' | 'edit'
	relation: FormRelationModel
}

const openRelation = (props: OpenConnectionProps) => {
	return openSlideout<FormRelationModel>({
		render: (close) => <Component close={close} {...props} />,
	})
}

type FormSchema = FormRelationModel

const Component: FC<OpenConnectionProps & { close: SlideoutResolveFn<FormRelationModel> }> = ({
	mode = 'create',
	relation,
	close,
}) => {
	const formContext = useForm<FormSchema>({
		initialValues: relation as FormSchema,
		onSubmit: (formData) => {
			close({
				...formData,
				key: slugify(formData.name),
			})
		},
	})

	return (
		<Form context={formContext} className="contents">
			<SlideoutHeader title={mode === 'create' ? 'Create Connection' : 'Edit Connection'} />

			<SlideoutContent>
				<Stack className="py-6">
					<div className="px-6">
						<Stack>
							<FormInput name="name" label="Name" validations={{ required: 'Name is required' }} />

							<FormSelect
								name="relatedId"
								label="Entity"
								options={db.entities.all().map((x) => ({ value: x.id, label: x.name }))}
								validations={{ required: 'Entity is required' }}
							/>

							<FormSelect
								name="type"
								label="Cardinality"
								options={relationTypeOptions}
								validations={{ required: 'Cardinality is required' }}
							/>
						</Stack>
					</div>
				</Stack>
			</SlideoutContent>

			<div className="p-6 border-t border-gray-200 dark:border-gray-600 bg-gray-100 dark:bg-gray-750 flex justify-end">
				<Actions
					actions={[
						{
							title: 'Cancel',
							onClick: () => close(),
						},
						{
							title: 'Save',
							icon: <CheckIcon className="w-5 h-5" />,
							intent: 'save',
							type: 'submit',
						},
					]}
				/>
			</div>
		</Form>
	)
}

export default openRelation
