import { FC, useState } from 'react'
import { v4 as uuid } from 'uuid'
import Card, { CardWrapper } from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon, PencilIcon, PlusIcon, XIcon } from '@heroicons/react/outline'
import Actions from '@primer/components/Actions'
import Stack from '@primer/components/Stack'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import db from 'db'
import {
	ActionModel,
	ActionPermissionModel,
	RelationModel,
	RelationPermissionModel,
	FieldModel,
	FieldPermissionModel,
} from 'db/types'
import useFieldArray from '@primer/components/Form/useFieldArray'
import FormSection from '@primer/components/Form/FormSection'
import Table, { TableSchema } from '@primer/components/Table'
import openField, { typeOptions } from 'slideouts/openField'
import openAction from 'slideouts/openAction'
import { confirm } from '@primer/alerts'
import { slugify } from '@primer/utils/funcs'
import openRelation, { relationTypeOptions, oppositeRelationTypeOptions } from 'slideouts/openRelation'
import produce from 'immer'

type FormFieldModel = FieldModel & { permissions: FieldPermissionModel[] }
type FormActionModel = ActionModel & { permissions: ActionPermissionModel[] }
type FormRelationModel = RelationModel & { permissions: RelationPermissionModel[] }

interface FormSchema {
	name: string
	plural: string
	fields: FormFieldModel[]
	actions: FormActionModel[]
	relations: FormRelationModel[]
}

const EntityForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const original = db.entities.find(id)

	const originalFields = db.fields.all((x) => x.entityId === id)
	const originalActions = db.actions.all((x) => x.entityId === id)
	const originalRelations = db.relations.all((x) => x.entityId === id || x.relatedId === id)

	const formContext = useForm<FormSchema>({
		initialValues: {
			name: original?.name || '',
			plural: original?.plural || '',
			fields: originalFields
				? originalFields.map((x) => ({
						...x,
						permissions: db.fieldPermissions.all((y) => y.fieldId === x.id),
				  }))
				: [],
			actions: originalActions
				? originalActions.map((x) => ({
						...x,
						permissions: db.actionPermissions.all((y) => y.actionId === x.id),
				  }))
				: [],
			relations: originalRelations
				? originalRelations.map((x) => ({
						...x,
						permissions: db.relationPermissions.all((y) => y.relationId === x.id),
				  }))
				: [],
		},
		onSubmit: (formSchema) => {
			const newEntityType = db.entities.save({
				...original,
				key: slugify(formSchema.name),
				name: formSchema.name,
				plural: formSchema.plural,
			})

			formSchema.fields.forEach((entityField) => {
				db.fields.save({
					...entityField,
					entityId: newEntityType.id,
				})
			})

			formSchema.actions.forEach((entityAction) => {
				db.actions.save({
					...entityAction,
					entityId: newEntityType.id,
				})
			})

			formSchema.relations.forEach((relation) => {
				db.relations.save({
					...relation,
					entityId: newEntityType.id,
				})
			})

			toDelete.fields.forEach(db.fields.remove)
			toDelete.actions.forEach(db.actions.remove)
			toDelete.relations.forEach(db.relations.remove)

			navigate(`${process.env.PUBLIC_URL}/settings/entities`)
		},
	})

	const { values: fields, arrayHelpers: fieldsHelpers } = useFieldArray('fields', formContext)
	const { values: actions, arrayHelpers: actionsHelpers } = useFieldArray('actions', formContext)
	const { values: relations, arrayHelpers: relationsHelpers } = useFieldArray('relations', formContext)

	const [toDelete, setToDelete] = useState<{
		fields: string[]
		actions: string[]
		relations: string[]
	}>({
		fields: [],
		actions: [],
		relations: [],
	})

	const fieldSchema: TableSchema<FormFieldModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Type',
				value: (x) => typeOptions.find((y) => y.value === x.type)?.label || '',
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedField = await openField({ mode: 'edit', field: x })
					if (editedField) {
						fieldsHelpers.replace(i, editedField)
					}
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							fieldsHelpers.remove(i)
							setToDelete(
								produce((draft) => {
									draft.fields.push(x.id)
								})
							)
						},
					})
				},
			},
		],
	}

	const actionsSchema: TableSchema<FormActionModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
		],
		actions: (x, i) => [
			{
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedAction = await openAction({ mode: 'edit', action: x, fields })
					if (editedAction) {
						actionsHelpers.replace(i, editedAction)
					}
				},
			},
			{
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							actionsHelpers.remove(i)
							setToDelete(
								produce((draft) => {
									draft.actions.push(x.id)
								})
							)
						},
					})
				},
			},
		],
	}

	const relationSchema: TableSchema<FormRelationModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Related Entity',
				value: (x) =>
					x.entityId === id
						? db.entities.find((y) => y.id === x.relatedId)?.name
						: db.entities.find((y) => y.id === x.entityId)?.name,
			},
			{
				title: 'Cardinality',
				value: (x) =>
					x.entityId === id
						? relationTypeOptions.find((y) => y.value === x.type)?.label
						: oppositeRelationTypeOptions.find((y) => y.value === x.type)?.label,
			},
		],
		actions: (x, i) => [
			x.entityId === id && {
				icon: <PencilIcon className="w-5 h-5" />,
				intent: 'menu',
				onClick: async () => {
					const editedRelation = await openRelation({ mode: 'edit', relation: x })
					if (editedRelation) {
						relationsHelpers.replace(i, editedRelation)
					}
				},
			},
			x.entityId === id && {
				icon: <XIcon className="w-5 h-5" />,
				intent: 'menu-danger',
				onClick: () => {
					confirm({
						title: 'Are you sure?',
						message: 'This cannot be undone',
						onAccept: () => {
							relationsHelpers.remove(i)
							setToDelete(
								produce((draft) => {
									draft.relations.push(x.id)
								})
							)
						},
					})
				},
			},
		],
	}

	const title = mode === 'create' ? 'Create Entity' : 'Edit Entity'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/entities',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<Form context={formContext}>
				<PageContent>
					<Stack dividers>
						<FormSection title="Details" subtitle="The name and different types of labels for the entity">
							<Card>
								<Stack>
									<FormInput
										name="name"
										label="Name"
										validations={{ required: 'Name is required' }}
									/>

									<FormInput name="plural" label="Plural" />
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Fields"
							subtitle="Fields are custom fields that are attached to this entity, that can be configured to contain many different kinds of data"
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={fields} schema={fieldSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Field Type',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												let id = uuid()
												const newField = await openField({
													field: {
														id,
														entityId: '',
														key: '',
														name: '',
														plural: '',
														template: '',
														type: 'string',
														options: [''],
													},
												})

												if (newField) fieldsHelpers.push(newField)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<FormSection
							title="Actions"
							subtitle="Actions are custom interactions that the user can use do to this entity. They can modify a field, or be used as triggers for notifications."
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={actions} schema={actionsSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Action',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												let id = uuid()
												const newAction = await openAction({
													action: {
														id,
														entityId: '',
														key: '',
														name: '',
														changes: [],
													},
													fields,
												})

												if (newAction) actionsHelpers.push(newAction)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<FormSection
							title="Relations"
							subtitle={
								<div className="space-y-3">
									<p>
										Relations are a way to define a relationship between this entity type, and
										another entity type.
									</p>
									<p>
										For example, if a "case has many events", then a case should have a relation
										named "Events", set to the Event entity and a cardinality of "Many"
									</p>
								</div>
							}
						>
							<Stack>
								<CardWrapper className="rounded-md overflow-hidden">
									<Table items={relations} schema={relationSchema} className="-my-px" />
								</CardWrapper>

								<div className="flex justify-end">
									<Actions
										actions={{
											title: 'Add Relation',
											icon: <PlusIcon className="w-5 h-5" />,
											intent: 'invisible',
											onClick: async () => {
												let id = uuid()
												const newRelation = await openRelation({
													relation: {
														id,
														key: '',
														name: '',
														entityId: '',
														relatedId: '',
														type: 'one-to-many',
													},
												})

												if (newRelation) relationsHelpers.push(newRelation)
											},
										}}
									/>
								</div>
							</Stack>
						</FormSection>

						<div className="flex justify-end">
							<Actions
								actions={[
									{
										title: mode === 'create' ? 'Create' : 'Save',
										intent: 'save',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</PageContent>
			</Form>
		</>
	)
}

export default EntityForm
