import { FC, useContext } from 'react'
import clsx from 'clsx'
import Dropper from 'components/Builder/Dropper'
import { PageBuilder2Context } from '../utils'
import { Tool } from '../utils'

interface DropTargetProps {
	parentId?: string
	index: number
	accept: string | string[]
	isFirst?: boolean
	isLast?: boolean
}

const DropTarget: FC<DropTargetProps> = ({ parentId = undefined, index, accept, isFirst = false, isLast = false }) => {
	const { addItem } = useContext(PageBuilder2Context)

	return (
		<Dropper
			accept={accept}
			onDrop={(item: Tool) => {
				addItem(item.create({ index, parentId }))
				// if (isItemAndPath(item)) {
				// 	let { item: existingItem, path: oldPath } = item
				// 	addItem(path, existingItem)
				// 	removeItem(oldPath)
				// } else if (isTool(item)) {
				// 	addItem(path, item.create())
				// } else {
				// 	addItem(path, item)
				// }
			}}
			distance={20}
		>
			{({ canDrop, isOver, isClose }) =>
				(canDrop && isClose) || isLast ? (
					<div className={clsx('p-1')}>
						<div
							className={clsx(
								'px-6 h-10 flex items-center justify-center rounded-lg border-2 border-dashed',
								{
									'my-0 h-full': isFirst && isLast,
									'mb-6': isFirst && !isLast,
									'mt-6': isLast && !isFirst,
								},
								{
									'border-indigo-400 bg-indigo-50': isOver,
									'border-gray-900 border-opacity-10 bg-white bg-opacity-50': !isOver,
								}
							)}
						/>
					</div>
				) : (
					<div className={clsx(!isFirst && !isLast && 'h-4')} />
				)
			}
		</Dropper>
	)
}

export default DropTarget
