import { useContext } from 'react'
import { lensPath, view } from 'ramda'
import { PageBuilderContext, PageRendererContext, Path, Widget } from '../utils'
import Dragger from 'components/Builder/Dragger'

import {
	CardType,
	WellType,
	PageTitleType,
	FieldType,
	ColumnsType,
	DividerType,
	SectionType,
	TableType,
	FormSectionType,
	InputType,
} from './types'

import { CardTool, CardBuild, CardView, openEditCard } from './Card'
import { WellTool, WellBuild, WellView, openEditWell } from './Well'
import { PageTitleTool, PageTitleBuild, PageTitleView, openEditPageTitle } from './PageTitle'
import { ColumnsTool, ColumnsBuild, ColumnsView, openEditColumns } from './Columns'
import { FieldTool, FieldBuild, FieldView, openEditField } from './Field'
import { DividerTool, DividerBuild, DividerView, openEditDivider } from './Divider'
import { SectionTool, SectionBuild, SectionView, openEditSection } from './Section'
import { TableTool, TableBuild, TableView, openEditTable } from './Table'
import { FormSectionTool, FormSectionBuild, FormSectionView, openEditFormSection } from './FormSection'
import { InputTool, InputBuild, InputView, openEditInput } from './Input'

export const pbtools = {
	[PageTitleType]: PageTitleTool,
	[CardType]: CardTool,
	[SectionType]: SectionTool,
	[WellType]: WellTool,
	[ColumnsType]: ColumnsTool,
	[FieldType]: FieldTool,
	[DividerType]: DividerTool,
	[TableType]: TableTool,
}

export const formtools = {
	[PageTitleType]: PageTitleTool,
	[CardType]: CardTool,
	[SectionType]: SectionTool,
	[WellType]: WellTool,
	[ColumnsType]: ColumnsTool,
	[DividerType]: DividerTool,
	[TableType]: TableTool,
	[FormSectionType]: FormSectionTool,
	[InputType]: InputTool,
}

export const builds = {
	[CardType]: CardBuild,
	[WellType]: WellBuild,
	[PageTitleType]: PageTitleBuild,
	[ColumnsType]: ColumnsBuild,
	[FieldType]: FieldBuild,
	[DividerType]: DividerBuild,
	[SectionType]: SectionBuild,
	[TableType]: TableBuild,
	[FormSectionType]: FormSectionBuild,
	[InputType]: InputBuild,
}

export const views = {
	[CardType]: CardView,
	[WellType]: WellView,
	[PageTitleType]: PageTitleView,
	[ColumnsType]: ColumnsView,
	[FieldType]: FieldView,
	[DividerType]: DividerView,
	[SectionType]: SectionView,
	[TableType]: TableView,
	[FormSectionType]: FormSectionView,
	[InputType]: InputView,
}

export const editors = {
	[CardType]: openEditCard,
	[WellType]: openEditWell,
	[PageTitleType]: openEditPageTitle,
	[ColumnsType]: openEditColumns,
	[FieldType]: openEditField,
	[DividerType]: openEditDivider,
	[SectionType]: openEditSection,
	[TableType]: openEditTable,
	[FormSectionType]: openEditFormSection,
	[InputType]: openEditInput,
}

export const BuildComp = ({ path }: { path: Path }) => {
	const { itemList } = useContext(PageBuilderContext)
	let item: Widget = view(lensPath(path), itemList)

	const Comp = builds[item.type]

	return (
		<Dragger type={item.type} item={{ item, path }}>
			<Comp path={path} />
		</Dragger>
	)
}

export const ViewComp = ({ path }: { path: Path }) => {
	const { itemList } = useContext(PageRendererContext)
	let item: Widget = view(lensPath(path), itemList)

	const Comp = views[item.type]

	return <Comp path={path} />
}
