import { FC, useCallback, useMemo, useRef, useState } from 'react'
import { navigate, RouteComponentProps } from '@reach/router'
import ReactFlow, {
	ArrowHeadType,
	Background,
	BackgroundVariant,
	Connection,
	ConnectionLineType,
	ConnectionMode,
	Controls,
	Edge,
	Elements,
	isEdge,
	isNode,
	Node,
	OnLoadParams,
	ReactFlowProvider,
	useStoreState,
} from 'react-flow-renderer'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, CheckIcon, PencilIcon, PlusIcon, XIcon } from '@heroicons/react/outline'
import { uuid } from 'utils/funcs'
import clsx from 'clsx'
import openWorkflowStep from './openWorkflowStep'
import { workflowResource, WorkflowStepConnectionModel, WorkflowStepModel, workflowStepResource } from 'db/types'
import useDismissable from '@primer/hooks/useDismissable'
import db from 'db'
import FormLabel from '@primer/components/Form/FormLabel'
import dagre from 'dagre'
import { confirm } from '@primer/alerts'

const dagreGraph = new dagre.graphlib.Graph()
dagreGraph.setDefaultEdgeLabel(() => ({}))

const withFlowProvider = <T,>(Comp: FC<T>) => {
	return (props: T) => {
		return (
			<ReactFlowProvider>
				<Comp {...props} />
			</ReactFlowProvider>
		)
	}
}

const getLayoutedElements = (elements: Elements, nodes: Node<any>[], direction = 'TB') => {
	const isHorizontal = direction === 'LR'
	dagreGraph.setGraph({ rankdir: direction })

	elements.forEach((el) => {
		if (isNode(el)) {
			let node = nodes.find((x) => x.id === el.id)
			dagreGraph.setNode(el.id, { width: node?.__rf.width || 0, height: node?.__rf.height || 0 })
		} else {
			dagreGraph.setEdge(el.source, el.target)
		}
	})

	dagre.layout(dagreGraph)

	return elements.map((el) => {
		if (isNode(el)) {
			let node = nodes.find((x) => x.id === el.id)

			const nodeWithPosition = dagreGraph.node(el.id)
			// @ts-ignore
			el.targetPosition = isHorizontal ? 'left' : 'top'
			// @ts-ignore
			el.sourcePosition = isHorizontal ? 'right' : 'bottom'

			// unfortunately we need this little hack to pass a slightly different position
			// to notify react flow about the change. Moreover we are shifting the dagre node position
			// (anchor=center center) to the top left so it matches the react flow node anchor point (top left).
			el.position = {
				x: nodeWithPosition.x - (node?.__rf.width || 0) / 2 + Math.random() / 1000 + 20,
				y: nodeWithPosition.y - (node?.__rf.height || 0) / 2 + 20,
			}
		}

		return el
	})
}

const WorkflowForm: FC<RouteComponentProps<{ id: string }> & { mode: string }> = withFlowProvider(({ id, mode }) => {
	const workflowId = id || uuid()

	let workflow = workflowResource(workflowId)
	let [steps, setSteps] = useState<WorkflowStepModel[]>(workflow?.steps || [])
	let [connections, setConnections] = useState<WorkflowStepConnectionModel[]>(
		steps?.map((x) => workflowStepResource(x)?.connections || []).flat() || []
	)

	const nodes = useStoreState((state) => state.nodes)
	const edges = useStoreState((state) => state.edges)

	const generateElements = useCallback(
		() => [
			...steps.map((x) => ({
				id: x.id,
				type: x.isTransition ? 'output' : x.isStart ? 'input' : 'default',
				data: {
					label: (
						<StepDisplay
							step={x}
							onClick={async () => {
								let updatedStep = await openWorkflowStep({
									step: x,
									mode: 'edit',
									elements: [...steps, ...connections],
								})

								if (!updatedStep) return

								const { connections: cons, ...updatedStepValues } = updatedStep

								setSteps((s) => {
									return s.map((y) => {
										if (!updatedStep) return y

										return y.id !== x.id
											? y
											: {
													...y,
													...updatedStepValues,
											  }
									})
								})

								setConnections((c) => {
									return c.map((x) => {
										if (cons[x.id] === undefined) return x
										return cons[x.id]
									})
								})
							}}
							onDelete={() => {
								confirm({
									title: 'Are you sure you want to delete this step?',
									message: 'This cannot be undone',
									onAccept: () => {
										onElementsRemove([x])
									},
								})
							}}
						/>
					),
				},
				position: { x: x.posX || 0, y: x.posY || 0 },
			})),
			...connections.map((x) => ({
				id: x.id,
				source: x.sourceId,
				target: x.targetId,
				type: ConnectionLineType.SmoothStep,
				arrowHeadType: ArrowHeadType.Arrow,
			})),
		],
		[steps, connections]
	)

	const elements = useMemo(() => generateElements(), [generateElements])

	const [stepsToDelete, setStepsToDelete] = useState<string[]>([])
	const [connectionsToDelete, setConnectionsToDelete] = useState<string[]>([])

	const onElementAdd = (element: WorkflowStepModel) => {
		setSteps((s) => (!element ? s : [...s, element]))
	}

	const onElementsRemove = (elementsToRemove: { id: string }[]) => {
		elementsToRemove.forEach((el) => {
			setSteps((x) => x.filter((y) => y.id !== el.id))
			setConnections((x) => x.filter((y) => y.id !== el.id && y.targetId !== el.id && y.sourceId !== el.id))
			setStepsToDelete((x) => [...x, el.id])
			setConnectionsToDelete((x) => [...x, el.id])
		})
	}

	const onConnect = (params: Edge | Connection) => {
		setConnections((c) => {
			if (!params.source || !params.target) return c
			return [
				...c,
				{
					id: uuid(),
					sourceId: params.source,
					targetId: params.target,
					trigger: '',
					field: '',
					fieldValue: '',
				},
			]
		})

		// setElements((els) => addEdge({ ...params }, els))
	}

	const updateNodePosition = (node: Node) => {
		setSteps((s) => {
			return s.map((x) =>
				x.id !== node.id
					? x
					: {
							...x,
							posX: node.position.x,
							posY: node.position.y,
					  }
			)
		})
	}

	const doLayout = () => {
		let layouted = getLayoutedElements(elements, nodes, 'TB')

		layouted.forEach((x) => {
			if (isNode(x)) updateNodePosition(x)
		})

		if (instance) {
			instance.setTransform({ x: 0, y: 0, zoom: 1 })
		}
	}

	const [name, setName] = useState(workflow?.name || '')

	const [instance, setInstance] = useState<OnLoadParams<any> | null>(null)
	const containerRef = useRef<HTMLDivElement>(null)
	const menuRef = useRef<HTMLDivElement>(null)

	const [menuPos, setMenuPos] = useState<{ x: number; y: number }>({ x: 0, y: 0 })

	const { open, setOpen, ref: clickRef } = useDismissable()

	const save = () => {
		db.workflows.save({ id: workflowId, name })

		steps.forEach((step) => {
			let node = nodes.find((x) => x.id === step.id)

			if (!workflowId || !node || !isNode(node)) return

			db.workflowSteps.save({
				id: step.id,
				workflowId,
				name: step.name,
				description: step.description,
				targetWorkflow: step.targetWorkflow,
				isTransition: step.isTransition,
				isStart: step.isStart,
				posX: node?.position.x,
				posY: node?.position.y,
			})
		})

		connections.forEach((connection) => {
			let edge = edges.find((x) => x.id === connection.id)

			if (!edge || !isEdge(edge)) return

			db.workflowStepConnections.save({
				id: connection.id,
				sourceId: edge.source,
				targetId: edge.target,
				trigger: connection.trigger,
				field: connection.field,
				fieldValue: connection.fieldValue,
			})
		})

		stepsToDelete.forEach((id) => {
			db.workflowSteps.remove(id)
		})

		connectionsToDelete.forEach((id) => {
			db.workflowStepConnections.remove(id)
		})

		navigate(`${process.env.PUBLIC_URL}/settings/workflows`)
	}

	return (
		<div className="h-screen flex flex-col bg-gray-200 dark:bg-gray-800">
			<div className="flex justify-between bg-white shadow h-16">
				<div className="flex items-center p-4 border-r border-gray-300">
					<Actions
						actions={{
							icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
							intent: 'borderless',
							rounded: 'full',
							onClick: () => window.history.back(),
						}}
					/>
				</div>
				<div className="flex-1 flex items-center px-6 border-r border-gray-300 space-x-6">
					<div className="text-2xl font-medium">{mode === 'create' ? 'Create' : 'Edit'} Workflow</div>
					<div className="flex items-center space-x-3">
						<FormLabel>Name:</FormLabel>
						<input
							type="text"
							value={name}
							onChange={(e) => setName(e.target.value)}
							className="form-input"
						/>
						<button
							onClick={doLayout}
							className="text-sm font-medium px-3 py-2 rounded-md border border-gray-200 shadow-sm ring-0 focus:ring-1 focus:border-primary-500 focus:ring-primary-500 transition-all duration-100"
						>
							Reorganise
						</button>
					</div>
				</div>
				<div className="flex items-center px-6">
					<Actions
						actions={[
							{
								title: 'Save',
								intent: 'save',
								icon: <CheckIcon className="w-5 h-5" />,
								onClick: () => save(),
							},
						]}
					/>
				</div>
			</div>

			<div className="flex-1 flex relative" ref={containerRef}>
				<div
					className={clsx('absolute z-10 shadow-lg rounded-md', !open && 'hidden')}
					style={{ left: menuPos.x + 'px', top: menuPos.y + 'px' }}
					ref={menuRef}
				>
					<div className="p-2 bg-white rounded-md ring-1 ring-black ring-opacity-5" ref={clickRef}>
						<button
							className="text-sm font-medium hover:bg-primary-50 active:bg-primary-100 flex items-center px-4 py-2 text-left leading-5 rounded-md"
							onClick={async () => {
								setOpen(false)
								let newStep = await openWorkflowStep({
									mode: 'create',
									step: {
										id: uuid(),
										workflowId,
										posX: menuPos.x,
										posY: menuPos.y,
										name: '',
										description: '',
									},
									elements: [...connections, ...steps],
								})

								if (!newStep) return

								const { connections: cons, ...newStepValues } = newStep

								onElementAdd(newStepValues as WorkflowStepModel)
							}}
						>
							<PlusIcon className="w-5 h-5 mr-2 -ml-1" />
							Add Step
						</button>
					</div>
				</div>

				<ReactFlow
					elements={elements}
					onElementsRemove={onElementsRemove}
					onNodeDragStop={(_, node) => updateNodePosition(node)}
					onSelectionDragStop={(_, nodes) => {
						nodes.filter(isNode).forEach((node) => {
							updateNodePosition(node)
						})
					}}
					onLoad={(instance) => setInstance(instance)}
					onConnect={onConnect}
					deleteKeyCode={8} /* backspace-key */
					onClick={() => setOpen(false)}
					onPaneContextMenu={(e) => {
						e.preventDefault()

						let rect = containerRef.current?.getBoundingClientRect()

						let x = e.pageX - (rect?.left || 0)
						let y = e.pageY - (rect?.top || 0)

						setOpen(true)
						setMenuPos({ x, y })
					}}
					connectionMode={ConnectionMode.Loose}
				>
					<Controls />
					<Background variant={BackgroundVariant.Dots} gap={12} size={1} color={'#ccc'} />
				</ReactFlow>
			</div>
		</div>
	)
})

const StepDisplay: FC<{ step: WorkflowStepModel; onClick: () => void; onDelete: () => void }> = ({
	step,
	onClick,
	onDelete,
}) => {
	return (
		<div className="relative">
			<button
				className="absolute top-0 right-0 -mt-4 -mr-4 w-5 h-5 rounded-full bg-red-100 hover:bg-red-200 transition-colors duration-100 ring-2 ring-white flex items-center justify-center"
				onClick={onDelete}
			>
				<XIcon className="w-4 h-4 text-red-500" />
			</button>

			<div className="px-2 flex items-center space-x-4 relative">
				<button
					className="w-8 h-8 rounded-full flex items-center justify-center bg-gray-200 hover:bg-gray-300 text-gray-600 transition-all duration-100"
					onClick={onClick}
				>
					<PencilIcon className="w-5 h-5" />
				</button>

				<div className="flex flex-col items-start">
					<div className="text-base text-gray-900 font-medium">{step.name}</div>
					<div className="text-sm text-gray-500 text-left">{step?.description}</div>
				</div>
			</div>
		</div>
	)
}

export default WorkflowForm
