import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { DividerType } from '../types'
import Build from './Build'
import View from './View'
import openEdit from './Edit'

export interface DividerWidget extends Widget {
	type: typeof DividerType
	data: {
		items: Widget[]
	}
}

export const isDividerWidget = (x: any): x is DividerWidget => x.type === DividerType

export const DividerTool: Tool<DividerWidget> = {
	name: 'Divider',
	type: DividerType,
	description: 'A box used for sectioning content',
	create: (data: Partial<DividerWidget>) => ({
		id: uuid(),
		type: DividerType,
		index: 0,
		...data,
		data: {
			items: [],
		},
	}),
}

export const DividerBuild = Build
export const DividerView = View
export const openEditDivider = openEdit
