import { FC } from 'react'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import Card, { CardWrapper } from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import db from 'db'
import FormSection from '@primer/components/Form/FormSection'
import { EntityModel, SiteSettings } from 'db/types'
import LayoutSidebar from 'assets/img/layout-sidebar.svg'
import LayoutTopbar from 'assets/img/layout-topbar.svg'
import { watch } from '@primer/components/Form/utils'
import clsx from 'clsx'
import Table, { TableCol, TableSchema } from '@primer/components/Table'
import { getIn } from 'formik'
import Checkbox from '@primer/components/Checkbox'
import FormEditor from '@primer/components/Form/FormEditor'

type FormSchema = SiteSettings & {
	nav: {
		[x: string]: {
			link: string
			permissions: string[]
		}
	}
	signature: string
}

const SiteSettingsForm: FC<RouteComponentProps> = () => {
	const formContext = useForm<FormSchema>({
		initialValues: {
			title: db.siteSettings.get('title') || '',
			layout: db.siteSettings.get('layout') || 'sidebar',
			nav: db.entities.all().reduce(
				(list, entity) => ({
					...list,
					[entity.key]: {
						link: entity.plural || entity.name,
						permissions: db.roles.all().map((x) => x.id),
					},
				}),
				{}
			),
			signature: '',
		},
		onSubmit: (formValues) => {
			db.siteSettings.set('title', formValues.title)
			db.siteSettings.set('layout', formValues.layout)

			navigate(`${process.env.PUBLIC_URL}/settings/site-settings`)
		},
	})

	const layout = watch('layout', formContext)

	const navSchema: TableSchema<EntityModel> = {
		cols: [
			{
				title: 'Entity Type',
				value: (x) => x.name,
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Link Name',
				value: (x) => <FormInput name={`nav.${x.key}.link`} />,
			},
			...db.roles.all().map<TableCol<EntityModel>>((role) => ({
				title: role.name,
				value: (x) => {
					let perms = getIn(formContext.values, `nav.${x.key}.permissions`) as string[]

					return (
						<div className="w-full flex justify-center">
							<Checkbox
								checked={perms?.includes(role.id)}
								onChange={(e) =>
									formContext.setFieldValue(
										`nav.${x.key}.permissions`,
										e.target.checked ? [...perms, role.id] : perms.filter((y) => y !== role.id)
									)
								}
							/>
						</div>
					)
				},
				width: 'minmax(auto, max-content)',
			})),
		],
	}

	const title = 'Edit Site Settings'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/site-settings',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<Stack>
						<FormSection
							title="Page Title"
							subtitle="This title appears in the browser tab, is important for SEO"
						>
							<Card>
								<FormInput name="title" label="Title" validations={{ required: 'Title is required' }} />
							</Card>
						</FormSection>

						<FormSection title="Layout" subtitle="Control the sites layout design">
							<Card>
								<div className="flex space-x-6">
									<button
										className={clsx(
											'w-[200px] h-[200px] relative rounded-md hover:opacity-80 cursor-pointer',
											layout === 'sidebar' && 'ring-2 ring-green-500'
										)}
										onClick={() => {
											formContext.setFieldValue('layout', 'sidebar')
										}}
										type="button"
									>
										<img src={LayoutSidebar} alt="Sidebar" />
									</button>
									<button
										className={clsx(
											'w-[200px] h-[200px] relative rounded-md hover:opacity-80 cursor-pointer',
											layout === 'topbar' && 'ring-2 ring-green-500'
										)}
										onClick={() => {
											formContext.setFieldValue('layout', 'topbar')
										}}
										type="button"
									>
										<img src={LayoutTopbar} alt="Topbar" />
									</button>
								</div>
							</Card>
						</FormSection>

						<FormSection title="Navigation" subtitle="Set up what pages are visible in the nav bar">
							<CardWrapper className="overflow-hidden">
								<Table schema={navSchema} items={db.entities.all()} />
							</CardWrapper>
						</FormSection>

						<FormSection
							title="Email Signature"
							subtitle="This signature will be included at the bottom of every notification."
						>
							<Card>
								<FormEditor name="signature" />
							</Card>
						</FormSection>

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: 'Update',
										intent: 'primary',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

export default SiteSettingsForm
