import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { TableType } from '../types'
import openEdit from './Edit'
import Build from './Build'
import View from './View'

export type TableWidgetWrapper = 'none' | 'card' | 'outline'

export const tableWidgetWrapperOptions = [
	{ label: 'None', value: 'none' },
	{ label: 'Card', value: 'card' },
	{ label: 'Outline', value: 'outline' },
]

export interface TableWidget extends Widget {
	type: typeof TableType
	data: {
		wrapper: TableWidgetWrapper
	}
}

export const isTableWidget = (x: any): x is TableWidget => x.type === TableType

export const TableTool: Tool<TableWidget> = {
	name: 'Table',
	type: TableType,
	description: 'Display a value',
	create: () => ({
		id: uuid(),
		type: TableType,
		data: {
			wrapper: 'none',
		},
	}),
}

export const TableBuild = Build
export const TableView = View
export const openEditTable = openEdit
