type FormActionModel = ActionModel

interface OpenActionProps {
	mode?: 'create' | 'edit'
	action: FormActionModel
	fields: FieldModel[]
}

const openAction = (props: OpenActionProps) => {
	return openSlideout<FormActionModel>({
		size: '2xl',
		render: (close) => <Component close={close} {...props} />,
	})
}

