import { CardWrapper } from '@primer/components/Card'
import CardHeader from '@primer/components/CardHeader'
import Stack from '@primer/components/Stack'
import { PageRenderer2Context } from 'components/PageBuilder2/utils'
import { FC, useContext } from 'react'
import { CardWidget } from '.'
import { ViewComp } from '..'

const View: FC<{ id: string }> = ({ id }) => {
	let { items } = useContext(PageRenderer2Context)
	let item = items.find((x) => x.id === id) as CardWidget
	let childItems = items.filter((x) => x.parentId === id)

	return !item ? null : (
		<CardWrapper>
			{item.data.title && <CardHeader title={item.data.title} />}
			<div className="py-6">
				<Stack>
					{childItems.map((x, i) => (
						<ViewComp key={i} id={x.id} />
					))}
				</Stack>
			</div>
		</CardWrapper>
	)
}

export default View
