import { FC } from 'react'
import { RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, ClockIcon, PencilIcon, PlusIcon, TemplateIcon } from '@heroicons/react/outline'
import { unitResource } from 'db/types'
// import PageRenderer from 'pages/PageBuilder/PageRenderer'
import { replaceMarkers } from 'utils/funcs'
import PageRenderer from 'components/PageBuilder/PageRenderer'

const ViewUnit: FC<RouteComponentProps<{ id: string }>> = ({ id = '' }) => {
	const unit = unitResource(id)
	const entity = unit?.entity

	const page = entity?.page('view')
	console.log('entities', entity && entity.pages)

	// const markers = entity?.fields?.reduce<Record<string, string>>(
	// 	(list, field) => ({ ...list, [field.key]: unit?.value(field)?.value || '-' }),
	// 	{}
	// )
	const markers = unit?.resolvedValues

	const title = replaceMarkers(page?.name || `View ${entity?.name}`, markers || {}, (str) => `{{${str}}}`)

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entity?.key}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
				actions={[
					page && {
						title: 'Edit Page Page',
						icon: <TemplateIcon className="w-5 h-5" />,
						to: `/${entity?.key}/${id}/page/view`,
					},
					{
						title: 'History',
						icon: <ClockIcon className="w-5 h-5" />,
						to: `/${entity?.key}/${id}/history`,
					},
					{
						title: `Edit ${entity?.name}`,
						icon: <PencilIcon className="w-5 h-5" />,
						intent: 'primary',
						to: `/${entity?.key}/${id}/edit`,
					},
				]}
			/>

			<PageContent>
				{page ? (
					<PageRenderer id={page.id} unitId={id} />
				) : (
					<div className="flex flex-col items-center justify-center space-y-6">
						<div className="text-2xl font-medium">No page exists for this page</div>
						<Actions
							actions={[
								{
									title: 'Create Page',
									icon: <PlusIcon className="w-5 h-5" />,
									intent: 'primary',
									size: 'lg',
									to: `/${entity?.key}/${id}/page/view`,
								},
							]}
						/>
					</div>
				)}
			</PageContent>
		</>
	)
}

export default ViewUnit
