import { useEffect, useReducer } from 'react'
import Router from './Router'
import 'react-quill/dist/quill.snow.css'
import './main.css'
import { renderContainer } from 'db'

const App = () => {
	const [, dispatch] = useReducer(() => ({}), {})
	useEffect(() => {
		renderContainer.current = dispatch
	}, [dispatch])

	return <Router />
}

export default App
