import { FC } from 'react'
import PageHeading from '@primer/components/PageHeading'
import PageNavbar from '@primer/components/PageNavbar'
import { RouteComponentProps } from '@reach/router'
import db from 'db'
import { entityResource, PageModel, pageTypeOptions, PAGE_TYPE, relationResource } from 'db/types'
import Table, { TableSchema } from '@primer/components/Table'
import { DotsHorizontalIcon, PencilIcon, PlusIcon, TrashIcon } from '@heroicons/react/outline'
import { links } from '../Settings'
import SectionHeader from '@primer/components/SectionHeader'
import Stack from '@primer/components/Stack'
import { confirm } from '@primer/alerts'

const Pages: FC<RouteComponentProps> = () => {
	const schema: TableSchema<PageModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Type',
				value: (x) => pageTypeOptions.find((y) => y.value === x.type)?.label || '-',
			},
			{
				title: 'Data',
				value: (x) => {
					if (x.type === PAGE_TYPE.Relation) {
						let relation = relationResource(x?.relationId || '')
						return `${relation?.name} (${relation?.related?.name})`
					} else if (x.type === PAGE_TYPE.None) {
						return '-'
					} else {
						return entityResource(x.entityId || '')?.name
					}
				},
			},
			{
				title: 'URL',
				value: (x) => x.path,
			},
		],
		actions: (x) => [
			{
				icon: <DotsHorizontalIcon className="w-5 h-5" />,
				intent: 'menu',
				actions: [
					[
						{
							title: 'Edit',
							icon: <PencilIcon className="w-5 h-5" />,
							to: `/settings/pages/${x.id}/edit`,
						},
					],
					[
						{
							title: 'Delete',
							intent: 'danger',
							icon: <TrashIcon className="w-5 h-5" />,
							onClick: () => {
								confirm({
									title: 'Are you sure?',
									message: 'This cannot be undone',
									onAccept: () => {
										db.pages.remove(x.id)
									},
								})
							},
						},
					],
				],
			},
		],
	}

	return (
		<>
			<PageHeading title="Settings" />

			<PageNavbar links={links}>
				<Stack>
					<div className="px-6 pt-6">
						<SectionHeader
							title="Pages"
							actions={[
								{
									title: 'Create Page',
									icon: <PlusIcon className="w-5 h-5" />,
									to: '/settings/pages/create',
								},
							]}
						/>
					</div>
					<div className="overflow-hidden rounded-br-md">
						<Table schema={schema} items={db.pages.all()} className="-mb-px" />
					</div>
				</Stack>
			</PageNavbar>
		</>
	)
}

export default Pages
