import { FC, useContext } from 'react'
import { PageBuilderContext, Path } from 'components/PageBuilder/utils'
import { lensPath, view } from 'ramda'
import { TableWidget } from '.'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'
import Table, { TableSchema } from '@primer/components/Table'
import { CardWrapper } from '@primer/components/Card'
import TableFrame from '@primer/components/TableFrame'

interface BuildProps {
	path: Path
}

const Build: FC<BuildProps> = ({ path }) => {
	const context = useContext(PageBuilderContext)
	const { itemList } = context
	const item: TableWidget = view(lensPath(path), itemList)

	const [isHovered, bindHoverable] = useHoverable()

	if (!item) return null

	const schema: TableSchema<any> = {
		cols: [
			{
				title: 'Title',
				value: (x) => x,
			},
		],
	}

	const WrapperComp =
		item.data.wrapper === 'card' ? CardWrapper : item.data.wrapper === 'outline' ? TableFrame : 'div'

	return (
		<div
			onClick={(e) => {
				e.stopPropagation()
				openEdit({ item, path, context })
			}}
			{...bindHoverable}
			className={clsx(
				'p-1 rounded-md cursor-pointer transition-all duration-100',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			<WrapperComp>
				<Table
					schema={schema}
					items={[]}
					className={clsx(
						item.data.wrapper === 'card'
							? 'overflow-hidden rounded-md'
							: item.data.wrapper === 'outline'
							? '-my-px'
							: ''
					)}
				/>
			</WrapperComp>
		</div>
	)
}

export default Build
