import { ReactNode, FC } from 'react'
import clsx, { ClassValue } from 'clsx'

export const tabIntents = {
	default: {
		container: 'border-b border-gray-200',
		tab: (isActive: boolean) => [
			isActive
				? 'border-indigo-500 text-indigo-600'
				: 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300',
			'whitespace-nowrap py-4 px-3 border-b-2 font-medium text-sm',
		],
	},
	card: {
		container: 'ml-2',
		tab: (isActive: boolean) => [
			'whitespace-nowrap px-4 py-3 mr-2 font-medium text-sm rounded-t-md shadow',
			isActive ? 'bg-white' : 'bg-white bg-opacity-50',
		],
	},
}

interface TabsProps {
	tabs: { label: ReactNode; value: string }[]
	value: string
	onChange: (x: string) => void
	className?: ClassValue
	intent?: keyof typeof tabIntents
}

const Tabs: FC<TabsProps> = ({ tabs, value, onChange, className, intent = 'default' }) => {
	return (
		<div className={clsx('pt-2 px-2 -mt-2 -mx-2 overflow-hidden', className)}>
			<div className="sm:hidden">
				<label htmlFor="tabs" className="sr-only">
					Select a tab
				</label>
				<select
					id="tabs"
					name="tabs"
					className="block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
					value={value}
					onChange={(e) => onChange(e.target.value)}
				>
					{tabs.map((tab) => (
						<option key={tab.value}>{tab.label}</option>
					))}
				</select>
			</div>
			<div className="hidden sm:block">
				<div className={tabIntents[intent].container}>
					<nav className="-mb-px flex" aria-label="Tabs">
						{tabs.map((tab) => (
							<button
								key={tab.value}
								className={clsx(tabIntents[intent].tab(tab.value === value))}
								aria-current={tab.value === value ? 'page' : undefined}
								onClick={() => onChange(tab.value)}
								type="button"
							>
								{tab.label}
							</button>
						))}
					</nav>
				</div>
			</div>
		</div>
	)
}

export default Tabs
