import { FC, useContext } from 'react'
import { PageBuilderContext, Path } from 'components/PageBuilder/utils'
import { lensPath, view } from 'ramda'
import { InputWidget } from '.'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'
import { fieldResource } from 'db/types'
import Input from '@primer/components/Input'

const placeholders = {
	string: 'Text',
	number: 'Number',
	date: 'dd/mm/yyyy',
	boolean: 'true/false',
	option: 'Options',
}

interface BuildProps {
	path: Path
}

const Build: FC<BuildProps> = ({ path }) => {
	const context = useContext(PageBuilderContext)
	const { itemList } = context
	const item: InputWidget = view(lensPath(path), itemList)

	const field = fieldResource(item.data.fieldId)

	const [isHovered, bindHoverable] = useHoverable()

	if (!item) return null

	return (
		<div
			onClick={(e) => {
				e.stopPropagation()
				openEdit({ item, path, context })
			}}
			{...bindHoverable}
			className={clsx(
				'p-1 rounded-md cursor-pointer transition-all duration-100',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			<div className="pointer-events-none">
				<Input
					label={item.data.label || field?.name || 'Please choose a field'}
					type="text"
					placeholder={`${field?.type ? placeholders[field.type] || field.type : ''}...`}
					intent={item.data.intent}
					isRequired={item.data.required ? 'required' : undefined}
				/>
			</div>
		</div>
	)
}

export default Build
