import { FC } from 'react'
import PageHeading from '@primer/components/PageHeading'
import PageNavbar from '@primer/components/PageNavbar'
import { RouteComponentProps } from '@reach/router'
import db from 'db'
import { FormModel } from 'db/types'
import Table, { TableSchema } from '@primer/components/Table'
import { DotsHorizontalIcon, PencilIcon, PlusIcon, TrashIcon } from '@heroicons/react/outline'
import { links } from '../Settings'
import SectionHeader from '@primer/components/SectionHeader'
import Stack from '@primer/components/Stack'
import { confirm } from '@primer/alerts'

const Forms: FC<RouteComponentProps> = () => {
	const schema: TableSchema<FormModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
		],
		actions: (x) => [
			{
				icon: <DotsHorizontalIcon className="w-5 h-5" />,
				intent: 'menu',
				actions: [
					[
						{
							title: 'Edit',
							icon: <PencilIcon className="w-5 h-5" />,
							to: `/settings/Forms/${x.id}/edit`,
						},
					],
					[
						{
							title: 'Delete',
							intent: 'danger',
							icon: <TrashIcon className="w-5 h-5" />,
							onClick: () => {
								confirm({
									title: 'Are you sure?',
									message: 'This cannot be undone',
									onAccept: () => {
										db.forms.remove(x.id)
									},
								})
							},
						},
					],
				],
			},
		],
	}

	return (
		<>
			<PageHeading title="Settings" />

			<PageNavbar links={links}>
				<Stack>
					<div className="px-6 pt-6">
						<SectionHeader
							title="Forms"
							actions={[
								{
									title: 'Create Form',
									icon: <PlusIcon className="w-5 h-5" />,
									to: '/settings/forms/create',
								},
							]}
						/>
					</div>
					<div className="overflow-hidden rounded-br-md">
						<Table schema={schema} items={db.forms.all()} className="-mb-px" />
					</div>
				</Stack>
			</PageNavbar>
		</>
	)
}

export default Forms
