import { FC, useContext } from 'react'
import { PageBuilder2Context } from 'components/PageBuilder2/utils'
import { DividerWidget } from '.'
import openEdit from './Edit'
import clsx from 'clsx'
import useHoverable from 'hooks/useHoverable'
import Divider from '@primer/components/Divider'

interface BuildProps {
	id: string
}

const Build: FC<BuildProps> = ({ id }) => {
	const { items, removeItem } = useContext(PageBuilder2Context)
	let item = items.find((x) => x.id === id) as DividerWidget

	const [isHovered, bindHoverable] = useHoverable()

	return !item ? null : (
		<div
			onClick={(e) => {
				e.stopPropagation()
				if (item) openEdit({ item, removeItem })
			}}
			{...bindHoverable}
			className={clsx(
				'p-2 rounded-md cursor-pointer transition-all duration-10',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			<Divider />
		</div>
	)
}

export default Build
