import { FC } from 'react'
import { navigate, RouteComponentProps } from '@reach/router'
import { ArrowLeftIcon, CheckIcon } from '@heroicons/react/outline'
import Form, { useForm } from '@primer/components/Form/Form'
import FormInput from '@primer/components/Form/FormInput'
import Actions from '@primer/components/Actions'
import PageContent from '@primer/components/PageContent'
import PageHeading from '@primer/components/PageHeading'
import Card, { CardWrapper } from '@primer/components/Card'
import Stack from '@primer/components/Stack'
import FormCheckbox from '@primer/components/Form/FormCheckbox'
import CardHeader from '@primer/components/CardHeader'
import db from 'db'
import FormSection from '@primer/components/Form/FormSection'
import Table, { TableSchema } from '@primer/components/Table'
import TableFrame from '@primer/components/TableFrame'
import { FieldPermissionModel, ActionPermissionModel, RelationPermissionModel } from 'db/types'

interface PermissionSet<T> {
	[key: string]: T
}

interface Permissions {
	[key: string]: {
		fields: PermissionSet<FieldPermissionModel>
		actions: PermissionSet<ActionPermissionModel>
		relations: PermissionSet<RelationPermissionModel>
	}
}

interface FormSchema {
	name: string
	entities: Permissions
}

const RoleForm: FC<RouteComponentProps<{ id: string; mode: 'create' | 'edit' }>> = ({ id, mode }) => {
	const originalRole = db.roles.find(id)

	const formContext = useForm<FormSchema>({
		initialValues: {
			name: originalRole?.name || '',
			entities: db.entities.all().reduce<Permissions>((list, entity) => {
				return {
					...list,
					[entity.key]: {
						fields: db.fields
							.all((x) => x.entityId === entity.id)
							.reduce<PermissionSet<FieldPermissionModel>>(
								(items, x) => ({
									...items,
									[x.key]: db.fieldPermissions.find((y) => y.fieldId === x.id && y.roleId === id) || {
										id: '',
										view: true,
										modify: true,
										roleId: id || '',
										fieldId: x.id,
									},
								}),
								{}
							),
						actions: db.actions
							.all((x) => x.entityId === entity.id)
							.reduce<PermissionSet<ActionPermissionModel>>(
								(items, x) => ({
									...items,
									[x.key]: db.actionPermissions.find(
										(y) => y.actionId === x.id && y.roleId === id
									) || {
										id: '',
										perform: true,
										roleId: id || '',
										actionId: x.id,
									},
								}),
								{}
							),
						relations: db.relations
							.all((x) => x.entityId === entity.id)
							.reduce<PermissionSet<RelationPermissionModel>>(
								(items, x) => ({
									...items,
									[x.key]: db.relationPermissions.find(
										(y) => y.relationId === x.id && y.roleId === id
									) || {
										id: '',
										view: true,
										modify: true,
										remove: true,
										roleId: id || '',
										relationId: x.id,
									},
								}),
								{}
							),
					},
				}
			}, {}),
		},
		onSubmit: (formValues) => {
			db.roles.save({
				...(originalRole || {}),
				name: formValues.name,
			})

			db.entities.all().forEach((entityType) => {
				Object.values(formValues.entities[entityType.key].fields).forEach((val) => {
					db.fieldPermissions.save({ ...val, id: val.id || undefined })
				})

				Object.values(formValues.entities[entityType.key].actions).forEach((val) => {
					db.actionPermissions.save({ ...val, id: val.id || undefined })
				})

				Object.values(formValues.entities[entityType.key].relations).forEach((val) => {
					db.relationPermissions.save({ ...val, id: val.id || undefined })
				})
			})

			navigate(`${process.env.PUBLIC_URL}/settings/roles`)
		},
	})

	const fieldSchema = (entityTypeKey: string): TableSchema<any> => ({
		cols: [
			{
				title: 'Field',
				value: (x) => x.name,
			},
			{
				title: 'View',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entities.${entityTypeKey}.fields.${x.key}.view`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Modify',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entities.${entityTypeKey}.fields.${x.key}.modify`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	})

	const actionSchema = (entityTypeKey: string): TableSchema<any> => ({
		cols: [
			{
				title: 'Action',
				value: (x) => x.name,
			},
			{
				title: 'Perform',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entities.${entityTypeKey}.actions.${x.key}.perform`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	})

	const relationSchema = (entityTypeKey: string): TableSchema<any> => ({
		cols: [
			{
				title: 'Relation',
				value: (x) => x.name,
			},
			{
				title: 'View',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entities.${entityTypeKey}.relations.${x.key}.view`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Modify',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entities.${entityTypeKey}.relations.${x.key}.modify`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
			{
				title: 'Remove',
				value: (x, i) => (
					<div className="w-full flex justify-center">
						<FormCheckbox name={`entities.${entityTypeKey}.relations.${x.key}.remove`} />
					</div>
				),
				width: 'minmax(auto, max-content)',
			},
		],
	})

	const title = mode === 'create' ? 'Create Role' : 'Edit Role'

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: '/settings/roles',
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Form context={formContext}>
					<Stack>
						<FormSection title="Details" subtitle="Basic details about the role">
							<Card>
								<Stack>
									<FormInput
										name="name"
										label="Name"
										validations={{ required: 'Name is required' }}
									/>
								</Stack>
							</Card>
						</FormSection>

						<FormSection
							title="Permissions"
							subtitle="A view of permissions for all existing entity types and their fields/actions/relations"
						>
							<Stack>
								{db.entities.all().map((entityType) => (
									<CardWrapper key={entityType.key}>
										<CardHeader title={entityType.name} />
										<div className="rounded-b-md overflow-hidden">
											<Stack className="p-6">
												{(() => {
													let items = db.fields.all((x) => x.entityId === entityType.id)

													return (
														items.length > 0 && (
															<TableFrame>
																<Table
																	schema={fieldSchema(entityType.key)}
																	items={items}
																	className="-my-px"
																/>
															</TableFrame>
														)
													)
												})()}

												{(() => {
													let items = db.actions.all((x) => x.entityId === entityType.id)

													return (
														items.length > 0 && (
															<TableFrame>
																<Table
																	schema={actionSchema(entityType.key)}
																	items={items}
																	className="-my-px"
																/>
															</TableFrame>
														)
													)
												})()}

												{(() => {
													let items = db.relations.all((x) => x.entityId === entityType.id)

													return (
														items.length > 0 && (
															<TableFrame>
																<Table
																	schema={relationSchema(entityType.key)}
																	items={items}
																	className="-my-px"
																/>
															</TableFrame>
														)
													)
												})()}
											</Stack>
										</div>
									</CardWrapper>
								))}
							</Stack>
						</FormSection>

						<div className="flex items-center justify-end">
							<Actions
								actions={[
									{
										title: mode === 'create' ? 'Create' : 'Save',
										intent: 'primary',
										icon: <CheckIcon className="w-5 h-5" />,
										type: 'submit',
									},
								]}
							/>
						</div>
					</Stack>
				</Form>
			</PageContent>
		</>
	)
}

export default RoleForm
