import { uuid } from 'utils/funcs'
import { Tool, Widget } from 'components/PageBuilder/utils'
import { FormSectionType } from 'components/PageBuilder/Widgets/types'
import openEdit from './Edit'

import Build from './Build'
import View from './View'

export interface FormSectionWidget extends Widget {
	type: typeof FormSectionType
	data: {
		title?: string
		subtitle?: string
		items: Widget[]
	}
}

export const isFormSectionWidget = (x: any): x is FormSectionWidget => x.type === FormSectionType

export const FormSectionTool: Tool<FormSectionWidget> = {
	name: 'Form Section',
	type: FormSectionType,
	description: 'A box used for containing content',
	create: () => ({
		id: uuid(),
		type: FormSectionType,
		data: {
			title: '',
			subtitle: '',
			items: [],
		},
	}),
}

export const FormSectionBuild = Build
export const FormSectionView = View
export const openEditFormSection = openEdit
