import { FC, ReactNode, useEffect, useState } from 'react'
import PageContent from '@primer/components/PageContent'
import { CardWrapper } from '@primer/components/Card'
import clsx, { ClassValue } from 'clsx'
import Sidebar from '@primer/components/Sidebar'
import { isNavAnchor, isNavButton, isNavLink, NavLink, NavListItems } from '@primer/components/NavList'
import { navigate, useMatch } from '@reach/router'

interface PageNavbarProps {
	links: NavListItems
	className?: ClassValue
	children: ReactNode
}

const PageNavbar: FC<PageNavbarProps> = ({ links, className, children }) => {
	return (
		<PageContent className={className}>
			<CardWrapper className="relative overflow-hidden">
				<div className="max-w-full">
					<div className={clsx('flex flex-col md:grid grid-cols-[auto,1fr] max-w-full')}>
						<Sidebar
							minimizable
							items={links}
							intent="light"
							name="settings"
							className="py-3 col-span-1 hidden md:flex"
						/>

						<div className="md:hidden flex items-center p-4 space-x-4">
							<div className="text-sm font-medium">Navigation:</div>
							<SelectMenu routes={links} />
						</div>

						<div className="col-span-1 relative border-t md:border-t-0 md:border-l border-gray-200 dark:border-gray-600">
							<div>{children}</div>
						</div>
					</div>
				</div>
			</CardWrapper>
		</PageContent>
	)
}

interface SelectMenuProps {
	routes: NavListItems
}

const SelectMenu: FC<SelectMenuProps> = ({ routes }) => {
	const [match, setMatch] = useState(0)

	const handleNav = (i: number) => {
		let link = routes[i]

		if (isNavLink(link)) navigate(`${process.env.PUBLIC_URL}${link.to}`)
		if (isNavAnchor(link)) window.open(link.href)
		if (isNavButton(link)) link.onClick()
	}

	return (
		<select
			className="mt-1 block w-full pl-3 pr-10 py-2 text-base border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md"
			onChange={(e) => handleNav(+e.target.value)}
			value={match}
		>
			{routes
				// .filter(isNotNone)
				.filter(isNavLink)
				.map((route, i) => (
					<SelectOption key={i} route={route} index={i} setMatch={() => setMatch(i)} />
				))}
		</select>
	)
}

interface SelectOptionProps {
	route: NavLink
	index: number
	setMatch: () => void
}

const SelectOption: FC<SelectOptionProps> = ({ route, index, setMatch }) => {
	const match = useMatch(`${process.env.PUBLIC_URL}${route.to}`)

	useEffect(() => {
		match && setMatch()
	}, [match, setMatch])

	if (!route) return null

	return <option value={index}>{route.title}</option>
}

export default PageNavbar
