import { FC } from 'react'
import PageHeading from '@primer/components/PageHeading'
import PageNavbar from '@primer/components/PageNavbar'
import { RouteComponentProps } from '@reach/router'
import Table, { TableSchema } from '@primer/components/Table'
import {
	BeakerIcon,
	CheckIcon,
	DotsHorizontalIcon,
	PencilIcon,
	PlusIcon,
	TrashIcon,
	XIcon,
} from '@heroicons/react/outline'
import Badge from '@primer/components/Badge'
import { links } from '../Settings'
import SectionHeader from '@primer/components/SectionHeader'
import Stack from '@primer/components/Stack'
import db from 'db'
import { NotificationModel } from 'db/types'
import { confirm } from '@primer/alerts'

const Notifications: FC<RouteComponentProps> = () => {
	const schema: TableSchema<NotificationModel> = {
		cols: [
			{
				title: 'Name',
				value: (x) => x.name,
			},
			{
				title: 'Enabled',
				value: (x) => (
					<Badge
						color={x.enabled ? 'green' : 'red'}
						icon={x.enabled ? <CheckIcon className="w-5 h-5" /> : <XIcon className="w-5 h-5" />}
					/>
				),
				width: 'minmax(auto, max-content)',
			},
		],
		actions: (x) => [
			{
				icon: <DotsHorizontalIcon className="w-5 h-5" />,
				intent: 'menu',
				actions: [
					[
						{
							title: 'Edit',
							icon: <PencilIcon className="w-5 h-5" />,
							to: `/settings/notifications/${x.id}/edit`,
						},
						{
							title: 'Test',
							icon: <BeakerIcon className="w-5 h-5" />,
							onClick: () => {},
							disabled: true,
						},
					],
					[
						{
							title: 'Delete',
							intent: 'danger',
							icon: <TrashIcon className="w-5 h-5" />,
							onClick: () => {
								confirm({
									title: 'Are you sure?',
									message: 'This cannot be undone',
									onAccept: () => {
										db.notifications.remove(x.id)
									},
								})
							},
						},
					],
				],
			},
		],
	}

	return (
		<>
			<PageHeading title="Settings" />

			<PageNavbar links={links}>
				<Stack>
					<div className="px-6 pt-6">
						<SectionHeader
							title="Notifications"
							actions={[
								{
									title: 'Create Notification',
									icon: <PlusIcon className="w-5 h-5" />,
									to: '/settings/notifications/create',
								},
							]}
						/>
					</div>
					<div className="overflow-hidden rounded-br-md">
						<Table schema={schema} items={db.notifications.all()} className="-mb-px" />
					</div>
				</Stack>
			</PageNavbar>
		</>
	)
}

export default Notifications
