import { FC, useContext } from 'react'
import { PageBuilderContext, Path } from 'components/PageBuilder/utils'
import { lensPath, view } from 'ramda'
import { PageTitleWidget } from '.'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'

interface BuildProps {
	path: Path
}

const Build: FC<BuildProps> = ({ path }) => {
	const { itemList, removeItem, updateItem } = useContext(PageBuilderContext)
	let item: PageTitleWidget = view(lensPath(path), itemList)

	const [isHovered, bindHoverable] = useHoverable()

	if (!item) return null

	return (
		<div
			onClick={(e) => {
				e.stopPropagation()
				openEdit({ item, path, removeItem, updateItem })
			}}
			{...bindHoverable}
			className={clsx(
				'p-2 text-2xl leading-8 font-bold text-gray-900 dark:text-white sm:text-3xl sm:leading-9 whitespace-nowrap antialiased rounded-md cursor-pointer transition-all duration-100',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			{item.data.title}
		</div>
	)
}

export default Build
