import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { PageTitleType } from '../types'
import Build from './Build'
import View from './View'
import openEdit from './Edit'

export interface PageTitleWidget extends Widget {
	type: typeof PageTitleType
	data: {
		title: string
	}
}

export const isPageTitleWidget = (x: any): x is PageTitleWidget => x.type === PageTitleType

export const PageTitleTool: Tool<PageTitleWidget> = {
	name: 'Page Title',
	type: PageTitleType,
	description: 'A large title for putting between cards',
	create: () => ({
		id: uuid(),
		type: PageTitleType,
		data: {
			title: 'Page title...',
		},
	}),
}

export const PageTitleBuild = Build
export const PageTitleView = View
export const openEditPageTitle = openEdit
