import { ChevronLeftIcon } from '@heroicons/react/outline'
import { ActionType } from '@primer/types'
import db from 'db'

export const templateTagActions = (insertFn: (x: string) => void, level: string): ActionType[] => {
	let userEntity = db.entities.find((x) => x.key === 'user')

	return [
		{
			title: 'Recipient',
			icon: <ChevronLeftIcon className="w-4 h-4" />,
			iconSide: 'left',
			hoverable: true,
			placement: 'left-start',
			actions: db.fields
				.all((x) => x.entityId === userEntity?.id)
				.map((x) => ({
					title: x.name,
					onClick: () => insertFn(`{{recipient-${x.key}}}`),
				})),
		},
		...db.entities.all().map<ActionType>((entity) => ({
			title: entity.name,
			icon: <ChevronLeftIcon className="w-4 h-4" />,
			iconSide: 'left',
			hoverable: true,
			placement: 'left-start',
			actions: db.fields
				.all((x) => x.entityId === entity.id)
				.map((x) => ({
					title: x.name,
					onClick: () => insertFn(`{{${entity.key}-${x.key}}}`),
				})),
		})),
	]
}
