import { EntityResource } from 'db/types'
import { createContext } from 'react'
import { WidgetType } from '../Widgets/types'

export type Path = (string | number)[]

export interface PageBuilderContextType {
	entity: EntityResource | undefined | null
	itemList: { items: Widget[] }
	addItem: (path: Path, item: Widget) => void
	removeItem: (path: Path) => void
	updateItem: (path: Path, newValue: Widget) => void
}

export const PageBuilderContext = createContext<PageBuilderContextType>({
	entity: undefined,
	itemList: { items: [] },
	addItem: () => {},
	removeItem: () => {},
	updateItem: () => {},
})

export const PageBuilderProvider = PageBuilderContext.Provider

export interface PageRendererContextType {
	unitId: string
	itemList: { items: Widget[] }
}

export const PageRendererContext = createContext<PageRendererContextType>({
	unitId: '',
	itemList: { items: [] },
})

export const PageRendererProvider = PageRendererContext.Provider

export interface Widget {
	id: string
	type: WidgetType
	data: unknown
}
export const isWidget = (x: any): x is Widget => typeof x.id === 'string'

export interface Tool<T = Widget> {
	name: string
	type: string
	description: string
	create: () => T
}
export const isTool = (x: any): x is Tool => typeof x.create === 'function'
