interface ButtonProps {
	onClick?: (e: MouseEvent) => void
	children: ReactNode
	className?: ClassValue
	intent?: IntentType
	type?: 'button' | 'submit'
	disabled?: boolean
	isLoading?: boolean
	size?: ButtonSize
	rounded?: ButtonRoundness
	as?: ElementType
}

const Button = forwardRef<HTMLElement, ButtonProps>(
	(
		{
			onClick,
			type = 'button',
			intent = 'secondary',
			size = 'md',
			rounded = 'rounded',
			disabled,
			isLoading,
			className,
			as = 'button',
			children,
		},
		ref
	) => {
		const Comp = as

		const attrs = {}

		if (as === 'button') {
			// @ts-ignore
			attrs.type = type
		}

		return (
			<span className={clsx('inline-flex select-none', intents[intent].span, roundedness[rounded])} ref={ref}>
				<Comp
					{...attrs}
					className={clsx(
						'focus:outline-none',
						intents[intent].button,
						sizes[size],
						roundedness[rounded],
						className
					)}
					onClick={onClick}
					disabled={disabled || isLoading || false}
				>
					{isLoading && <Spinner className="animate-spin -ml-1 mr-3 h-4 w-4" />}
					{children}
				</Comp>
			</span>
		)
	}
)

export default Button
