import { isNotNone } from '@primer/types'
import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { sortValuesByLatest } from 'utils/funcs'
import { EntityResource, ValueResource, entityResource, valueResource, FieldModel } from '.'

export interface UnitModel extends Model {
	entityId: FK
}

export interface UnitResource extends UnitModel {
	readonly resolvedValues: Record<string, string | undefined>
	readonly entity: EntityResource | null
	readonly values: ValueResource[]
	readonly value: (id: Identifier<FieldModel>) => ValueResource | null
}

export const unitResource = (id: Identifier<UnitModel>): UnitResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.units.find(id) : id

	if (!data) return null

	let fields = entityResource((x) => x.id === data?.entityId)?.fields

	return {
		...data,
		resolvedValues:
			fields?.reduce((values, field) => {
				let foundValue = db.values
					.all((y) => y.unitId === data?.id && y.fieldId === field?.id)
					.sort(sortValuesByLatest)
					.map(valueResource)[0]

				return {
					...values,
					[field.key]: foundValue?.value,
				}
			}, {}) || {},
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
		get values() {
			let entity = db.entities.find((x) => x.id === data?.entityId)
			let fields = db.fields.all((x) => x.entityId === entity?.id)
			return fields
				.map((x) => db.values.all((y) => y.fieldId === x.id).sort(sortValuesByLatest)[0])
				.map(valueResource)
				.filter(isNotNone)
		},
		value: (id) => {
			let field = typeof id === 'string' || typeof id === 'function' ? db.fields.find(id) : id

			return db.values
				.all((y) => y.unitId === data?.id && y.fieldId === field?.id)
				.sort(sortValuesByLatest)
				.map(valueResource)[0]
		},
	}
}
