import { FC, useState } from 'react'
import { CardWrapper } from '@primer/components/Card'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import { Link, RouteComponentProps } from '@reach/router'
import db from 'db'
import Table, { TableSchema } from '@primer/components/Table'
import { entityResource, UnitModel } from 'db/types'
import { AdjustmentsIcon, CheckIcon, DotsHorizontalIcon, PencilIcon, TrashIcon } from '@heroicons/react/outline'
import { confirm } from '@primer/alerts'
import Actions from '@primer/components/Actions'

const Units: FC<RouteComponentProps<{ entityId: string }>> = ({ entityId = '' }) => {
	const entity = entityResource(entityId)

	const fields = entity?.fields

	const [visibleFields, setVisibleFields] = useState(fields)

	const schema: TableSchema<UnitModel> = {
		cols:
			fields
				?.filter((x) => visibleFields?.includes(x))
				.map((field, i) => ({
					title: field.name,
					value: (x) => {
						let val = field.value(x.id)?.formatted
						return i === 0 ? (
							<Link to={x.id} className="anchor">
								{val || '-'}
							</Link>
						) : (
							val || '-'
						)
					},
				})) || [],
		actions: (x) => [
			{
				icon: <DotsHorizontalIcon className="w-5 h-5" />,
				intent: 'menu',
				actions: [
					[
						{
							title: `Edit ${entity?.name}`,
							icon: <PencilIcon className="w-5 h-5" />,
							to: `/${entity?.key}/${x.id}/edit`,
						},
					],
					[
						{
							title: `Delete ${entity?.name}`,
							icon: <TrashIcon className="w-5 h-5" />,
							intent: 'danger',
							onClick: () => {
								confirm({
									title: 'Are you sure?',
									message: 'This cannot be undone',
									onAccept: () => {
										db.units.remove(x.id)
									},
								})
							},
						},
					],
				],
			},
		],
	}

	return (
		<>
			<PageHeading
				title={entity?.plural || entity?.name || '-'}
				actions={[
					{
						title: `Create ${entity?.name}`,
						intent: 'primary',
						to: `/${entity?.key}/create`,
					},
				]}
			/>

			<PageContent>
				<CardWrapper className="overflow-hidden">
					<div className="relative">
						<div className="absolute top-0 right-0 h-[42px] flex items-center mr-1">
							<Actions
								actions={{
									icon: <AdjustmentsIcon className="w-4 h-4" />,
									intent: 'menu',
									actions:
										fields?.map((x) => ({
											title: x.name,
											icon: visibleFields?.includes(x) ? (
												<CheckIcon className="w-4 h-4" />
											) : (
												<div className="w-4 h-4" />
											),
											iconSide: 'left',
											disableDismiss: true,
											onClick: () =>
												setVisibleFields((f) =>
													visibleFields?.includes(x)
														? f?.filter((y) => y !== x) || []
														: [...(f || []), x]
												),
										})) || [],
								}}
							/>
						</div>

						<Table
							schema={schema}
							items={db.units.all((x) => x.entityId === entity?.id)}
							className="-my-px"
						/>
					</div>
				</CardWrapper>
			</PageContent>
		</>
	)
}

export default Units
