import { FC, useContext } from 'react'
import { PageBuilderContext, Path } from 'components/PageBuilder/utils'
import { lensPath, view } from 'ramda'
import { FieldWidget } from '.'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'

interface BuildProps {
	path: Path
}

const Build: FC<BuildProps> = ({ path }) => {
	const context = useContext(PageBuilderContext)
	const { entity, itemList } = context
	const item: FieldWidget = view(lensPath(path), itemList)

	const field = entity?.fields.find((x) => x.id === item.data.fieldId)

	const [isHovered, bindHoverable] = useHoverable()

	if (!item) return null

	return (
		<div
			onClick={(e) => {
				e.stopPropagation()
				openEdit({ item, path, context })
			}}
			{...bindHoverable}
			className={clsx(
				'p-1 rounded-md cursor-pointer transition-all duration-100',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			<div className="text-sm leading-5 text-gray-500 dark:text-gray-300">Field</div>
			<div className="mt-1 text-sm leading-5 break-all text-gray-900 dark:text-white">
				{(item.data.label ? `"${item.data.label}"${field?.name ? ` (${field?.name})` : ''}` : field?.name) ||
					'Please choose a field'}
			</div>
		</div>
	)
}

export default Build
