import { FC, useState } from 'react'
import { Link, RouteComponentProps } from '@reach/router'
import {
	CalendarIcon,
	ChartBarIcon,
	CogIcon,
	FolderIcon,
	HomeIcon,
	InboxIcon,
	MenuIcon,
	UsersIcon,
} from '@heroicons/react/outline'
import { FullLogo } from 'components/Logo'
import Sidebar from '@primer/components/Sidebar'
import { NavListItems } from '@primer/components/NavList'
import clsx from 'clsx'
import useDarkMode from '@primer/hooks/useDarkMode'
import Stack from '@primer/components/Stack'
import MobileSidebar from '@primer/components/MobileSidebar'
import Actions from '@primer/components/Actions'

const Test: FC<RouteComponentProps> = () => {
	const [darkMode, setDarkMode] = useDarkMode()
	const [isDarkOpen, setDarkOpen] = useState(false)
	const [isLightOpen, setLightOpen] = useState(false)

	const items: NavListItems = [
		{ title: 'Home', to: '/test', icon: <HomeIcon className="w-5 h-5" /> },
		{ title: 'Team', to: '/team', icon: <UsersIcon className="w-5 h-5" /> },
		{ title: 'Test', to: '/blah', icon: <CogIcon className="w-5 h-5" /> },
		{ title: 'Projects', to: '/projects', icon: <FolderIcon className="w-5 h-5" /> },
		{ title: 'Calendar', href: '/calendar', icon: <CalendarIcon className="w-5 h-5" /> },
		{ title: 'Documents', onClick: () => console.log('/documents'), icon: <InboxIcon className="w-5 h-5" /> },
		{ title: 'Reports', to: '/reports', icon: <ChartBarIcon className="w-5 h-5" /> },
	]

	const extraItems: NavListItems = [
		{
			title: 'Links',
			items: [{ title: 'Resources', to: '/reports' }],
		},
	]

	return (
		<div className="h-screen flex flex-row">
			<Sidebar
				minimizable
				items={(min) => (!min ? items.concat(extraItems) : items)}
				header={
					<div className="px-2 my-10">
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<FullLogo className="h-11" />
						</Link>
					</div>
				}
				footer={(min) => (
					<div className={clsx('py-4', min ? 'w-full flex justify-center' : 'px-6')}>
						<div className="text-gray-400 text-sm font-medium antialiased">v0.0.0</div>
					</div>
				)}
			/>

			<Sidebar
				minimizable
				intent="light"
				items={(min) => (!min ? items.concat(extraItems) : items)}
				header={
					<div className="px-2 my-10">
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<FullLogo className="h-11" textClassName="text-primary-800 dark:text-white" />
						</Link>
					</div>
				}
				footer={(min) => (
					<div className={clsx('py-4', min ? 'w-full flex justify-center' : 'px-6')}>
						<div className="text-gray-400 text-sm font-medium antialiased">v0.0.0</div>
					</div>
				)}
			/>

			<MobileSidebar
				isOpen={isDarkOpen}
				setOpen={setDarkOpen}
				intent="dark"
				items={items.concat(extraItems)}
				header={
					<div className="px-2 my-2">
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<FullLogo className="h-11" />
						</Link>
					</div>
				}
				footer={
					<div className="py-4 px-6">
						<div className="text-gray-400 text-sm font-medium antialiased">v0.0.0</div>
					</div>
				}
			/>

			<MobileSidebar
				isOpen={isLightOpen}
				setOpen={setLightOpen}
				intent="light"
				items={items.concat(extraItems)}
				header={
					<div className="px-2 my-2">
						<Link to={`${process.env.PUBLIC_URL}/dashboard`}>
							<FullLogo className="h-11" textClassName="text-primary-800 dark:text-white" />
						</Link>
					</div>
				}
				footer={
					<div className="py-4 px-6">
						<div className="text-gray-400 text-sm font-medium antialiased">v0.0.0</div>
					</div>
				}
			/>

			<div className="p-6">
				<Stack>
					<Actions
						actions={[
							{
								title: `Toggle Dark Mode: ${darkMode ? 'On' : 'Off'}`,
								onClick: () => setDarkMode(!darkMode),
								intent: 'primary',
							},
						]}
					/>

					<button
						type="button"
						className="-ml-0.5 -mt-0.5 h-12 w-12 inline-flex items-center justify-center rounded-md text-gray-500 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500"
						onClick={() => setDarkOpen(true)}
					>
						<span className="sr-only">Open sidebar</span>
						<MenuIcon className="h-6 w-6" aria-hidden="true" />
					</button>

					<button
						type="button"
						className="-ml-0.5 -mt-0.5 h-12 w-12 inline-flex items-center justify-center rounded-md text-gray-500 hover:text-gray-900 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500"
						onClick={() => setLightOpen(true)}
					>
						<span className="sr-only">Open sidebar</span>
						<MenuIcon className="h-6 w-6" aria-hidden="true" />
					</button>
				</Stack>
			</div>
		</div>
	)
}

export default Test
