export const generateMonthList = (length: number) =>
	new Array(length).fill(null).map((_, i) => ({ label: ordinal(i + 1), value: i + 1 }))

export const getMonthOnOptions = (str: string) => {
	// @ts-ignore
	return {
		january: generateMonthList(31),
		february: generateMonthList(28),
		march: generateMonthList(31),
		april: generateMonthList(30),
		may: generateMonthList(31),
		june: generateMonthList(30),
		july: generateMonthList(31),
		august: generateMonthList(31),
		september: generateMonthList(30),
		october: generateMonthList(31),
		november: generateMonthList(30),
		december: generateMonthList(31),
	}[str]
}

