import { useCallback, useRef, useEffect } from 'react'
import { FormikContextType } from 'formik'
import update from 'immutability-helper'
import { FieldArray, FieldArrayPath, FieldValues } from '@primer/types/form'


// problem: https://github.com/formium/formik/issues/1476
// solution: https://gist.github.com/joshsalverda/d808d92f46a7085be062b2cbde978ae6
// plus some types from react-hook-form
const useFieldArray = <
  TFieldValues extends FieldValues = FieldValues,
  TFieldArrayName extends FieldArrayPath<TFieldValues> = FieldArrayPath<TFieldValues>,
>(
	name: TFieldArrayName,
	context: FormikContextType<TFieldValues>
) => {
	let setFieldValue = context.setFieldValue

	let field = context.getFieldProps<FieldArray<TFieldValues, TFieldArrayName>[]>(name)
	let meta = context.getFieldMeta(field.name)

	let fieldArray = useRef(field.value)

	useEffect(() => {
		fieldArray.current = field.value
	}, [field.value])

	const push = useCallback(
		(value) => {
			fieldArray.current = update(fieldArray.current, {
				$push: [value],
			})

			setFieldValue(field.name, fieldArray.current)
		},
		[field.name, setFieldValue]
	)

	const swap = useCallback(
		(indexA, indexB) => {
			const swapA = fieldArray.current[indexA]
			const swapB = fieldArray.current[indexB]

			fieldArray.current = update(fieldArray.current, {
				$splice: [
					[indexA, 1, swapB],
					[indexB, 1, swapA],
				],
			})

			setFieldValue(field.name, fieldArray.current)
		},
		[field.name, setFieldValue]
	)

	const move = useCallback(
		(from, to) => {
			const toMove = fieldArray.current[from]

			fieldArray.current = update(fieldArray.current, {
				$splice: [
					[from, 1],
					[to, 0, toMove],
				],
			})

			setFieldValue(field.name, fieldArray.current)
		},
		[field.name, setFieldValue]
	)

	const insert = useCallback(
		(index, value) => {
			fieldArray.current = update(fieldArray.current, {
				$splice: [[index, 0, value]],
			})

			setFieldValue(field.name, fieldArray.current)
		},
		[field.name, setFieldValue]
	)

	const unshift = useCallback(
		(value) => {
			fieldArray.current = update(fieldArray.current, {
				$unshift: [value],
			})

			setFieldValue(field.name, fieldArray.current)
			return fieldArray.current.length
		},
		[field.name, setFieldValue]
	)

	const remove = useCallback(
		(index) => {
			const removedItem = fieldArray.current[index]

			fieldArray.current = update(fieldArray.current, {
				$splice: [[index, 1]],
			})

			setFieldValue(field.name, fieldArray.current)
			return removedItem
		},
		[field.name, setFieldValue]
	)

	const pop = useCallback(() => {
		const lastIndex = fieldArray.current.length - 1
		const poppedItem = fieldArray.current[lastIndex]

		fieldArray.current = update(fieldArray.current, {
			$splice: [[lastIndex, 1]],
		})

		setFieldValue(field.name, fieldArray.current)
		return poppedItem
	}, [field.name, setFieldValue])

	const replace = useCallback(
		(index, value) => {
			fieldArray.current = update(fieldArray.current, {
				$splice: [[index, 1, value]],
			})

			setFieldValue(field.name, fieldArray.current)
		},
		[field.name, setFieldValue]
	)

	return {
		field,
		// this is kind of weird, if the type of this field in your form is an array, then cool.
		// If you're trying to use useFieldArray on a field in your form that is not an array, then, you're an idiot
		// and the "values" property will be incorrectly typed as an arrayed version of the fields type
		values: field.value || [],
		meta,
		arrayHelpers: {
			push,
			swap,
			move,
			insert,
			unshift,
			remove,
			pop,
			replace,
		},
	}
}

export default useFieldArray
