import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { RoleResource, ActionResource, roleResource, actionResource } from '.'

export interface ActionPermissionModel extends Model {
	roleId: FK
	actionId: FK
	perform: boolean
}

export interface ActionPermissionResource extends ActionPermissionModel {
	readonly role: RoleResource | null
	readonly action: ActionResource | null
}

export const actionPermissionResource = (id: Identifier<ActionPermissionModel>): ActionPermissionResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.actionPermissions.find(id) : id

	if (!data) return null

	return {
		...data,
		get role() {
			return roleResource((x) => x.id === data?.roleId)
		},
		get action() {
			return actionResource((x) => x.id === data?.actionId)
		},
	}
}
