import { forwardRef } from 'react'
import clsx, { ClassValue } from 'clsx'
import ReactQuill from 'react-quill'
import Actions from '../Actions'
import { ActionType } from '@primer/types'
import { useField, useFormikContext } from 'formik'
import { FormValidations, validate } from './utils'

export const defaultEditorModules = {
	toolbar: [
		[{ header: [1, 2, false] }],
		['bold', 'italic', { color: [] }],
		[{ list: 'ordered' }, { list: 'bullet' }],
		[{ align: [] }],
		['clean'],
	],
}

interface FormEditorProps {
	name: string
	label?: string
	className?: ClassValue
	validations?: FormValidations
	hideOptional?: boolean
	disabled?: boolean
	modules?: any
	actions?: ActionType[]
}

const FormEditor = forwardRef<ReactQuill, FormEditorProps>(
	({ name, label, className, validations = {}, hideOptional, disabled, modules, actions }, ref) => {
		const formik = useFormikContext()

		const [{ value }, { error, touched }, { setValue, setTouched }] = useField({
			name,
			validate: validate(validations),
		})

		const isRequired = !!validations.required

		return (
			<div className={clsx(className)}>
				{label && (
					<div className="block text-sm font-medium leading-5 text-gray-700 dark:text-gray-200 cursor-default">
						{label}{' '}
						{!isRequired && !hideOptional && (
							<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
								(optional)
							</span>
						)}
					</div>
				)}

				<div className={clsx('relative h-full', label && 'mt-1')}>
					{actions && (
						<div className="absolute top-0 right-0 mr-1.5 h-11 flex items-center">
							<Actions actions={actions} />
						</div>
					)}

					<ReactQuill
						ref={ref}
						value={value || ''}
						onChange={(content) => {
							// setTouched(true)
							setValue(content)
						}}
						onBlur={(e) => {
							setTouched(true)
						}}
						modules={modules || defaultEditorModules}
						readOnly={disabled || formik.isSubmitting}
						className="h-full"
					/>
				</div>

				{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
			</div>
		)
	}
)

export default FormEditor
