import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { InputType } from '../types'
import openEdit from './Edit'
import Build from './Build'
import View from './View'
import { InputIntent } from '@primer/components/Input'

export interface InputWidget extends Widget {
	type: typeof InputType
	data: {
		fieldId: string
		label?: string
		intent: InputIntent
		required: boolean
	}
}

export const isInputWidget = (x: any): x is InputWidget => x.type === InputType

export const InputTool: Tool<InputWidget> = {
	name: 'Input',
	type: InputType,
	description: 'Input a value',
	create: () => ({
		id: uuid(),
		type: InputType,
		data: {
			fieldId: '',
			label: '',
			intent: 'default',
			required: false,
		},
	}),
}

export const InputBuild = Build
export const InputView = View
export const openEditInput = openEdit
