export type Box = { top: number; right: number; bottom: number; left: number }
export type Point = { x: number; y: number }

export const inside = (point: Point, box: Box) => {
	return point.x > box.left && point.x < box.right && point.y > box.top && point.y < box.bottom
}

export const distance_aux = (p: number, lower: number, upper: number) => {
	if (p < lower) return lower - p
	if (p > upper) return p - upper
	return 0
}

export const distance = (point: Point, box: Box) => {
	let dx = distance_aux(point.x, box.left, box.right)
	let dy = distance_aux(point.y, box.top, box.bottom)
	if (inside(point, box)) return Math.min(dx, dy)
	// or 0 in case of distance from the area
	else return Math.sqrt(dx * dx + dy * dy)
}
