export type Path = (string | number)[]

export interface Widget {
	id: string
	type: string
	data: unknown
}
export const isWidget = (x: any): x is Widget => typeof x.id === 'string'

export interface Tool<T = Widget> {
	name: string
	type: string
	description: string
	create: () => T
}
export const isTool = (x: any): x is Tool => typeof x.create === 'function'

export const CardType = 'Card'
export const WellType = 'Well'
export const PageTitleType = 'PageTitle'
export const ColumnsType = 'Columns'
export const FieldType = 'Field'
export const DividerType = 'Divider'
export const SectionType = 'Section'
export const TableType = 'Table'
export const FormSectionType = 'FormSection'
export const InputType = 'Input'

export type WidgetType =
	| typeof CardType
	| typeof WellType
	| typeof PageTitleType
	| typeof ColumnsType
	| typeof FieldType
	| typeof DividerType
	| typeof SectionType
	| typeof TableType
	| typeof FormSectionType
	| typeof InputType
