import { FC, useState } from 'react'
import { RouteComponentProps } from '@reach/router'
import PageHeading from '@primer/components/PageHeading'
import PageContent from '@primer/components/PageContent'
import Actions from '@primer/components/Actions'
import { ArrowLeftIcon, ChevronDownIcon, ChevronUpIcon, PencilIcon } from '@heroicons/react/outline'
import { FieldModel, unitResource, valueResource, ValueResource } from 'db/types'
import clsx from 'clsx'
import Card, { CardContent, CardWrapper } from '@primer/components/Card'
import { isNotNone } from '@primer/types'
import { formatRelative } from 'date-fns'
import CardHeader from '@primer/components/CardHeader'
import Select from '@primer/components/Select'
import Stack from '@primer/components/Stack'
import FormLabel from '@primer/components/Form/FormLabel'
import DatePicker from '@primer/components/DatePicker'
import AutoHeight from '@primer/components/AutoHeight'

const UnitHistory: FC<RouteComponentProps<{ id: string }>> = ({ id = '' }) => {
	const unit = unitResource(id)
	const entity = unit?.entity

	const values = unit?.values || []

	const history = values
		.reduce<ValueResource[]>((list, value) => {
			let history = value.history.map(valueResource).filter(isNotNone)
			return [...list, ...history]
		}, [])
		.sort((a: ValueResource, b: ValueResource) => {
			return new Date(b.created).valueOf() - new Date(a.created).valueOf()
		})

	const title = `${entity?.name} History`

	const [open, setOpen] = useState(false)

	const [fieldFilter, setFieldFilter] = useState<FieldModel | null>(null)
	const [dateFromFilter, setDateFromFilter] = useState<Date | null>(null)
	const [dateToFilter, setDateToFilter] = useState<Date | null>(null)
	const [userFilter, setUserFilter] = useState<string | null>(null)

	const filteredHistory = history
		.filter((val) => {
			if (fieldFilter === null) return true
			return val.fieldId === fieldFilter.id
		})
		.filter((val) => {
			if (dateFromFilter === null) return true
			return new Date(val.created).valueOf() > dateFromFilter.valueOf()
		})
		.filter((val) => {
			if (dateToFilter === null) return true
			return new Date(val.created).valueOf() < dateToFilter.valueOf()
		})

	return (
		<>
			<PageHeading
				pageTitle={title}
				title={
					<div className="flex items-center space-x-3">
						<Actions
							actions={{
								icon: <ArrowLeftIcon className="w-5 h-5 -mx-2" />,
								intent: 'borderless',
								rounded: 'full',
								to: `/${entity?.key}/${id}`,
							}}
						/>
						<div>{title}</div>
					</div>
				}
			/>

			<PageContent>
				<Stack>
					<CardWrapper>
						<CardHeader
							title="Filter"
							actions={[
								{
									icon: open ? (
										<ChevronUpIcon className="w-5 h-5" />
									) : (
										<ChevronDownIcon className="w-5 h-5" />
									),
									intent: 'menu',
									onClick: () => setOpen((x) => !x),
								},
							]}
						/>
						<AutoHeight show={open}>
							<CardContent>
								<div className="grid grid-cols-2 gap-4">
									<div className="col-span-1">
										<FormLabel>Field</FormLabel>
										<Select
											value={fieldFilter}
											onChange={setFieldFilter}
											options={entity?.fields.map((x) => ({ value: x, label: x.name })) || []}
											clearable
											className="mt-1"
										/>
									</div>
									<div className="col-span-1">
										<FormLabel>User</FormLabel>
										<Select
											value={userFilter}
											onChange={setUserFilter}
											options={[{ value: 'user', label: 'User' }]}
											clearable
											className="mt-1"
										/>
									</div>
									<div className="col-span-1">
										<FormLabel>Created From</FormLabel>
										<DatePicker
											value={dateFromFilter}
											onChange={setDateFromFilter}
											className="mt-1"
										/>
									</div>
									<div className="col-span-1">
										<FormLabel>Created To</FormLabel>
										<DatePicker value={dateToFilter} onChange={setDateToFilter} className="mt-1" />
									</div>
								</div>
							</CardContent>
						</AutoHeight>
					</CardWrapper>

					<Card>
						<div className="flow-root">
							<ul className="-mb-8">
								{filteredHistory.map((value, i) => (
									<li key={value.id}>
										<div className="relative pb-8">
											{i !== filteredHistory.length - 1 ? (
												<span
													className="absolute top-4 left-4 -ml-px h-full w-0.5 bg-gray-200"
													aria-hidden="true"
												/>
											) : null}
											<div className="relative flex items-center space-x-3">
												<div>
													<span
														className={clsx(
															'bg-primary-600',
															'h-8 w-8 rounded-full flex items-center justify-center ring-8 ring-white'
														)}
													>
														<PencilIcon className="h-5 w-5 text-white" aria-hidden="true" />
													</span>
												</div>
												<div className="min-w-0 flex-1 pt-0 flex justify-between space-x-4">
													<div>
														<p className="text-sm text-gray-500">
															{value.field?.name} set to{' '}
															<strong className="font-medium text-gray-900">
																"{value.formatted}"
															</strong>{' '}
															by <a href="/">User</a>
														</p>
													</div>
													<div className="text-right text-sm whitespace-nowrap text-gray-500">
														<time dateTime={value.created}>
															{formatRelative(new Date(value.created || 0), new Date())}
														</time>
													</div>
												</div>
											</div>
										</div>
									</li>
								))}
							</ul>
						</div>
					</Card>
				</Stack>
			</PageContent>
		</>
	)
}

export default UnitHistory
