import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { CardType } from '../types'
import openEdit from './Edit'

import Build from './Build'
import View from './View'

export interface CardWidget extends Widget {
	type: typeof CardType
	data: {
		title?: string
	}
}

export const isCardWidget = (x: any): x is CardWidget => x.type === CardType

export const CardTool: Tool<CardWidget> = {
	name: 'Card',
	type: CardType,
	description: 'A box used for containing content',
	create: (data: Partial<CardWidget>) => ({
		id: uuid(),
		type: CardType,
		index: 0,
		...data,
		data: {
			title: '',
		},
	}),
}

export const CardBuild = Build
export const CardView = View
export const openEditCard = openEdit
