import { FC, Fragment, useContext } from 'react'
import { PageBuilderContext, Path } from 'components/PageBuilder/utils'
import { lensPath, view } from 'ramda'
import { FormSectionWidget } from '.'
import DropTarget from 'components/PageBuilder/components/DropTarget'
import { BuildComp } from 'components/PageBuilder/Widgets/index'
import { CardType } from 'components/PageBuilder/Widgets/types'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'
import FormSection from '@primer/components/Form/FormSection'

const Accepts: string[] = [CardType]

interface BuildProps {
	path: Path
}

const Build: FC<BuildProps> = ({ path }) => {
	const { itemList, removeItem, updateItem } = useContext(PageBuilderContext)
	let item: FormSectionWidget = view(lensPath(path), itemList)

	const [isHovered, bindHoverable] = useHoverable()

	if (!item) return null

	return (
		<div
			onClick={(e) => {
				e.stopPropagation()
				openEdit({ item, path, removeItem, updateItem })
			}}
			{...bindHoverable}
			className={clsx(
				'rounded-md cursor-pointer transition-all duration-100',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			<FormSection
				title={item.data.title || 'Form Section Title...'}
				subtitle={item.data.subtitle || 'Form Section Subtitle...'}
			>
				{item.data.items.map((x, i) => (
					<Fragment key={x.id}>
						<DropTarget path={[...path, 'data', 'items', i]} isFirst={i === 0} accept={Accepts} />
						<BuildComp path={[...path, 'data', 'items', i]} />
					</Fragment>
				))}
				<DropTarget
					path={[...path, 'data', 'items', item.data.items.length]}
					isFirst={item.data.items.length === 0}
					isLast
					accept={Accepts}
				/>
			</FormSection>
		</div>
	)
}

export default Build
