import { uuid } from 'utils/funcs'
import { Tool, Widget } from '../../utils'
import { WellType } from '../types'
import Build from './Build'
import View from './View'
import openEdit from './Edit'

export interface WellWidget extends Widget {
	type: typeof WellType
	data: {
		items: Widget[]
	}
}

export const isWellWidget = (x: any): x is WellWidget => x.type === WellType

export const WellTool: Tool<WellWidget> = {
	name: 'Well',
	type: WellType,
	description: 'A box used for sectioning content',
	create: () => ({
		id: uuid(),
		type: WellType,
		data: {
			items: [],
		},
	}),
}

export const WellBuild = Build
export const WellView = View
export const openEditWell = openEdit
