import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { RoleResource, FieldResource, roleResource, fieldResource } from '.'

export interface FieldPermissionModel extends Model {
	roleId: FK
	fieldId: FK
	view: boolean
	modify: boolean
}

export interface FieldPermissionResource extends FieldPermissionModel {
	readonly role: RoleResource | null
	readonly field: FieldResource | null
}

export const fieldPermissionResource = (id: Identifier<FieldPermissionModel>): FieldPermissionResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.fieldPermissions.find(id) : id

	if (!data) return null

	return {
		...data,
		get role() {
			return roleResource((x) => x.id === data?.roleId)
		},
		get field() {
			return fieldResource((x) => x.id === data?.fieldId)
		},
	}
}
