import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { RoleResource, RelationResource, roleResource, relationResource } from '.'

export interface RelationPermissionModel extends Model {
	roleId: FK
	relationId: FK
	view: boolean
	modify: boolean
	remove: boolean
}

export interface RelationPermissionResource extends RelationPermissionModel {
	readonly role: RoleResource | null
	readonly relation: RelationResource | null
}

export const relationPermissionResource = (
	id: Identifier<RelationPermissionModel>
): RelationPermissionResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.relationPermissions.find(id) : id

	if (!data) return null

	return {
		...data,
		get role() {
			return roleResource((x) => x.id === data?.roleId)
		},
		get relation() {
			return relationResource((x) => x.id === data?.relationId)
		},
	}
}
