import db from 'db'
import { Model, Identifier } from 'types/db'
import { workflowStepResource, WorkflowStepResource } from '.'

export interface WorkflowStepConnectionModel extends Model {
	sourceId: string
	targetId: string
	trigger: string
	field: string
	fieldValue: string
}

export interface WorkflowStepConnectionResource extends WorkflowStepConnectionModel {
	readonly target: WorkflowStepResource | null
}

export const workflowStepConnectionResource = (
	id: Identifier<WorkflowStepConnectionModel>
): WorkflowStepConnectionResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.workflowStepConnections.find(id) : id

	if (!data) return null

	return {
		...data,
		get target() {
			return workflowStepResource(data?.targetId || '')
		},
	}
}
