import { FC, Fragment, useContext } from 'react'
import { PageBuilderContext, Path } from 'components/PageBuilder/utils'
import { lensPath, view } from 'ramda'
import { ColumnsWidget } from '.'
import clsx from 'clsx'
import openEdit from './Edit'
import useHoverable from 'hooks/useHoverable'
import DropTarget from 'components/PageBuilder/components/DropTarget'
import { BuildComp } from '..'
import { FieldType } from '../types'

const Accepts = [FieldType]

interface BuildProps {
	path: Path
}

const Build: FC<BuildProps> = ({ path }) => {
	const context = useContext(PageBuilderContext)
	const { itemList } = context
	const item: ColumnsWidget = view(lensPath(path), itemList)

	const [isHovered, bindHoverable] = useHoverable()

	if (!item) return null

	return (
		<div
			onClick={(e) => {
				e.stopPropagation()
				openEdit({ item, path, context })
			}}
			{...bindHoverable}
			className={clsx(
				'flex space-x-2 bg-gray-100 p-2 rounded-md cursor-pointer transition-all duration-10',
				isHovered && 'ring-4 ring-primary-500'
			)}
		>
			<div
				className={clsx('p-2 rounded-md bg-white', {
					'w-1/2': item.data.format === '1:1',
					'w-1/3': item.data.format === '1:2',
					'w-2/3': item.data.format === '2:1',
				})}
			>
				{item.data.items[0].map((x, i) => (
					<Fragment key={x.id}>
						<DropTarget path={[...path, 'data', 'items', 0, i]} isFirst={i === 0} accept={Accepts} />
						<BuildComp path={[...path, 'data', 'items', 0, i]} />
					</Fragment>
				))}
				<DropTarget
					path={[...path, 'data', 'items', 0, item.data.items.length]}
					isFirst={item.data.items[0].length === 0}
					isLast
					accept={Accepts}
				/>
			</div>

			<div
				className={clsx('p-2 rounded-md bg-white', {
					'w-1/2': item.data.format === '1:1',
					'w-1/3': item.data.format === '2:1',
					'w-2/3': item.data.format === '1:2',
				})}
			>
				{item.data.items[1].map((x, i) => (
					<Fragment key={x.id}>
						<DropTarget path={[...path, 'data', 'items', 1, i]} isFirst={i === 0} accept={Accepts} />
						<BuildComp path={[...path, 'data', 'items', 1, i]} />
					</Fragment>
				))}
				<DropTarget
					path={[...path, 'data', 'items', 1, item.data.items.length]}
					isFirst={item.data.items[1].length === 0}
					isLast
					accept={Accepts}
				/>
			</div>
		</div>
	)
}

export default Build
