import { FC, Component } from 'react'
import { Location, WindowLocation } from '@reach/router'

interface OnRouteChangeProps {
	action: () => void
}

const OnRouteChange: FC<OnRouteChangeProps> = ({ action }) => (
	<Location>
		{({ location }: { location: WindowLocation }) => <OnRouteChangeWorker location={location} action={action} />}
	</Location>
)

interface OnRouteChangeWorkerProps {
	location: WindowLocation
	action: () => void
}

class OnRouteChangeWorker extends Component<OnRouteChangeWorkerProps, {}> {
	componentDidUpdate(prevProps: OnRouteChangeWorkerProps) {
		if (this.props.location.pathname !== prevProps.location.pathname) {
			this.props.action()
		}
	}

	render() {
		return null
	}
}

export default OnRouteChange
