import { FC, useContext } from 'react'
import { PageRendererContext } from 'components/PageBuilder/utils'
import { view, lensPath } from 'ramda'
import { PageTitleWidget } from '.'
import { Path } from '../types'
import { replaceMarkers } from 'utils/funcs'
import { unitResource } from 'db/types'

const View: FC<{ path: Path }> = ({ path }) => {
	let { itemList, unitId } = useContext(PageRendererContext)
	let item: PageTitleWidget = view(lensPath(path), itemList)

	if (!item) return null

	let unit = unitResource(unitId)

	const markers = unit?.resolvedValues || {}

	return (
		<div className="text-2xl leading-8 font-bold text-gray-900 dark:text-white sm:text-3xl sm:leading-9 whitespace-nowrap antialiased">
			{replaceMarkers(item.data.title, markers, (str) => `{{${str}}}`)}
		</div>
	)
}

export default View
