import { ChangeEvent, FC, ReactNode, useState } from 'react'
import clsx, { ClassValue } from 'clsx'
import useAutoFocus from '@primer/hooks/useAutoFocus'
import { v4 as uuidv4 } from 'uuid'
import { ExclamationCircleIcon } from '@heroicons/react/outline'

export type InputIntent = 'default' | 'overlap' | 'inset'
export const inputIntentOptions = [
	{ label: 'Default', value: 'default' },
	{ label: 'Overlap', value: 'overlap' },
	{ label: 'Inset', value: 'inset' },
]

interface InputProps {
	label?: ReactNode
	type?: string
	value?: any
	onChange?: (e: ChangeEvent<HTMLInputElement>) => void
	onBlur?: () => void
	intent?: InputIntent
	isRequired?: string
	error?: string
	touched?: boolean
	description?: string
	className?: ClassValue
	autoFocus?: boolean
	hideOptional?: boolean
	placeholder?: string
	disabled?: boolean
	step?: number
	inputProps?: {
		min?: number
		max?: number
		step?: number
		className?: string
	}
}

const Input: FC<InputProps> = ({
	label,
	type = 'text',
	value,
	onChange,
	onBlur,
	intent = 'default',
	isRequired,
	error,
	touched = false,
	description,
	className,
	autoFocus = false,
	hideOptional = false,
	placeholder,
	disabled = false,
	inputProps = {},
}) => {
	const ref = useAutoFocus<HTMLInputElement>(autoFocus)

	const { className: extraClassName, ...extraProps } = inputProps

	const [token] = useState(uuidv4())

	return intent === 'default' ? (
		<div className={clsx(className)}>
			{label && (
				<div
					className="block text-sm font-medium leading-5 text-gray-700 dark:text-gray-200 cursor-default"
					onClick={() => ref.current?.focus()}
				>
					{label}{' '}
					{!isRequired && !hideOptional && (
						<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
							(optional)
						</span>
					)}
				</div>
			)}
			<div className="text-[0px] leading-none select-none">{token}</div>
			<div className={clsx('relative rounded-md', label && 'mt-1')}>
				<input
					ref={ref}
					type={type}
					value={value}
					onChange={onChange}
					onBlur={onBlur}
					placeholder={placeholder}
					disabled={disabled}
					className={clsx(
						'form-input block w-full sm:text-sm sm:leading-5',
						extraClassName,
						error &&
							touched &&
							'pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500'
					)}
					{...extraProps}
				/>
				{error && touched && (
					<div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
						<ExclamationCircleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
					</div>
				)}
			</div>
			{description && <p className="mt-2 text-xs text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	) : intent === 'overlap' ? (
		<div className={clsx(className)}>
			<div
				className={clsx(
					'relative border border-gray-300 dark:border-gray-500 rounded-md px-3 py-2 focus-within:ring-1 focus-within:ring-primary-500 focus-within:border-primary-500 transition-all duration-200',
					error && touched
						? 'focus-within:ring-red-500 focus-within:border-red-500 dark:focus-within:ring-red-500 dark:focus-within:border-red-500'
						: 'focus-within:ring-primary-500 focus-within:border-primary-500'
				)}
			>
				{label && (
					<div
						className="absolute -top-2 left-2 -mt-px inline-block px-1 bg-white dark:bg-gray-700 text-xs font-medium text-gray-900 dark:text-white"
						onClick={() => ref.current?.focus()}
					>
						{label}{' '}
						{!isRequired && !hideOptional && (
							<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
								(optional)
							</span>
						)}
					</div>
				)}
				<div className="text-[0px] leading-none select-none">{token}</div>

				<div className={clsx('relative rounded-md')}>
					<input
						ref={ref}
						type={type}
						value={value}
						onChange={onChange}
						onBlur={onBlur}
						placeholder={placeholder}
						disabled={disabled}
						className={clsx(
							'block w-full border-0 p-0 bg-white dark:bg-gray-700 focus:ring-0 sm:text-sm',
							extraClassName,
							error && touched
								? 'pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500'
								: 'text-gray-900 dark:text-white placeholder-gray-500 dark:placeholder-gray-400'
						)}
						{...extraProps}
					/>
				</div>
				{error && touched && (
					<div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
						<ExclamationCircleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
					</div>
				)}
			</div>
			{description && <p className="mt-2 text-xs text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	) : intent === 'inset' ? (
		<div className={clsx(className)}>
			<div
				className={clsx(
					'border rounded-md px-3 py-2 focus-within:ring-1 focus-within:ring-primary-500 transition-all duration-200'
				)}
			>
				<div className="block text-xs font-medium text-gray-900" onClick={() => ref.current?.focus()}>
					{label}{' '}
					{!isRequired && !hideOptional && (
						<span className="text-gray-400 dark:text-gray-300 font-normal text-xs leading-4 opacity-75">
							(optional)
						</span>
					)}
				</div>
				<div className="relative">
					<input
						ref={ref}
						type={type}
						value={value}
						onChange={onChange}
						onBlur={onBlur}
						placeholder={placeholder}
						disabled={disabled}
						className={clsx(
							'block w-full border-0 p-0 text-gray-900 placeholder-gray-500 focus:ring-0 sm:text-sm',
							extraClassName,
							error &&
								touched &&
								'pr-10 border-red-300 text-red-900 placeholder-red-300 focus:outline-none focus:ring-red-500 focus:border-red-500'
						)}
						{...extraProps}
					/>
					{error && touched && (
						<div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
							<ExclamationCircleIcon className="h-5 w-5 text-red-500" aria-hidden="true" />
						</div>
					)}
				</div>
			</div>
			{description && <p className="mt-2 text-xs text-gray-500">{description}</p>}
			{error && touched && <p className="mt-2 text-sm text-red-600">{error}</p>}
		</div>
	) : null
}

export default Input
