import { isNotNone } from '@primer/types'
import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { sortValuesByLatest } from 'utils/funcs'
import {
	EntityResource,
	UnitModel,
	ValueResource,
	FieldPermissionResource,
	RoleModel,
	entityResource,
	valueResource,
	fieldPermissionResource,
} from '.'

export type FieldType = 'string' | 'number' | 'date' | 'boolean' | 'option'

export interface FieldModel extends Model {
	entityId: FK
	key: string
	name: string
	plural?: string
	template?: string
	type: FieldType
	options: Array<string>
	dateFormat?: string
	defaultValue?: string
}

export interface FieldResource extends FieldModel {
	readonly entity: EntityResource | null
	readonly value: (unitId: Identifier<UnitModel>) => ValueResource | null
	readonly values: (unitId: Identifier<UnitModel>) => ValueResource[]
	readonly permissions: FieldPermissionResource[]
	readonly permission: (roleId: Identifier<RoleModel>) => FieldPermissionResource | null
}

export const fieldResource = (id: Identifier<FieldModel>): FieldResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.fields.find(id) : id

	if (!data) return null

	return {
		...data,
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
		value: (unitId) => {
			let unit = typeof unitId === 'string' || typeof unitId === 'function' ? db.units.find(unitId) : unitId
			return valueResource(
				db.values.all((x) => x.unitId === unit?.id && x.fieldId === data?.id).sort(sortValuesByLatest)[0]
			)
		},
		values: (unitId) => {
			let unit = typeof unitId === 'string' || typeof unitId === 'function' ? db.units.find(unitId) : unitId
			return db.values
				.all((x) => x.unitId === unit?.id && x.fieldId === data?.id)
				.sort(sortValuesByLatest)
				.map(valueResource)
				.filter(isNotNone)
		},
		get permissions() {
			return db.fieldPermissions
				.all((x) => x.fieldId === data?.id)
				.map(fieldPermissionResource)
				.filter(isNotNone)
		},
		permission: (roleId) => {
			let role = typeof roleId === 'string' || typeof roleId === 'function' ? db.roles.find(roleId) : roleId
			return fieldPermissionResource((x) => x.fieldId === data?.id && x.roleId === role?.id)
		},
	}
}
