import { FC } from 'react'
import { PageModel } from 'db/types'
import PageBuilder2, { PageStructure } from 'components/PageBuilder2/PageBuilder'
import { ModalResolveFn } from '@primer/types'
import openModal from '@primer/hooks/useModal'
import { pbtools } from 'components/PageBuilder2/Widgets'

interface OpenPageBuilderProps {
	page: PageModel
	mode: 'create' | 'edit'
	entityId?: string
}

const openPageBuilder = ({ mode, ...props }: OpenPageBuilderProps) => {
	return openModal<PageModel>({
		title: mode === 'create' ? 'Create Page Layout' : 'Edit Page Layout',
		size: 'screen',
		render: (close) => <PageBuilderModal {...props} close={close} />,
	})
}

const PageBuilderModal: FC<Omit<OpenPageBuilderProps, 'mode'> & { close: ModalResolveFn<PageModel> }> = ({
	page,
	entityId,
	close,
}) => {
	return (
		<>
			<PageBuilder2
				page={page}
				onSave={(x: PageStructure) => close({ ...page, ...x })}
				entityId={entityId}
				tools={pbtools}
			/>
		</>
	)
}

export default openPageBuilder
