import { isNotNone } from '@primer/types'
import db from 'db'
import { Identifier, Model } from 'types/db'
import {
	FieldModel,
	EntityModel,
	fieldPermissionResource,
	FieldPermissionResource,
	ActionModel,
	ActionPermissionResource,
	actionPermissionResource,
	RelationModel,
	RelationPermissionResource,
	relationPermissionResource,
} from '.'

export interface RoleModel extends Model {
	name: string
}

export interface RoleResource extends RoleModel {
	readonly fieldPermission: (x: Identifier<FieldModel>) => FieldPermissionResource | null
	readonly fieldPermissions: (x: Identifier<EntityModel>) => FieldPermissionResource[]
	readonly actionPermission: (x: Identifier<ActionModel>) => ActionPermissionResource | null
	readonly actionPermissions: (x: Identifier<EntityModel>) => ActionPermissionResource[]
	readonly relationPermission: (x: Identifier<RelationModel>) => RelationPermissionResource | null
	readonly relationPermissions: (x: Identifier<EntityModel>) => RelationPermissionResource[]
}

export const roleResource = (id: Identifier<RoleModel>): RoleResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.roles.find(id) : id

	if (!data) return null

	return {
		...data,
		fieldPermission: (fieldId) => {
			let field = typeof fieldId === 'string' || typeof fieldId === 'function' ? db.fields.find(fieldId) : fieldId
			return fieldPermissionResource((y) => y.fieldId === field?.id && y.roleId === data?.id)
		},
		fieldPermissions: (entityId) => {
			let entity =
				typeof entityId === 'string' || typeof entityId === 'function' ? db.entities.find(entityId) : entityId
			let fields = db.fields.all((x) => x.entityId === entity?.id)
			return fields
				.map((x) => db.fieldPermissions.find((y) => y.fieldId === x.id && y.roleId === data?.id))
				.filter(isNotNone)
				.map(fieldPermissionResource)
				.filter(isNotNone)
		},

		actionPermission: (actionId) => {
			let action =
				typeof actionId === 'string' || typeof actionId === 'function' ? db.actions.find(actionId) : actionId
			return actionPermissionResource((y) => y.actionId === action?.id && y.roleId === data?.id)
		},
		actionPermissions: (entityId) => {
			let entity =
				typeof entityId === 'string' || typeof entityId === 'function' ? db.entities.find(entityId) : entityId
			let actions = db.actions.all((x) => x.entityId === entity?.id)
			return actions
				.map((x) => db.actionPermissions.find((y) => y.actionId === x.id && y.roleId === data?.id))
				.filter(isNotNone)
				.map(actionPermissionResource)
				.filter(isNotNone)
		},

		relationPermission: (relationId) => {
			let relation =
				typeof relationId === 'string' || typeof relationId === 'function'
					? db.relations.find(relationId)
					: relationId
			return relationPermissionResource((y) => y.relationId === relation?.id && y.roleId === data?.id)
		},
		relationPermissions: (entityId) => {
			let entity =
				typeof entityId === 'string' || typeof entityId === 'function' ? db.entities.find(entityId) : entityId
			let relations = db.relations.all((x) => x.entityId === entity?.id)
			return relations
				.map((x) => db.relationPermissions.find((y) => y.relationId === x.id && y.roleId === data?.id))
				.filter(isNotNone)
				.map(relationPermissionResource)
				.filter(isNotNone)
		},
	}
}
