import { FormikContextType, getIn } from 'formik'
import { FieldPath, FieldPathValue, FieldValues } from '@primer/types/form'

export interface FormValidations {
	required?: string
	min?: {
		value: number
		message: string
	}
	max?: {
		value: number
		message: string
	}
}

export const validate =
	(validations?: FormValidations) =>
	(value: any): string | undefined => {
		if (validations?.required && !value) {
			return validations.required
		}

		if (validations?.min && +value < +validations.min.value) {
			return validations.min.message
		}

		if (validations?.max && +value > +validations.max.value) {
			return validations.max.message
		}

		return undefined
	}

export function watch<
	TFieldValues extends FieldValues = FieldValues,
	TFieldName extends FieldPath<TFieldValues> = FieldPath<TFieldValues>
>(name: TFieldName, context: FormikContextType<TFieldValues>): FieldPathValue<TFieldValues, TFieldName> {
	return getIn(context?.values, name)
}
