import { isNotNone } from '@primer/types'
import db from 'db'
import { Identifier, Model } from 'types/db'
import {
	FieldResource,
	ActionResource,
	RelationResource,
	UnitResource,
	fieldResource,
	actionResource,
	relationResource,
	unitResource,
	EntityPermissionResource,
	RoleModel,
	entityPermissionResource,
	PageResource,
	pageResource,
} from '.'

export interface EntityModel extends Model {
	key: string
	name: string
	plural?: string
}

export interface EntityResource extends EntityModel {
	readonly fields: FieldResource[]
	readonly actions: ActionResource[]
	readonly relations: RelationResource[]
	readonly units: UnitResource[]
	readonly permissions: EntityPermissionResource[]
	readonly permission: (roleId: Identifier<RoleModel>) => EntityPermissionResource | null
	readonly page: (key: string) => PageResource | null
	readonly pages: PageResource[]
}

export const entityResource = (id: Identifier<EntityModel>): EntityResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.entities.find(id) : id

	if (!data) return null

	return {
		...data,
		get fields() {
			return db.fields
				.all((x) => x.entityId === data?.id)
				.map(fieldResource)
				.filter(isNotNone)
		},
		get actions() {
			return db.actions
				.all((x) => x.entityId === data?.id)
				.map(actionResource)
				.filter(isNotNone)
		},
		get relations() {
			return db.relations
				.all((x) => x.entityId === data?.id)
				.map(relationResource)
				.filter(isNotNone)
		},
		get units() {
			return db.fields
				.all((x) => x.entityId === data?.id)
				.map(unitResource)
				.filter(isNotNone)
		},
		get permissions() {
			return db.entityPermissions
				.all((x) => x.entityId === data?.id)
				.map(entityPermissionResource)
				.filter(isNotNone)
		},
		permission: (roleId) => {
			let role = typeof roleId === 'string' || typeof roleId === 'function' ? db.roles.find(roleId) : roleId
			return entityPermissionResource((x) => x.entityId === data?.id && x.roleId === role?.id)
		},
		page: (key) => {
			return pageResource((x) => x.key === key && x.entityId === data?.id)
		},
		get pages() {
			return db.pages
				.all((x) => x.entityId === data?.id)
				.map(pageResource)
				.filter(isNotNone)
		},
	}
}
