import { isNotNone } from '@primer/types'
import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { actionPermissionResource, ActionPermissionResource, EntityResource, entityResource, RoleModel } from '.'

export interface ActionModel extends Model {
	entityId: FK
	key: string
	name: string
	changes?: ActionChangeModel[]
}

export interface ActionChangeModel extends Model {
	actionId: FK
	fieldId: FK
	userDefined: boolean
}

export interface ActionResource extends ActionModel {
	readonly entity: EntityResource | null
	readonly permissions: ActionPermissionResource[]
	readonly permission: (roleId: Identifier<RoleModel>) => ActionPermissionResource | null
}

export const actionResource = (id: Identifier<ActionModel>): ActionResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.actions.find(id) : id

	if (!data) return null

	return {
		...data,
		get entity() {
			return entityResource((x) => x.id === data?.entityId)
		},
		get permissions() {
			return db.actionPermissions
				.all((x) => x.actionId === data?.id)
				.map(actionPermissionResource)
				.filter(isNotNone)
		},
		permission: (roleId) => {
			let role = typeof roleId === 'string' || typeof roleId === 'function' ? db.roles.find(roleId) : roleId
			return actionPermissionResource((x) => x.actionId === data?.id && x.roleId === role?.id)
		},
	}
}
