import { format } from 'date-fns'
import db from 'db'
import { Model, FK, Identifier } from 'types/db'
import { sortValuesByLatest } from 'utils/funcs'
import { FieldResource, UnitResource, fieldResource, unitResource } from '.'

export interface ValueModel extends Model {
	unitId: FK
	fieldId: FK
	value: string
	created: string
}

export interface ValueResource extends ValueModel {
	readonly field: FieldResource | null
	readonly unit: UnitResource | null
	readonly history: ValueModel[]
	readonly formatted: string | null
}

export const valueResource = (id: Identifier<ValueModel>): ValueResource | null => {
	let data = typeof id === 'string' || typeof id === 'function' ? db.values.find(id) : id

	if (!data) return null

	return {
		...data,
		get field() {
			return fieldResource((x) => x.id === data?.fieldId)
		},
		get unit() {
			return unitResource((x) => x.id === data?.unitId)
		},
		get history() {
			return db.values
				.all((x) => x.fieldId === data?.fieldId && x.unitId === data?.unitId)
				.sort(sortValuesByLatest)
		},
		get formatted() {
			let field = fieldResource((x) => x.id === data?.fieldId)
			if (!field) return null

			try {
				let output =
					field.type === 'date'
						? format(new Date(data?.value || ''), field?.dateFormat || 'dd/MM/yyyy')
						: data?.value || null

				return output
			} catch (error) {
				return null
			}
		},
	}
}
