import { ValueModel } from 'db/types'
import { Identity } from 'types'
import { v4 } from 'uuid'

export const sortValuesByLatest = (a: ValueModel, b: ValueModel) => {
	if (a.created && b.created) return new Date(b.created).valueOf() - new Date(a.created).valueOf()
	return 0
}

export const uuid = v4

export const replaceMarkers = (
	pattern: string,
	data: Record<string, string | undefined>,
	delimiter: string | Identity<string> = '$'
) => {
	return Object.entries(data)
		.reduce((output, [key, val]) => {
			return output.replace(typeof delimiter === 'string' ? `${delimiter}${key}` : delimiter(key), val || '')
		}, pattern)
		.replace(/\s{2,}/g, ' ')
}
